   <template>
    <div class="level">
      <div class="levelBox" id="levelBox">
        </div>
          <div class="clevel" id="clevel">
            <div>
              <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
              <span>班级层次</span>
               <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
            </div>
            <div class="clevelmain">
              <el-row class="clevel-first">
                <el-col :span="24">
                  <el-button type="primary" @click="addComShow">新增</el-button>
                </el-col>
              </el-row >
              <el-row v-show="isAddShow" class="clevel-scond">
                <el-col :span="12">
                  <input type="text" v-model="itemVal">
                </el-col>
                <el-col :span="12">
                  <el-button @click="addItem" type="primary" >确定</el-button>
                </el-col>
              </el-row>
              <el-row class="clevel-three">
                <el-col :span="12">
                  <span>班级层次名称</span>
                </el-col>
                <el-col :span="12">
                </el-col>
              </el-row>
              <div class="clevel-scroll">
                <ul class="cls-list">
                  <li v-for="clsItem in clevelItems" :key="clsItem.classLevelId">
                  <el-row class="clevel-four">
                    <el-col :span="12">
                      <input type="text" v-model="clsItem.classlevelName" class="classtyleBoxinput" :id="clsItem.classLevelId" readonly>
                    </el-col>
                    <el-col :span="12">
                      <el-button type="warning" @click="changeEditable(clsItem.classLevelId, $event)" >修改</el-button>
                      <el-button type="danger" @click="delClsLevel(clsItem.classLevelId)">删除</el-button>
                    </el-col>
                  </el-row>
                  </li>
                </ul>
              </div>
                <div class="clevelfooter">
                  <div slot="footer" class="dialog-footer">
                      <el-button class="save" @click="addClsLevel">保 存</el-button>
                      <el-button class="exit" @click="hideDialog">返回</el-button>
                  </div>
                </div>
            </div>
          </div>
        </div>
   </template>
<script>
export default {
  name: 'levelpopover',
  props: {},
  data () {
    return {
      isAddShow: false,
      itemVal: '',
      clevelItems: [],
      perLevelLi: [],
      liLen: 0,
      isfocus: false,
      curId: null,
      addData: {
        clevel: this.itemVal
      }
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
    },
    addComShow () {
      this.isAddShow = !this.isAddShow
      this.itemVal = ''
    },
    addItem () {
      if (this.itemVal !== '') {
        let item = {
          'classlevelName': this.itemVal,
          'classLevelId': '$' + this.liLen,
          'isEdit': '1'
        }
        this.perLevelLi.push(item)
        this.clevelItems.push(item)
        console.log('perLevelLi', this.perLevelLi)
        console.log('clevelItems', this.clevelItems)
      }
    },
    changeEditable (id, event) {
      let itemId = document.getElementById(id)
      console.log('itemId', itemId.value)
      itemId.removeAttribute('readonly')
      if (!this.isfocus) {
        itemId.focus()
        this.curId = id
        event.target.innerHTML = '保存'
        this.isfocus = !this.isfocus
        itemId.style.backgroundColor = '#666'
        itemId.style.color = 'white'
      } else {
        if (this.curId !== id) {
          this.$message({
            //  饿了么的消息弹窗组件,类似toast
            showClose: true,
            message: '请先保存上一次更改',
            type: 'error'
          })
          return
        }
        itemId.blur()
        this.curId = null
        itemId.setAttribute('readonly', 'true')
        itemId.style.backgroundColor = 'white'
        itemId.style.color = '#999'
        event.target.innerHTML = '修改'
        this.isfocus = !this.isfocus
        // console.log('changeEditable', this.clevelItems)
        this.upClsLevel(id)
      }
    },
    /**
     * 如果数据perLevelLi 不为空
     * 添加数据到数据库
     */
    addClsLevel () {
      if (this.perLevelLi.length !== 0) {
        let updata = []
        for (let clstp of this.perLevelLi) {
          updata.push(clstp.classlevelName)
        }
        this.perLevelLi = []

        this.$http
          .post(
            '/api/v1/schoolclass/addClassLevel',
            updata
          )
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$message({
                  showClose: true,
                  message: res.data.msg,
                  type: 'success'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    /**
     *  判断是否是假数据，
     * 如果是假数据就直接修改，如果是真数据在提交数据库成功后修改
     */
    upClsLevel (id) {
      console.log('typeof', typeof id)
      let itemId = document.getElementById(id)

      if (typeof id === 'string') {
        for (let clstp of this.perLevelLi) {
          if (clstp.classLevelId === id) {
            clstp.classlevelName = itemId.value
          }
        }
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '修改临时数据成功',
          type: 'success'
        })
        console.log('perLevelLi', this.perLevelLi)
      }
      if (typeof id === 'number') {
        let updata = {
          'classLevelId': id,
          'classlevelName': itemId.value

        }
        console.log('upClsLevel', updata)
        this.$http
          .put(
            '/api/v1/schoolclass/updateClassLevel',
            this.$qs.stringify(updata)
          )
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                console.log('upClsLevel')
                // // this.initClstype()
                this.$message({
                //  饿了么的消息弹窗组件,类似toast
                  showClose: true,
                  message: res.data.msg,
                  type: 'success'
                })
              } else {
                this.$message({
                //  饿了么的消息弹窗组件,类似toast
                  showClose: true,
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    /**
     * 根据id删除数据
     * 1.新增数据删除 更新 perLevelLi和clevelItems
     * 2.数据库数据删除 异步执行成功 更新clevelItems
     */
    delClsLevel (id) {
      // 异步调用 利用ID删除
      // let itemId = document.getElementById(id)
      let index = 0
      if (typeof id === 'string') {
        for (let clstp of this.perLevelLi) {
          if (clstp.classLevelId === id) {
            this.perLevelLi.splice(index, 1)
            index++
          }
        }
        index = 0
        for (let clstp of this.clevelItems) {
          if (clstp.classLevelId === id) {
            this.clevelItems.splice(index, 1)
            index++
          }
          console.log('delete', this.clevelItems)
          this.$message({
            //  饿了么的消息弹窗组件,类似toast
            showClose: true,
            message: '删除临时数据成功',
            type: 'success'
          })
        }
      }

      if (typeof id === 'number') {
        console.log('delete id', this.$qs.stringify({ classLevelId: id }))
        this.$http.delete('/api/v1/schoolclass/deleteClassLevel',
          {
            params: {
              classLevelId: id
            }
          }
        ).then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.msg)
              index = 0
              for (let clstp of this.clevelItems) {
                if (clstp.classLevelId === id) {
                  this.clevelItems.splice(index, 1)
                  index++
                }
              }
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
          .catch(err => {
            console.log(err)
          })
      }
    },
    initClsLevel () {
      this.$http.get('/api/v1/schoolclass/findLevel')
        .then(res => {
          if (res.status === 200) {
            console.log(res.data)
            if (res.data.code === 1) {
              console.log('clevelItems', res.data.data)
              this.clevelItems = res.data.data
            } else {
              console.log(res.data.msg)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }

  },
  mounted () {
    this.initClsLevel()
  }

}
</script>
<style lang="scss">
.level {
  .levelBox {
    position: fixed;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    //  height: 2000px;
    min-width: 960px;
    z-index: 100;
  }
  .clevel {
    background: white;
    position: absolute;
    width: 420px;
    height: 435px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 120;
  }
  .clevel > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .clevel > div:nth-of-type(1) > img {
    vertical-align: middle;
  }

  .clevelmain {
    color: #c3c3c3;
  }
  .clevel-first,
  .clevel-scond,
  .clevel-three,
  .clevel-four {
    text-align: left;
    margin-top :10px;
    margin-left: 20px;

  }
  .clevel-first {
    margin-top :20px;
    margin-left: 20px;
  }
  .clevel-first button {
    padding: 7px 50px;
  }
  .clevel-scond,
  .clevel-four {
    // font-size: 14px;
     input {
      font-size: 16px;
      line-height: 26px;
      padding: 0 5px;
      width: 190px;
      border:1px solid #6bc08b;
      color: #999

    }
  }
  .clevel-scond button,
  .clevel-four button{
    padding: 7px 25px;

  }
  .clevelfooter {
    position: absolute;
    bottom: 20px;
    width: 420px;
    margin: 0 auto;
  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }
  .clevel-scroll {
    height: 200px;
    border:1px solid rgb(184, 171, 171);
    overflow: auto;
    margin-top: 10px;
  }

}
</style>



