    submitForm () {
      var flagPop = this.modify.flag

      if (this.midifyObj.fullName === '') {
        this.$message.error('姓名不能为空')
        return false
      }
      if (this.midifyObj.staffNumber === '') {
        this.$message.error('教职工编号不能为空')
        return false
      }
      if (this.midifyObj.adminType === '') {
        this.$message.error('账户类型不能为空')
        return false
      }
      if (this.midifyObj.birthDate === '') {
        this.$message.error('出生日期不能为空')
        return false
      }
      if (this.midifyObj.englishName === '') {
        this.$message.error('英文名不能为空')
        return false
      }
      if (this.midifyObj.gender === '') {
        this.$message.error('性别是必选项')
        return false
      }

      if (this.placeOfOriginArr !== '') {
        this.midifyObj.placeOfOrigin = this.placeOfOriginArr.toString()
      }
      var arradmin = {}
      console.log(this.midifyObj.birthDate)
      console.log(flagPop)
      if (flagPop === 1) {
        if (this.midifyObj.theDepartmentStr) {
          console.log(this.midifyObj.theDepartmentStr)
          var result = this.midifyObj.theDepartmentStr.split(',')
          this.midifyObj.theDepartmentId = result[0]
          this.midifyObj.theDepartment = result[1]
          console.log(result)
          console.log(this.midifyObj.theDepartmentId)
          console.log(this.midifyObj.theDepartment)
          delete this.midifyObj.theDepartmentStr
        }
        for (var item in this.midifyObj) { // 过滤掉valer=null
          if (this.midifyObj[item]) {
            // if(this.midifyObj[item].length>0){
            //
            // }
            arradmin[item] = this.midifyObj[item]
          }
        }
        this.$http.post('/api/v1/personnelManagement/addStaff', arradmin)
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$emit('refreshList')
                this.$notify({
                  title: '提示信息',
                  message: '操作成功',
                  type: 'success'
                })

                this.hideFun()
              } else {
                this.$notify({
                  title: '提示信息',
                  message: res.data.code,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
