        //获取所有任教学科
        this.$http.get('/api/v1/schoolsubject/findSubjectInfo')
        .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.theSubjectSelect=res.data.data
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
    }
  },
  computed: {
    updateHeadUrl () {
      let url = '/api/v1/common/uploadFile'
      const baseURL = this.$http.defaults.baseURL
      const ans = baseURL ? baseURL + url : url
      return ans
    }
  }

}
</script>

<style lang="scss">
  .popupBox{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
    z-index: 15;
    /*display: none;*/
  }
.departmentPopup{
  width: 850px;
  height: 760px;
  background-color: #ffffff;
  border-radius: 10px 10px;
  box-shadow: 0px 0px 6px #c4c8ca;
  position: absolute;
  left: 50%;
  right: 50%;
  top: 50%;
  bottom: 50%;
  margin: -385px -425px;
  .departmentPopTitBox{
    height: 50px;
    line-height: 50px;
    background-color: #6bc08b;
    text-align: center;
    color: #fff;
    font-size: 18px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    img{
      vertical-align: middle;

    }
  }

  .departmentPopImg{
    width: 140px;
    height: 140px;
    line-height: 140px;
    text-align: center;
    font-size: 28px;
    border: 1px dashed #d9d9d9;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin: 15px auto;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .departmentPopImgbox{
    background-color: #fafcfc;
    padding: 10px 15px;
    p{
      width: 80px;
      height: 25px;
      border-radius:5px ;
      border:1px solid #6bc08b;
      display: block;
      background-color: transparent;
      color: #6bc08b;
      font-size: 12px;
      margin: auto;
      cursor: pointer;
    }
  }
  .departmentPopCont{
    padding: 25px 30px 0 30px;
    max-height: 640px;
    overflow: auto;
  }
  .departmentPopContBx{
    border: 1px solid #eeeeee;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  .departmentPopContTit{
    width: 60px;
    height: 20px;
    color: #fff;
    font-size: 12px;
    background-color: #b26afe;
    text-align: center;
    line-height: 20px;
    margin-bottom: 10px;
  }
  .el-input__inner{
    height: 25px;
    line-height: 25px;
    background-color: #f2f2f2;
  }
  .el-input{
    font-size: 12px;

  }
  .el-form-item__content{
    line-height: 25px;
    margin-left: 150px !important;

  }
  .el-form-item__label{
    font-size: 12px;
    height: 26px;
    line-height: 26px;
    width: 150px !important;
  }
  .el-form-item{
    margin-bottom: 12px;
  }
  .depatPopupBtn{
    width: 100%;
    text-align: center;
    margin: 15px 0;
    button{
      border-radius: 25px;
      border-color: #6bc08b;
      color: #6bc08b;
      padding: 8px 30px;
    }
    button:hover,button:active,button:visited,button:focus{
      background-color: #6bc08b;
      color: #fff;
    }
  }
  #popoupBtn{
    color: #6bc08b;
    height: 40px;
    line-height: 40px;
    display: block;
    padding: 0 15px;
  }
  .el-input__icon{
    line-height: 25px;
  }
  .el-cascader{
    font-size: 12px;
    line-height: 25px;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 100%;
  }
  .el-input.is-disabled .el-input__inner{
    height: 25px !important;
  }
  .el-form-item.is-required .el-form-item__label:before{
    content: "*";
    color: #f56c6c;
    margin-right: 4px;
  }
}

</style>



