</template>

<script>
import vHeader from '@/components/header'
import bottom from '@/components/bottom'
import popup from '@/views/department/popup'
import {formatDate} from '@/api/date.js'
var querystring = require('querystring')

export default {
  name: 'department',
  components: {
    vHeader,
    bottom,
    popup
  },
  data () {
    return {
      storage: {// 该对象储存一些操作需要的标记等
        departId: '', // 储存正在查看的部门ID
        departName: '', // 储存正在查看的部门name
        departNameStr: '', // 储存正在查看的部门nameId和Name
        // tableDepartId: [],//
        teacherAllChecked: [], // 储存table选中的对象
        theDepartmentSelect: [], // 储存部门列表select
        adminIdsArr: [], // 储存需要操作的人员id
        dLeaderStr: '' // 储存新增部门领导的id，name
      },
      options4: [],
      value9: '',
      list: [],
      loading: false,
      modifyFromSelectFlag: false, // 控制修改部门flag
      departForm: {// 分页新增教职工
        parameter: '', // 条件查询
        pageNum: 1,
        pageSize: 10,
        currentPage: 1,
        recordCount: 1
      },
      departFindAllForm: {// 分页部门下人员分页
        parameter: '', // 条件查询
        pageNum: 1,
        pageSize: 10,
        currentPage: 1,
        recordCount: 1
      },
      departSure: {
        fullName: ''
      },
      form: {

      },
      todos: [ {
        dName: '', id: ''
      }],
      addfunFlaf: false, // 控制部门弹窗标记
      departAddFlag: '',
      departDNumber: '', // 储存部门编号
      departAdd: {// 新增
        dName: '',
        dSort: '',
        dLeaderId: '',
        iddAttribute: '',
        dEffectiveness: '1',
        method: '',
        id: '',
        dNumber: ''
      },
      input5: '',
      titleAdd: '',
      addTeacherFlag: false, // 控制教职工弹窗标记
      addTeacherAddTit: '',
      currentPage1: 5,
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 4,
      labelPosition: 'right',
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      },
      deparRenInformation: {
        dName: '',
        dNumber: '',
        createTime: '',
        dEffectiveness: '',
        dSort: '',
        dLeader: '',
        id: ''
      },
      multipleSelection: [],
      tableDataDepart: [], // 储存部门下教职工tabel数据
      tableDataAddTeacher: [], // 储存新增教职工tabel数据
      dpFlag: 1

    }
  },

  methods: {
    // 部门信息提交
    testFun () {
      console.log('你好')
    },
    onSubmit () {
      var url = this.departAddFlag
      if (this.departAdd.dName === '') {
        this.$message.error('部门名称不能为空')
        return false
      }
      if (this.departAdd.dSort === '') {
        this.$message.error('部门排序不能为空')
        return false
      }
      this.departAdd.dNumber = this.departDNumber
      if (this.storage.dLeaderStr.indexOf(',') > 0) {
        var result = this.storage.dLeaderStr.split(',')
        this.departAdd.dLeaderId = result[0]
        this.departAdd.dLeader = result[1]
      }
      delete this.departAdd.createTime
      if (url === 1) {
        this.$http.post('/api/v1/personnelManagement/addDepartment', this.departAdd)
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$notify({
                  title: '提示信息',
                  message: '操作成功',
                  type: 'success'
                })
                this.dnumberFun()
                this.addpartFun()
              } else {
                this.$notify({
                  title: '提示信息',
                  message: '操作失败',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      } else {
        // 编辑
        this.$http.put('/api/v1/personnelManagement/updateDepartmentInfo', querystring.stringify(this.departAdd))
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$notify({
                  title: '提示信息',
                  message: '操作成功',
                  type: 'success'
                })
                this.dnumberFun()
                this.addpartFun()
              } else {
                this.$notify({
                  title: '提示信息',
                  message: '操作失败',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    // 所有教职工分页
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      // 分页调用函数
      this.nameQueryFun('', val, 10)
    },
    // 部门下人员分页
    handleCurrentChangeFindAlldepat (val) {
      console.log(`当前页: ${val}`)
      // 分页调用函数
      var adminIdsObj = {
        'parameters': {},
        'currentPage': val,
        'pageSize': 10
      }
      this.nameQueryAllFun(adminIdsObj)
    },
    // 新增部门弹窗控制
    addpartFun (flagL) {
      // console.log(this.list)
      this.addfunFlaf = !this.addfunFlaf
      /**
       * 查询一波领导人
       */
      this.queryDepLdrFun()
      if (flagL === 1) {
        this.dpFlag = 1
        this.titleAdd = '新增部门'
        this.departAdd = {}
        this.departAddFlag = 1
        this.storage.dLeaderStr = ''
        this.options4 = {}
        /**
         * 获取部门编号
         */
        this.$http.get('/api/v1/personnelManagement/getDNumber')
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.departDNumber = res.data.data
              } else {
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      } else if (flagL === 2) {
        this.dpFlag = 2
        this.titleAdd = '部门编辑'
        this.departAddFlag = 2
        this.departAdd = this.deparRenInformation
        this.departDNumber = this.deparRenInformation.dNumber
      }
    },
    // 添加教职工弹窗控制
    teacherFun () {
      this.addTeacherFlag = !this.addTeacherFlag
      this.addTeacherAddTit = '新增教职工'
      var adminIdsObj = {
        'parameters': {'did': this.storage.departId, 'fullName': this.departSure.fullName},
        'currentPage': 1,
        'pageSize': 10
      }
      this.nameQueryAllFun(adminIdsObj)
    },
    // 批量编辑教职工所在部门
    modifyFormFun (row) {
      this.storage.adminIdsArr = []
      console.log('modify', row)
      if (row === false) { // 多选
        if (this.storage.teacherAllChecked.length < 1) {
          this.$message.error('请选择要编辑的教职工')
          return false
        }
        for (var i = 0; i < this.storage.teacherAllChecked.length; i++) { // 获取教职工id
          this.storage.adminIdsArr.push(this.storage.teacherAllChecked[i].adminId)
        };
        // 改  row === true  为 row
      } else if (row) {
        this.storage.adminIdsArr.push(row.adminId)
      }

      this.modifyFromSelectFlag = !this.modifyFromSelectFlag
    },
    // 查询部门列表
    dnumberFun () {
      var departmentArr = []
      var departmentJson = {}
      this.$http.get('/api/v1/personnelManagement/findAllDepartmentInfo')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.todos = res.data.data
              this.queryBmenFun(this.todos[0].id, this.todos[0].dName, this.todos[0].dLeaderId)
              for (var i = 0; i < res.data.data.length; i++) {
                departmentJson = {}
                departmentJson.value = res.data.data[i].id + ',' + res.data.data[i].dName
                departmentJson.label = res.data.data[i].dName
                departmentArr.push(departmentJson)
              }
              this.storage.theDepartmentSelect = departmentArr
            } else {

            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 删除部门信息
    delectFun () {
      this.$confirm('此操作将删除部门, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.delete('/api/v1/personnelManagement/deleteDepartmentInfo?id=' + this.deparRenInformation.id)
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                })
                this.dnumberFun()
              } else {
                this.$notify({
                  title: '提示信息',
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    departmentNavClick (id, name, event) {
      let li = this.$refs.leftNav.childNodes
      li.forEach((val) => {
        if (val.className) {
          val.className = ''
        }
      })

      var target = event.target
      if (target.nodeName.toLowerCase() === 'li') {
        target.className = 'leftNavbarIsActive'
      }

      this.queryBmenFun(id, name)
    },

    /**
     *  根据部门id 查询信息
     * @id 部门id
     * @name 姓名
     * */
    queryBmenFun (id, name) {
      this.storage.departId = id
      this.storage.departName = name
      // 查询部门信息
      this.$http.get('/api/v1/personnelManagement/selectDepartmentById?id=' + id)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.deparRenInformation.dName = res.data.data.dName
              this.deparRenInformation.dNumber = res.data.data.dNumber
              this.deparRenInformation.createTime = res.data.data.createTime
              this.deparRenInformation.dEffectiveness = res.data.data.dEffectiveness
              this.deparRenInformation.dSort = res.data.data.dSort
              this.deparRenInformation.dLeader = res.data.data.dLeader
              this.deparRenInformation.id = res.data.data.id
              this.deparRenInformation.dLeaderId = res.data.data.dLeaderId
            } else {
              this.$notify({
                title: '提示信息',
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
      // 调用查询部门下人员信息函数
      this.departQueryPerFun(id)
      // 查询一次所有的教职工信息
      this.nameQueryFun('', 1, 10)
    },
    /**
     * 点击部门查询部门下所有人员
     */
    departQueryPerFun (id) {
      var adminIdsObj = {
        'parameters': {'did': id},
        'currentPage': 1,
        'pageSize': 10
      }
      this.nameQueryAllFun(adminIdsObj)
    },
    /**
     * 模糊查询部门查询部门下所有人员
     */
    departQueryInput () {
      var adminIdsObj = {
        'parameters': {'did': this.storage.departId, 'fullName': this.departSure.fullName},
        'currentPage': 1,
        'pageSize': 10
      }
      this.nameQueryAllFun(adminIdsObj)
    },
    // 根据姓名查询教职工信息
    nameQuery () {
      this.nameQueryFun(this.departForm.parameter, 1, 10)
    },
    /**
     * 查询所有教职工信息方法
     * @param name 姓名或者教职工编号
     * @param currentPage 页数
     * @param pageSize 条数
     */
    nameQueryFun (name, currentPage, pageSize) {
      this.$http.post('/api/v1/personnelManagement/selectAdminInfoAndDepartment?parameter=' + name + '&&currentPage=' + currentPage + '&&pageSize=' + pageSize)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.tableDataAddTeacher = []
              var tableDataAddobj = {}
              for (var i = 0; i < res.data.data.content.length; i++) {
                tableDataAddobj = {}
                tableDataAddobj.serialNumber = i + 1
                tableDataAddobj.adminId = res.data.data.content[i].adminId
                tableDataAddobj.fullName = res.data.data.content[i].fullName
                tableDataAddobj.theDepartment = res.data.data.content[i].theDepartment
                tableDataAddobj.staffNumber = res.data.data.content[i].staffNumber
                this.tableDataAddTeacher.push(tableDataAddobj)
              }
              this.departForm.currentPage = res.data.data.currentPage
              this.departForm.pageSize = res.data.data.pageSize
              this.departForm.recordCount = res.data.data.recordTotal
            } else {
              this.$notify({
                title: '提示信息',
                message: '账号或密码错误',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 查询部门下所有教职工信息方法
     * @param name 姓名或者教职工编号
     * @param currentPage 页数
     * @param pageSize 条数
     */
    nameQueryAllFun (adminIdsObj) {
      this.$http.post('/api/v1/personnelManagement/selectAdminInfoByDepartment', adminIdsObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.tableDataDepart = []
              var tableDataAddobj = {}
              // let isLeader = ''
              for (var i = 0; i < res.data.data.content.length; i++) {
                tableDataAddobj = {}
                tableDataAddobj.serialNumber = i + 1
                tableDataAddobj.adminId = res.data.data.content[i].adminId

                tableDataAddobj.fullName = res.data.data.content[i].fullName
                tableDataAddobj.theDepartment = res.data.data.content[i].theDepartment
                tableDataAddobj.mailBox = res.data.data.content[i].mailBox
                tableDataAddobj.mobilePhone = res.data.data.content[i].mobilePhone
                tableDataAddobj.staffNumber = res.data.data.content[i].staffNumber
                console.log('leader', this.deparRenInformation.dLeaderId, tableDataAddobj.adminId)
                if (tableDataAddobj.adminId === this.deparRenInformation.dLeaderId) {
                  this.tableDataDepart.unshift(tableDataAddobj)
                } else {
                  this.tableDataDepart.push(tableDataAddobj)
                }
              }

              this.departFindAllForm.currentPage = res.data.data.currentPage
              this.departFindAllForm.pageSize = res.data.data.pageSize
              this.departFindAllForm.recordCount = res.data.data.recordTotal
            } else {
              this.$notify({
                title: '提示信息',
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 单个修改部门
     * @param row 是this父级tr下的所有值
     */
    adddepartBtnFun (row) {
      console.log('单个修改部门', row)
      var adminIdsArr = []
      adminIdsArr.push(row.adminId)
      var adminIdsObj = {
        'adminIds': adminIdsArr,
        'did': this.storage.departId,
        'theDepartment': this.storage.departName
      }
      this.adminIdsPutFun(adminIdsObj)
    },
    /**
     * 新增-----编辑/教职工部门函数
     * @param adminIdsObj 是个对象
     */
    adminIdsPutFun (adminIdsObj) {
      console.log('adminIdsObj', adminIdsObj)

      this.$http.put('/api/v1/personnelManagement/updateStaffDepartment', adminIdsObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$notify({
                title: '提示信息',
                message: '添加成功',
                type: 'success'
              })
              // 成功过后查询一次所有的教职工信息
              this.nameQueryFun('', 1, 10)
              if (this.modifyFromSelectFlag) {
                this.modifyFromSelectFlag = false
                this.departQueryPerFun(this.storage.departId)
              }
            } else {
              this.$notify({
                title: '提示信息',
                message: '操作失败',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     *批量添加教职工
     *
     */
    addPlChecked () {
      if (this.storage.teacherAllChecked < 1) {
        this.$message.error('请至少选择一项')
        return false
      }
      var adminIdsArr = []
      for (var i = 0; i < this.storage.teacherAllChecked.length; i++) {
        adminIdsArr.push(this.storage.teacherAllChecked[i].adminId)
      };
      var adminIdsObj = {
        'adminIds': adminIdsArr,
        'did': this.storage.departId,
        'theDepartment': this.storage.departName
      }
      this.adminIdsPutFun(adminIdsObj)
    },

    /**
     * 批量添加储存点击事件函数
     * @param val
     */
    modifyPwassFun (val) {
      this.storage.teacherAllChecked = val
    },
    /**
     * 批量编辑部门提交
     * @returns {boolean}
     */
    modifyFormSubFun () {
      if (this.storage.departNameStr) {
        var result = this.storage.departNameStr.split(',')
        var adminIdsObj = {
          'adminIds': this.storage.adminIdsArr,
          'did': Number(result[0]),
          'theDepartment': result[1]
        }
        this.adminIdsPutFun(adminIdsObj)
      }
    },
    remoteMethod (query) {
      console.log(query)
      if (query !== '') {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.options4 = this.list.filter(item => {
            return item.label.toLowerCase()
              .indexOf(query.toLowerCase()) > -1
          })
        }, 200)
      } else {
        this.options4 = []
      }
    },
    /**
     *查询部门领导人
     */
    queryDepLdrFun () {
      var departmentArr = []
      var departmentJson = {}
      var queryDepLdrOnb = {
        parameter: ''
      }
      console.log('leadership', queryDepLdrOnb)

      this.$http.post('/api/v1/personnelManagement/findAllDepartmentalLeadership', queryDepLdrOnb)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              var bmldrStr = res.data.data.content
              console.log('leadership', res.data.data.content)
              for (var i = 0; i < bmldrStr.length; i++) {
                departmentJson = {}
                departmentJson.value = bmldrStr[i].adminId + ',' + bmldrStr[i].fullName
                departmentJson.label = '(' + bmldrStr[i].staffNumber + ')' + bmldrStr[i].fullName
                departmentArr.push(departmentJson)
              }
              this.list = departmentArr
              // console.log('this.list', this.list)
            } else {

            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  },
  mounted () {
    this.dnumberFun()
    this.queryDepLdrFun()
    console.log('department.vue')
  },
  filters: {
    changeDate (val) {
      return formatDate(new Date(val), 'yyyy/MM/dd')
    }
  },
  watch: {
    dpFlag (cur, old) {
      if (cur === 1) {
        // this.departAdd = {}
        // this.departAddFlag = 1
        this.storage.dLeaderStr = ''
        // /**
        //  * 获取部门编号
        //  */
        // this.$http.get('/api/v1/personnelManagement/getDNumber')
        //   .then(res => {
        //     if (res.status === 200) {
        //       if (res.data.code === 1) {
        //         this.departDNumber = res.data.data
        //       } else {
        //       }
        //     }
        //   })
        //   .catch(err => {
        //     console.log(err)
        //   })
      } else if (cur === 2) {
        this.departAddFlag = 2
        this.departAdd = this.deparRenInformation
        this.departDNumber = this.deparRenInformation.dNumber
      }
    }
  }

}
</script>

