<template>
    <div class="classinfoBox">
       <v-header></v-header>
        <el-tabs type="border-card" class="class-tabs"  v-model="getActiveTab"   @tab-click ="handleClick" >
             <el-tab-pane label="班级主任" name="five">
              <div class="selectbyq">
                <h1>条件查询</h1>
                <div class="inputselect">
                  <span>输入查询条件:</span>
                  <el-input placeholder="请输入班级管理人名称" v-model="clsDpVal" class="input-with-select">
                    <el-button slot="append" icon="el-icon-search" @click="clsDpShClick"></el-button>
                  </el-input>
                </div>
              </div>
                <el-table
                :data="clsDpData"
                border
                style="width: 600px ;font-size:12px;margin: 0 auto;margin-top:34px;font-size:14px;margin-bottom:15px">
                <el-table-column
                  prop="staffNumber"
                  label="职工编号"
                  width="100">
                </el-table-column>
                <el-table-column
                  prop="fullName"
                  label="班主任"
                  width="140">
                </el-table-column>
                <el-table-column
                  prop="className"
                  label="班级"
                  width="120">
                </el-table-column>
                 <el-table-column
                  prop="classNumber"
                  label="班级号"
                  width="120">
                </el-table-column>
                <el-table-column
                  prop="manage"
                  label="操作"
                  >
                  <template slot-scope="scope">
                    <span  style="cursor: pointer;color: #6bc08b;" @click="clsDeployClick(scope.row)">编辑</span>
                    <span  style="cursor: pointer;color: #6bc08b;" @click="clsDeployDelClsClick(scope.row)">清空</span>
                    <!-- <el-button style="padding: 3px 4px; font-size: 12px" type="primary"  @click="clsDeployClick(scope.row)">编辑</el-button>
                    <el-button style="padding: 3px 4px; font-size: 12px" type="danger"  @click="clsDeployDelClsClick(scope.row)">清空</el-button> -->
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @current-change="clsDpHandleCurrentChange"
                :current-page="clsDpCurPage"
                :page-size="10"
                layout="total, prev, pager, next, jumper"
                :total="clsDpTotal">
              </el-pagination>

            </el-tab-pane>
             <el-tab-pane label="年级主任" name="six">
                   <div class="selectbyq">
                <h1>条件查询</h1>
                <div class="inputselect">
                  <span>输入查询条件:</span>
                  <el-input placeholder="请输入年级管理人名称" v-model="gdDpVal" class="input-with-select">
                    <el-button slot="append" icon="el-icon-search" @click="gdDpShClick"></el-button>
                  </el-input>
                </div>
              </div>
              <el-table
                :data="gdDpData"
                border
                style="width: 600px ;font-size:12px;margin: 0 auto;margin-top:34px; font-size:14px; margin-bottom:15px">
                <el-table-column
                  prop="staffNumber"
                  label="职工编号"
                  width="100">
                </el-table-column>
                <el-table-column
                  prop="fullName"
                  label="年级主任"
                  width="140">
                </el-table-column>
                <el-table-column
                  prop="periodName"
                  label="学段"
                  width="120">
                </el-table-column>
                <el-table-column
                  prop="thetime"
                  label="届次"
                  width="120">
                </el-table-column>
                <el-table-column
                  prop="manage"
                  label="操作"
                  >
                    <template slot-scope="scope">
                      <span style="cursor: pointer;color: #6bc08b;" @click="gdDeployClick(scope.row)">编辑</span>
                      <span style="cursor: pointer;color: #6bc08b;" @click="gdDeployDelClsClick(scope.row)">清空</span>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @current-change="gdDpHandleCurrentChange"
                :current-page="gdDpCurPage"
                :page-size="10"
                layout="total, prev, pager, next, jumper"
                :total="gdDpTotal">
              </el-pagination>

            </el-tab-pane>
        </el-tabs>

      <!-- 班级调配子组件 -->
      <cls-deploy @hideDialog="clsdeployhide" v-show="clsdeployvisible"  :clsdeployData="clsdeployRowData" @upTable="initClsDeployTable"></cls-deploy>
      <!-- 年级调配子组件 -->
      <gd-deploy  @hideDialog="gddeployhide" v-show="gddeployvisible" :gddeployData="gddeployRowData" @upTable="initGdDpTable"></gd-deploy>
    </div>
</template>
<script>
import vFooter from '@/components/bottom'
import vHeader from '@/components/header'
import ClsDeploy from '@/views/classInfo/clsdeploy'
import GdDeploy from '@/views/classInfo/gddeploy'
import { formatDate } from '@/api/date.js'
export default {
  components: {
    vHeader,
    vFooter,
    ClsDeploy,
    GdDeploy
  },
  data () {
    return {
      // --------------------- 班级分配管理--------------------
      clsDpVal: '',
      clsDpData: [],
      clsdeployvisible: false,
      clsdeployRowData: null,
      clsDpCurPage: 1,
      clsDpTotal: null,
      // --------------------- 年级分配管理---------------------
      gdDpVal: '',
      gdDpData: [],
      gddeployvisible: false,
      gddeployRowData: null,
      gdDpCurPage: 1,
      gdDpTotal: null

    }
  },
  self: this,
  methods: {
    handleClick (tab, event) {
      // this.$router.push({path: 'classinfo', query: {clsInfoAct: event.target.id.slice(4)}})
    },
    clsdeployhide () {
      this.clsdeployvisible = false
    },
    clsdeployshow () {
      this.clsdeployvisible = true
    },
    gddeployhide () {
      this.gddeployvisible = false
    },
    gddeployshow () {
      this.gddeployvisible = true
    },
    // ---------------------- 班级调配--------------------------------
    initClsDeployTable () {
      this.$http
        .get(
          '/api/v1/schoolclass/findIsHeadmaster',
          {
            params: {
              currentPage: this.clsDpCurPage,
              vague: this.clsDpVal
            }
          }
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.clsDpData = res.data.data.content
              console.log('clsDpData', this.clsDpData)
              this.clsDpTotal = res.data.data.recordTotal
              console.log('clsDpTotal', this.clsDpTotal)
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    clsDpShClick () {
      this.clsDpCurPage = 1
      this.initClsDeployTable()
    },
    clsDpHandleCurrentChange (curPage) {
      this.clsDpCurPage = curPage
      this.initClsDeployTable()
    },
    clsDeployClick (row) {
      // todo 传递数据给编辑弹窗
      this.clsdeployRowData = row
      console.log('传递数据给子组件', this.clsdeployRowData)

      this.clsdeployshow()
    },

    /** @augments
      * 删除班长任时 提示框
      */
    clsDeployDelClsClick (row) {
      this.$confirm('是否删除该班主任所带的班级?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.clsDeployDel(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    /**
     *  异步执行删除班主任
     */
    clsDeployDel (row) {
      this.$http
        .delete(
          '/api/v1/schoolclass/updateHeadmasterDel',
          {
            params: {
              classId: row.classId
            }
          }
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
              // todo 更新清空班级后的数据
              this.initClsDeployTable()
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // ----------------------------年级调配---------------------------
    /**
     * 初始化 年级调配表
     */
    initGdDpTable () {
      this.$http
        .get(
          '/api/v1/schoolclass/findIsDirector',
          {
            params: {
              vague: this.gdDpVal,
              currentPage: this.gdDpCurPage
            }
          }
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.gdDpData = res.data.data.content
              this.gdDpTotal = res.data.data.recordTotal
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    gdDpHandleCurrentChange (curPage) {
      this.gdDpCurPage = curPage
      this.initGdDpTable()
    },

    /** 年级调配 搜索 */
    gdDpShClick () {
      this.gdDpCurPage = 1
      this.initGdDpTable()
    },
    /** @augments
     * 年级调配点击编辑
     * 传递参数给子子组件
     */
    gdDeployClick (row) {
      // todo 传递数据给子组件
      this.gddeployRowData = row
      console.log('父传子信息', this.gddeployRowData)
      this.gddeployshow()
    },
    /** @augments
     * 年级调配点击删除
     * 提示信息
     */
    gdDeployDelClsClick (row) {
      this.$confirm('是否删除该班主任所带的班级?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.gdDeployDel(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    /**
     *  执行删除逻辑
     *  异步执行删除 年级主任的年级信息
     */
    gdDeployDel (row) {
      console.log('row', row)
      this.$http
        .delete(
          '/api/v1/schoolclass/updateDirectorDel',
          {
            params: {
              directorId: row.directorId,
              periodId: row.periodId,
              theTime: row.thetime
            }
          }
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
              this.initGdDpTable()
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 根据gddeploy子组件emit触发更新表
     * gdDpData
     */
    gdDpTable (newRow) {
      console.log('子传父gdDp', newRow)
      this.gdDpData.forEach((val, index) => {
        if (val.directorId === newRow.directorId) {
          this.gdDpData[index] = newRow
        }
      })
    },
    /** 公共方法 */
    handleHref (url) {
      const baseURL = this.$http.defaults.baseURL
      return baseURL ? baseURL + url : url
    }
  }, /* method end */

  filters: {
    transDate (date) {
      date = new Date(date)
      return formatDate(date, 'yyyy-MM-dd')
    },
    getGradeList (li) {
      let gdArr = []
      li.forEach(val => {
        gdArr.push(val.gradeName)
      })
      li = gdArr.join(',')
      return li
    },
    getSubjectList (li) {
      let gdArr = []
      li.forEach(val => {
        gdArr.push(val.subjectName)
      })
      li = gdArr.join(',')
      return li
    },
    getsbjctName (val) {
      let li = []
      val.forEach(ele => {
        li.push(ele.subjectName)
      })
      return li.join(',')
    },
    judgeGender (gender) {
      let ans = ''
      if (gender === '0') {
        ans = ''
      } else if (gender === '1') {
        ans = '男'
      } else {
        ans = '女'
      }
      return ans
    },
    getTheTime (time) {
      time = new Date(time)
      return formatDate(time, 'yyyy')
    }
  }, /** filters end */
  computed: {
    getActiveTab: {
      get: function () {
        let ans = ''
        if (this.$route) {
          ans = this.$route.query.clsInfoAct
        }
        return ans
      },
      set: function () {
      }
    }

  },

  mounted () {
    this.initClsDeployTable()
    this.initGdDpTable()
  }
}

</script>

<style lang="scss">
body{
  background-color: #f2f4f5;
}
.classinfoBox {
  width: 100%;
  min-width: 960px;
  .el-tabs__item {
    height: 56px;
    line-height: 56px;
    width: 150px;
  }

  .class-tabs {
    width: 960px;
    margin: 0 auto;
    color: #afafaf;
    font-size: 12px;
  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item.is-active {
    color: #666666;
    font-size: 16px;
    font-weight: bold;
  }

  .el-tabs--border-card > .el-tabs__header .el-tabs__item:hover {
    color: #afafaf;
  }
  .tab {
    // height: 700px;
    width: 960px !important;
    margin-left: -16px;
  }
   .demo-table-expand {
    font-size: 0;
    text-align: left;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .selectbyq {
    height: 84px;
    border-top: 1px solid #eee;
    .el-input__inner {
    height: 30px;
    line-height: 30px;
    }
  }
  .selectbyq > h1 {
    color: white;
    background: #6cc08c;
    width: 50px;
    height: 18px;
    font-size: 12px;
    margin-top: 12px;
    margin-left: 20px;
    text-align: center;
  }
 .inputselect {
    text-align: left;
    margin-left: 160px;
 }
  .inputselect > input {
    width: 144px;
    height: 31px;
    border: 1px solid #c3c3c3;
    padding-left: 10px;
  }
  .classlist {
    border-top: 1px solid #eee;
    .el-button{
      padding: 3px 4px;
      font-size: 12px;
    }
  }
  .classlist > h1 {
    color: white;
    background: #6cc08c;
    width: 62px;
    height: 18px;
    font-size: 12px;
    margin-top: 12px;
    margin-left: 20px;
    text-align: center;
  }
  .course-style {
    display: block;
    width:100% !important;
  }
  .cell {
    text-align: center;
  }
  .tab {
    .card-style {
      margin-left:40px;
    }
    .el-card {
    display: inline-block;
    width: 130px;
    position: relative;
    // margin-left:25px;
    margin-right: 20px;
    text-align: center;
  }
  .el-card__body {
    padding: 0
  }
  }

  .input-with-select {
    margin-top:20px;
    margin-bottom: 20px;
    width:320px;
  }

  .el-tabs--border-card{
  background: #fff;
  border: none;
  -webkit-box-shadow: none;
  box-shadow:none;
  margin-top:10px;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
  border-bottom: 57px solid #fff;
  border-left: 31px solid transparent;
  border-right: 31px solid transparent;
  height: 0;
  /*width: 137px;*/
  color: #666666;
  font-size: 14px;
  background-color: transparent;

}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:first-child.is-active{
  border-left:none;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item{
  border: none;
  -webkit-transition: all 0s cubic-bezier(.645,.045,.355,1);
  transition: all 0s cubic-bezier(.645,.045,.355,1);
}
.el-tabs--border-card>.el-tabs__header{
  background-color: #f2f2f2;
  border-bottom: none;

}
.el-tabs__nav-scroll{
  line-height: 56px;
  border-top: 1px solid #e5e5e5;
  /*border-right: 1px solid #e5e5e5;*/
  /*border-left: 1px solid #e5e5e5;*/
}
.el-tabs--top.el-tabs--border-card .el-tabs__item:nth-child(2){
  padding: 0;
}
.el-tabs__item{
  line-height: 56px;
  width: 139px;
  text-align: center;
  padding: 0;
  height: 56px;
  font-size: 12px;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:hover{
  color: #6cc08c;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active:hover{
  color: #666666;
}
.el-tabs__content{
  padding: 10px 20px;
}
.manage-icon> span{
  cursor: pointer;
  color:#6bc08b ;
}
}
</style>



