
<template>
  <div class="export" v-show="importfunFlaf">
    <div class="exportBox" id="exportBox">
     </div>
    <div class="cExport" id="cExport">
      <div>
        <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
        <span>成绩Excel导入</span>
        <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
      </div>
      <div class="cExportmain">
        <div class="cExport-scroll">

          <!--输入查询条件-->
          <el-row class="cstyle">
            <el-col :span="6" style="text-align:right">
              <span style="font-size:16px;margin-right:5px;">学段</span>
            </el-col>
            <el-col :span="16">
              <el-select size="mini" class="dropselect" v-model="perSelected" @change="perSlctClick">
              <el-option
                v-for="umPerItem in umPerItems"
                :key="umPerItem.periodId"
                :value="umPerItem.periodId"
                :label="umPerItem.periodName">
              </el-option>
            </el-select>
            </el-col>
          </el-row>
          <el-row class="cstyle">
            <el-col :span="6" style="text-align:right">
              <span style="font-size:16px;margin-right:5px;">届次</span>
            </el-col>
            <el-col :span="16">
              <el-select size="mini" class="dropselect" v-model="slctGrade" @change="gdSlctClick">
                <el-option
                v-for="umgdItem in umgdItems"
                :key="umgdItem.thetime"
                :value="umgdItem.thetime"
                :label="umgdItem.thetime">
                </el-option>
              </el-select>
            </el-col>
          </el-row >
          <el-row class="cstyle">
            <el-col :span="6" style="text-align:right">
              <span style="font-size:16px;margin-right:5px;">第</span>
            </el-col>
            <el-col :span="10">
              <el-select size="mini" v-model="value1" placeholder="选择学期" >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            </el-col>
            <el-col :span="8" style="text-align:left">
              <span style="font-size:16px;margin-left:5px;">学期</span>
            </el-col>
          </el-row >
          <el-row class="cstyle">
            <el-col :span="6" style="text-align:right">
              <span style="font-size:16px;margin-right:5px;">考试名称</span>
            </el-col>
            <el-col :span="16">
               <el-select size="mini" v-model="examName" placeholder="选择考试名称" @change="examListQuerySearchFun">
                  <el-option
                    v-for="item in examListNameSelect"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-col>
          </el-row >
          <el-row class="cstyle">
            <el-col :span="6" style="text-align:right">
              <span style="font-size:16px;margin-right:5px;">模板编号</span>
            </el-col>
            <el-col :span="16">
              <el-select size="mini" v-model="value2" placeholder="选择模板" >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            </el-col>
          </el-row >
          <el-row class="cstyle-second">
            <el-upload
            class="upload-demo"
            ref="upload"
            action="/api/v1/schoolscore/bathImportInfo"
            :on-preview="handlePreview"
            :on-change="handleChange"
            :before-upload="beforeUpload"
            :file-list="fileList"
            :auto-upload="false"
            :limit="1"
            :multiple="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <!--<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">导入</el-button>-->
            <div slot="tip" class="el-upload__tip">上传模板只能是 xls、xlsx格式!并且文件小于12M</div>
          </el-upload>
          </el-row>
        </div>
        <div class="cExportfooter">
          <div slot="footer" class="dialog-footer">
            <button class="save" @click="submitUpload">导 入</button>
            <button class="exit" @click="hideFun">关 闭</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import { formatDate } from '@/api/date.js'
export default {
  name: 'importTemplate',
  data () {
    return {
      fileList: [],
      options:[{
        value: '选项1',
        label: '1'
        },{
        value: '选项2',
        label: '2'
        }],
        value1: '',
        value2:'',
        termNum:'',
        examName:'',
        perSelected: null,
        slctGrade: '',
        umPerItems: [],
        umgdItems:[],
        examListNameSelect: [],
        personPagObj: { // 储存分页数据
        pageNum: 1,
        pageSize: 10,
        currentPage: 1,
        recordCount: 1
      },
      // importfunFlaf: true
    }
  },
  props: ['importfunFlaf'],
  methods: {
    // hideDialog () {
    //   this.$emit('hideDialog')
    // },
    submitUpload () {
      this.$refs.upload.submit()
    },
    handleRemove (file, fileList) {
      console.log(file)
      console.log(fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    // handleinportSuccess(){
    //
    //
    //
    //
    // },
    // 关闭弹窗
    hideFun () {
      this.$emit('update:importfunFlaf', false)
    },
    handleChange (file, fileList) {
      console.log(file)

      console.log(fileList)
    },
    //beforeUpload: function (file)
    //上传文件前的钩子函数
    beforeUpload(file) {
      console.log("================",file)
      const extension = file.name.split('.')[1] === 'xls'
      const extension2 = file.name.split('.')[1] === 'xlsx'
      const isLt2M = file.size / 1024 / 1024 < 10
      if (!extension && !extension2) {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '上传模板只能是 xls、xlsx格式!',
          type: 'error'
        })
        return false
      }
      if (!isLt2M) {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '上传模板大小不能超过 10MB!',
          type: 'error'
        })
        return false
      }
      if ((extension || extension2) && isLt2M) {
        console.log(file)
        // 这里是重点，将文件转化为formdata数据上传
        let fd = new FormData()
        fd.append('file', file)

        this.$http.post('/api/v1/personnelManagement/loadingInStaffs', fd).then((res) => {
          if (res.status === 200) {
            let arr = res.data.data
            let arr2 = []
            arr.forEach((val) => {
              arr2.push(val + '<br>')
            })
            this.$alert(arr2.join(','), '导入信息', {
              dangerouslyUseHTMLString: true
            })
          }
        }, (err) => {
          console.log(err)
        })
      }
      return false
    },
     /** 学段被选中
     * 0. 初始化分页为首页
     * 1. 更新班级表
     * 2. 查询学段下的班级
     */
    perSlctClick () {
      //this.clsCurPage = 1
      this.findGradeByPer()
    },
    //查询届次
    findGradeByPer () {
      this.$http
        .get('/api/v1/schoolclass/findTheTime', {
          params: {
            periodId: this.perSelected
          }
        })
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            this.umgdItems = res.data.data
            if (this.umgdItems.length > 0) {
              this.slctGrade = this.umgdItems[0].thetime
              this.updateClsTable()
            } else {
              this.slctGrade = ''
              this.updateClsTable()
            }
            console.log('umgdItems', this.umgdItems, this.slctGrade)
          } else {
            console.log(res.data.msg)
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    initClsTable () {
      this.$http
        .get('/api/v1/schoolperiod/findInfo')
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.umPerItems = res.data.data
              if (this.umPerItems.length > 0) {
                console.log('this.umPerItems', this.umPerItems)
                this.perSelected = this.umPerItems[0].periodId
                // 根据学段更新表
              }

              this.$http
                .get('/api/v1/schoolclass/findTheTime', {
                  params: {
                    periodId: this.perSelected
                  }
                })
                .then(res => {
                  // console.log(res)
                  if (res.status === 200) {
                    if (res.data.code === 1) {
                      this.umgdItems = res.data.data
                      console.log(this.umgdItems)
                      if (this.umgdItems.length > 0) {
                        this.slctGrade = this.umgdItems[0].thetime
                      }

                      console.log('umgdItems', this.umgdItems, this.slctGrade)
                      this.updateClsTable()
                    } else {
                    // console.log(res.data.msg)
                    }
                  }
                })
                .catch(err => {
                  console.log(err)
                })
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     *  届次被点击 isGradeUp
     */
    gdSlctClick () {
      this.clsCurPage = 1
      this.updateClsTable()
    },
    /**
     * 根据 学段ID学段ID年级IDvague字符串查询
     * 班级信息
     */
    updateClsTable () {
      console.log('periodId', this.perSelected)
      console.log('thetime', this.slctGrade)
      //console.log('vagueSh', this.vagueVal)
      let updata = {
        currentPage: this.clsCurPage,
        pageSize: 10,
        parameters: {
          periodId: this.perSelected,
          thetime: Number(this.slctGrade),
         // vague: this.vagueVal
        }
      }
      this.$http
        .post('/api/v1/schoolclass/findSchoolClassInfoUBW', updata)
        .then(res => {
          if (res.status === 200) {
            console.log(res.data)
            if (res.data.code === 1) {
              console.log('clsData', res.data.data.content)
              this.clsData = res.data.data.content
              this.clsPageTotal = res.data.data.recordTotal
            } else {
              console.log(res.data.msg)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    //选择考试名称
    examListQuerySearchFun () {
      let adminIdsObj = {
        params: {
        'currentPage': 1,
        'pageSize': 10,
        'examName': this.examName,
        }

      }
      this.examListQueryFun(adminIdsObj)
    },
     examListQueryFun (adminIdsObj) {
      this.$http.get('/api/v1/schoolexam/findExamOfVague', adminIdsObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              this.examlistTbaleData = []
              let tableDataAddobj = {}
              for (var i = 0; i < res.data.data.content.length; i++) {
                tableDataAddobj = {}
                tableDataAddobj.serialNumber = i + 1
                tableDataAddobj.examName = res.data.data.content[i].examName
                tableDataAddobj.examTypeName = res.data.data.content[i].examTypeName
                if (res.data.data.content[i].examTime) {
                  var date = new Date(res.data.data.content[i].examTime)
                  tableDataAddobj.examTime = formatDate(date, 'yyyy-MM-dd')
                }
                this.examlistTbaleData.push(tableDataAddobj)
                }
              }
              this.personPagObj.currentPage = res.data.data.currentPage
              this.personPagObj.pageSize = res.data.data.pageSize
              this.personPagObj.recordCount = res.data.data.pageTotal

            } else {
              this.$notify({
                title: '提示信息',
                message: '账号或密码错误',
                type: 'error'
              })
            }
        })
        .catch(err => {
          console.log(err)
        })
    },
    examListQueryNameFun () {
      let examListNameArr = []
      let examListNameJson = {}
      this.$http.get('/api/v1/schoolexam/findExamName')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              for (var i = 0; i < res.data.data.length; i++) {
                examListNameJson = {}
                examListNameJson.value = res.data.data[i].examName
                examListNameJson.label = res.data.data[i].examName
                examListNameArr.push(examListNameJson)
              }
              console.log(examListNameArr)
              this.examListNameSelect = examListNameArr
            } else {
              this.$notify({
                title: '提示信息',
                message: '操作失败',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

  },
  mounted () {
    this.initClsTable()
    let adminIdsObj = {
      'parameters': {},
      'currentPage': 1,
      'pageSize': 10,
      'examName': '考试'
    }
    this.examListQueryFun(adminIdsObj)
    this.examListQueryNameFun()
  }
}
</script>
<style lang="scss">
  .export {
    text-align: center !important;
    .exportBox {
      position: fixed;
      background:rgba(0,0,0,.7) ;
      top: 0;
      bottom:0;
      left: 0;
      right: 0;
      filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
      width: 100%;
      height: 100%;
      min-width: 960px;
      z-index: 200;
    }
    .cExport {
      background: white;
      position: absolute;
      width: 420px;
      height: 435px;
      border-radius: 4px;
      margin: auto;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 300;
    }
    .cExport > div:nth-of-type(1) {
      height: 40px;
      font-size: 18px;
      font-weight: bold;
      color: white;
      line-height: 40px;
      background: #6bc08b;
    }
    .cExport > div:nth-of-type(1) > img {
      vertical-align: middle;
    }

    .cExportmain {
      color: #c3c3c3;
    }
    .cExport-first,
    .cExport-scond,
    .cExport-three,
    .cExport-four {
      text-align: left;
      margin-top :10px;
      margin-left: 20px;

    }
    .cExport-first {
      margin-top :20px;
      margin-left: 20px;
    }
    .cExport-first button {
      padding: 7px 50px;
    }
    .cExport-scond,
    .cExport-four {
      // font-size: 14px;
      input {
        font-size: 16px;
        line-height: 26px;
        padding: 0 5px;
        width: 190px;
        border:1px solid #6bc08b;
        color: #999

      }
    }
    .cExport-scond button,
    .cExport-four button{
      padding: 7px 25px;

    }
    .cExportfooter {
      position: absolute;
      bottom: 20px;
      width: 420px;
      margin: 0 auto;
    }
    .dialog-footer > button {
      border: 1px solid #6bc08b;
      font-size: 14px;
      border-radius: 20px;
      height: 32px;
      line-height: 32px;
      padding: 0px;
      width: 112px;
      color: #6bc08b;
      background: white;
    }
    .dialog-footer > button:hover {
      background: #6bc08b;
      color: white;
    }
    .cExport-scroll {
      height: 350px;
      overflow: auto;
      margin-top: 30px;
    }
    .el-upload-list__item {
    width: 300px;
    margin-left: 60px;
    }
    .cstyle{
      text-align: left;
      margin-bottom:10px;
    }
    .cstyle-second{
      margin-top: 20px;
    }

  }
</style>



