<template>
  <div class="achievemBox">
    <v-header></v-header>
    <div class="gradeSeeBox">
      <el-tabs type="border-card">
        <el-tab-pane label="考试管理">
          <exam-manage></exam-manage>
        </el-tab-pane>
        <el-tab-pane label="成绩查询">
          <view-the-results></view-the-results>
        </el-tab-pane>
        <el-tab-pane label="平均分">
          <average></average>
        </el-tab-pane>
        <el-tab-pane label="入围统计">
          <intotal></intotal>
        </el-tab-pane>
        <el-tab-pane label="临界生统计">
          <cricount></cricount>
        </el-tab-pane>
        <el-tab-pane label="成活率">
          <alivenum></alivenum>
        </el-tab-pane>
        <el-tab-pane label="名次段统计">
          <rank></rank>
        </el-tab-pane>
        <el-tab-pane label="分数段统计">
          <score></score>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="footer">
      <bottom></bottom>
    </div>
  </div>
</template>

<script>
import vHeader from '@/components/header'// 头部
import bottom from '@/components/bottom'// 底部
import examList from '@/views/exam/examList' // 考试管理
import gradeSee from '@/views/achievement/gradeSee' // 年级查询
import vClasssee from '@/views/achievement/vClassSee' // 班级查询
import pulled from '@/views/achievement/pulled' // 档次设置
import average from '@/views/achievement/average' // 平均分
import intotal from '@/views/achievement/intotal' // 入围统计
import cricount from '@/views/achievement/cricount' // 临界生统计
import alivenum from '@/views/achievement/alivenum' // 成活率
import rank from '@/views/achievement/rank' // 名次分段统计
import score from '@/views/achievement/score' // 分数段统计
import examManage from '@/views/achievement/examManage'
import viewTheResults from '@/views/achievement/viewTheResults'

export default {
  name: 'achievement',
  components: {
    vHeader,
    bottom,
    gradeSee,
    vClasssee,
    pulled,
    average,
    intotal,
    cricount,
    alivenum,
    rank,
    score,
    examList,
    examManage,
    viewTheResults
  },
  data: function () {
    return {
    }
  }

}
</script>

<style lang="scss">
body{
  background-color: #f2f4f5;
}
.achievemBox{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
 .gradeSeeBox{
    flex:1;
    width: 960px;
    margin: 10px auto;
    box-shadow: 0 0  6px #dbddde;
    border-radius: 10px;
    padding:0  0 20px  0;
    background-color: #fff;
    margin-bottom: 80px
  }
.el-tabs--border-card{
  background: #fff;
  border: none;
  -webkit-box-shadow: none;
  box-shadow:none;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
  border-bottom: 57px solid #fff;
  border-left: 31px solid transparent;
  border-right: 31px solid transparent;
  height: 0;
  /*width: 137px;*/
  color: #666666;
  font-size: 14px;
  background-color: transparent;

}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:first-child.is-active{
  border-left:none;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:last-child.is-active{
  border-right:none;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item{
  border: none;
  -webkit-transition: all 0s cubic-bezier(.645,.045,.355,1);
  transition: all 0s cubic-bezier(.645,.045,.355,1);
}
.el-tabs--border-card>.el-tabs__header{
  background-color: #f2f2f2;
  border-bottom: none;

}
.el-tabs__nav-scroll{
  line-height: 56px;
  border-top: 1px solid #e5e5e5;
  /*border-right: 1px solid #e5e5e5;*/
  /*border-left: 1px solid #e5e5e5;*/
}
.el-tabs--top.el-tabs--border-card .el-tabs__item:nth-child(2){
  padding: 0;
}
.el-tabs__item{
  line-height: 56px;
  width: 122px;
  text-align: center;
  padding: 0;
  height: 56px;
  font-size: 12px;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:hover{
  color: #6cc08c;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active:hover{
  color: #666666;
}
.el-tabs__content{
  padding: 10px 20px;
}
  .seeAchTit{
    width: 100%;
    .el-tabs--border-card > .el-tabs__header{
      width: 100%;
      background-color: #fff;
      border-bottom:1px solid #b8d6cd;
      overflow: hidden;
    }
    .el-tabs__nav{
      width: 100%;
    }
    .el-tabs__item {
      line-height: 45px;
      width: 33.5%;
      text-align: center;
      padding: 0;
      height: 45px;
      font-size: 12px;

    }
    .el-tabs--border-card > .el-tabs__header .el-tabs__item {
     color:#6cc08c ;
    }
    .el-tabs--border-card > .el-tabs__header .el-tabs__item.is-active{
      border: none;
      background-color: #b8d6cd ;
      color: #fff;
      height: 45px;
      line-height: 45px;
    }
    .el-tabs__nav-scroll {
      line-height: 45px;
      border: none;
    }
    .el-tabs--top.el-tabs--border-card .el-tabs__item:nth-child(2){
      padding: 0;
      border-right: 1px solid #b8d6cd;
      border-left: 1px solid #b8d6cd;
    }
    .el-tabs--border-card{
      border: 1px solid #b8d6cd;
    }
    .el-tabs--border-card>.el-tabs__content{
      padding: 0;
    }
    .footer{
      flex: 0
    }
  }
}
</style>



