   <template>
    <div class="class-add">
        <div class="class-addBox">
        </div>
         <div class="classAdd">
                <div>
                    <img src="@/assets/img/details-arrow-right_03.png" alt="">
                    <span>编辑班级信息</span>
                    <img src="@/assets/img/details-arrow-left_03.png" alt="">
                </div>
                <div class="classAddmain">
                    <div class="cd-first">
                      <el-form :rules="clsRules" ref="clsAddData" :model="clsEditData" inline>
                        <el-form-item label="班级名称:" prop="className">
                          <el-input v-model="clsEditData.className"></el-input>
                        </el-form-item>
                        <el-form-item label="班号:" prop="classNumber">
                          <el-input v-model="clsEditData.classNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="校区:" prop="classCampus">
                          <el-input v-model="clsEditData.classCampus"></el-input>
                        </el-form-item>
                        <el-form-item label="班级类型:" prop="classTypeId">
                          <el-select v-model="clsEditData.classTypeId">
                            <el-option
                            v-for="item in clsTypeData"
                            :key="item.classTypeId"
                            :value="item.classTypeId"
                            :label="item.classtypeName">
                            </el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="入学年度:" prop="classYear">
                          <el-select  v-model="clsEditData.classYear">
                            <el-option
                            v-for="item in getYearList"
                            :key="item"
                            :value="item"
                            :label="item">
                            </el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item label="学段:" prop="classPeriodId">
                          <el-select  v-model="clsEditData.classPeriodId">
                            <el-option
                              v-for="umPerItem in umPerItems"
                            :key="umPerItem.periodId"
                            :value="umPerItem.periodId"
                            :label="umPerItem.periodName">
                            </el-option>
                          </el-select>
                        </el-form-item>
                         <el-form-item label="班级层次:" prop="classLevel">
                          <el-select v-model="clsEditData.classLevelId">
                            <el-option
                            v-for="item in clevelItems"
                            :key="item.classLevelId"
                            :value="item.classLevelId"
                            :label="item.classlevelName">
                            </el-option>
                          </el-select>
                        </el-form-item>
                         <el-form-item label="班级简称:" prop="classAbbreviation">
                          <el-input v-model="clsEditData.classAbbreviation"></el-input>
                        </el-form-item>
                      </el-form>
                    </div>
                </div>
                <div class="classAddfooter">
                    <div slot="footer" class="dialog-footer">
                        <button class="save" @click="clsEditClick">修 改</button>
                        <button class="exit" @click="hideDialog">返 回</button>
                    </div>
                </div>
            </div>
            </div>
   </template>
<script>
export default {
  name: 'clsAddpopover',
  data () {
    return {
      umPerItems: null,
      clsTypeData: null,
      clevelItems: null,
      isUpdate: false,
      clsEditData: {},
      clsRules: {
        className: [
          { required: true, message: '班级名不能为空', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 12 个字符', trigger: 'blur' }
        ],
        classNumber: [
          { required: true, message: '班号不能为空', trigger: 'blur' },
          { min: 1, max: 12, message: '长度在 1 到 12 个字符', trigger: 'blur' }
        ],
        classCampus: [
          { required: true, message: '校区不能为空', trigger: 'blur' },
          { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }

        ],
        classPeriodId: [
          { required: true, message: '学段不能为空', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 12 个字符', trigger: 'blur' }
        ],
        classTypeId: [
          { required: true, message: '班级类型不能为空', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 12 个字符', trigger: 'blur' }

        ],
        classYear: [
          { required: true, message: '入学年度不能为空', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 12 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  props: ['clsRowData', 'stateChange'],
  methods: {
    hideDialog () {
      // 改 bug 点击编辑出现默认值消失逻辑
      // this.initClsData()
      if (this.isUpdate) {
        this.$emit('modify', this.clsEditData)
      }
      this.$emit('hideDialog')
    },
    initClsData () {
      this.clsEditData = {
        className: '',
        classNumber: '',
        classCampus: '',
        classPeriodId: '',
        classTypeId: '',
        classYear: '',
        classLevelId: '',
        classAbbreviation: ''
      }
    },
    findPeriod () {
      this.$http
        .get('/api/v1/schoolperiod/findInfo')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.umPerItems = res.data.data
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    findClsLevel () {
      this.$http.get('/api/v1/schoolclass/findLevel')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.clevelItems = res.data.data
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    findClsType () {
      this.$http.get('/api/v1/schoolclass/findType')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.clsTypeData = res.data.data
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    clsEditClick () {
      let updata = {
        classId: this.clsEditData.classId,
        className: this.clsEditData.className,
        classAbbreviation: this.clsEditData.classAbbreviation,
        classNumber: this.clsEditData.classNumber,
        classCampus: this.clsEditData.classCampus,
        classTypeId: this.clsEditData.classTypeId,
        classLevelId: this.clsEditData.classLevelId,
        classYear: this.clsEditData.classYear,
        classPeriodId: this.clsEditData.classPeriodId
      }
      this.$http
        .put(
          '/api/v1/schoolclass/updateClassInfo',
          this.$qs.stringify(updata)
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
              this.isUpdate = true
              this.hideDialog()
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    initSlctFun () {
      this.findPeriod()
      this.findClsLevel()
      this.findClsType()
    }

  },
  computed: {
    getYearList () {
      let curYear = new Date().getFullYear() + 5
      let ans = []
      while (curYear >= 2000) {
        ans.push(curYear--)
      }
      return ans
    }

  },
  watch: {
    'clsRowData' (cur, old) {
      this.clsEditData = JSON.parse(JSON.stringify(cur))
      if (this.clsEditData) {
        this.clsEditData.classYear = this.clsEditData.classYear.slice(0, 4)
      }
    },
    'stateChange' (cur, old) {
      this.initSlctFun()
    }
  }

  // mounted () {
  //   this.findPeriod()
  //   this.findClsLevel()
  //   this.findClsType()
  // }
}
</script>
   <style lang="scss">
  .class-add {
.class-addBox {
  position: fixed;
  background: black;
  top: 0;
  opacity: 0.7;
  filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
  width: 100%;
  height: 100%;
  min-width: 960px;
  z-index: 200;
}
.classAdd {
  background: white;
  position: absolute;
  width: 580px;
  height: 520px;
  border-radius: 4px;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 300;
}
.classAdd > div:nth-of-type(1) {
  height: 40px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  line-height: 40px;
  background: #6bc08b;
}
.classAdd > div:nth-of-type(1) > img {
  vertical-align: middle;
}
.classAddmain {
  text-align: left;
  font-size: 12px;
  color: #999;
}
.cd-first {
  margin-top: 50px;
  text-align: center;
}
.cd-first  input {
  width: 160px;
  border-radius: 5px;
  background: #f2f2f2;
  height: 35px;
  color: #999;
  padding-left: 5px;
}
.dt-input {
  width: 100px;
  border-radius: 5px;
  background: #f2f2f2;
  height: 35px;
  margin-left: 1px;
  color: #c3c3c3;
    padding-left: 5px;
}
.teacher {
  width: 100px;
  border-radius: 5px;
  background: #f5f7fa;
  height: 35px;
  margin-left: 1px;
  color: #999;
    padding-left: 5px;
}
#cd-classtype {
  border-radius: 5px;
  background: #f2f2f2;
  width: 200px;
  height: 35px;
  color: #c3c3c3;
  margin-left: 10px;
}

.classAddfooter {
  position: absolute;
  bottom: 20px;
  width: 580px;

}
.dialog-footer {
  text-align: center;

}
.dialog-footer > button {
  border: 1px solid #6bc08b;
  font-size: 14px;
  border-radius: 20px;
  height: 32px;
  line-height: 32px;
  padding: 0px;
  width: 112px;
  color: #6bc08b;
  background: white;
  // margin-left: 50px;
}
.dialog-footer > button:nth-child(2){
  margin-left: 50px;
}

.dialog-footer > button:hover {
  background: #6bc08b;
  color: white;
}
.el-input__inner {
    height: 30px;
    line-height: 30px;
    padding: 0 15px;
}
.el-form-item {
    margin-left: 5px;
}
.el-form-item__label {
  width: 82px;
}
}
</style>



