<template>
  <div id="main" style="width: 600px;height:400px;"></div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    drawLine () {
      var myChart = this.$echarts.init(document.getElementById('main'))
      // 绘制图表
      var option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ['班级任务数', '正差异', '负差异']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value'
          }
        ],
        xAxis: [
          {
            type: 'category',
            axisTick: {show: false},
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
          }
        ],
        series: [
          {
            name: '班级任务数',
            type: 'bar',
            label: {
              normal: {
                show: true,
                position: 'inside'
              }
            },
            data: [3, 5, 2, 4, 3, 20]
          },
          {
            name: '正差异',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true
              }
            },
            data: [ null, 4, null, null, 5, 6 ]
          },
          {
            name: '负差异',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'left'
              }
            },
            data: [-3, null, -1, -4, null, null]
          }
        ]
      }
      myChart.setOption(option)
    }
  },
  mounted () {
    this.drawLine()
  }
}
</script>



