      thetimeData: '',
      thetimeDatas: [], // 届次
      classTypeData: '',
      classTypeDatas: [], // 班级类型
      classLevelData: '',
      classLevelDatas: [], // 班级层次
      classData: '',
      classDatas: [], // 班级
      subjectData: '',
