      InTotalTotal: '',
      // 任务差异图
      taskDiffChartShow: false,
      // 入围贡献率图
      inTotalDevoteChartShow: false,
      // 单科入围统计
      sgInTotalChartShow: false,
      devoteChartData: [],
      totalScoreDatas: []

    }
  },
  methods: {
    // 初始化考试名称
    // initExamName () {
    //   this.examName = ''
    //   this.gradeData = ''
    //   this.thetimeData = ''
    //   this.classTypeData = ''
    //   this.classLevelData = ''

    //   this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: this.examName})
    //     .then(res => {
    //       if (res.status === 200) {
    //         if (res.data.code === 1) {
    //           this.examNames = res.data.data
    //         } else {
    //           console.log(res.data.msg)
    //           this.$message({
    //             //  饿了么的消息弹窗组件,类似toast
    //             showClose: true,
    //             message: res.data.msg,
    //             type: 'error'
    //           })
    //         }
    //       }
    //     })
    //     .catch(err => {
    //       console.log(err)
    //     })
    // },
    // 选择考试名称查询学段等信息
    examSlctClick () {
      this.gradeData = ''
      this.gradeDatas = []

      this.thetimeData = ''
      this.thetimeDatas = []

      this.classTypeData = ''
      this.classTypeDatas = []

      this.classLevelData = ''
      this.classLevelDatas = []

      this.classData = []
      this.classDatas = []

      this.subjectData = []
      this.subjectDatas = []

      this.tableData = []

      this.$http.get('/api/v1/schoolexam/findAllSchoolExamThetimeBySchoolExamParent',
        {
          params: {
            examParentId: this.examName
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.gradeDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据学段选择届次
    gradeSlctClick () {
      this.thetimeData = ''
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.gradeDatas.length; i++) {
        if (this.gradeData === this.gradeDatas[i].classPeriodId) {
          this.thetimeDatas = this.gradeDatas[i].thetimes
        }
      }
    },
    // 根据届次选择班级类型
    classTypeSlctClick () {
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.thetimeDatas.length; i++) {
        if (this.thetimeData === this.thetimeDatas[i].thetime) {
          this.classTypeDatas = this.thetimeDatas[i].classTypes
        }
      }
    },
    // 根据班级类型确定班级层次
    classLevelSlctClick () {
      this.classLevelData = ''

      for (let i = 0; i < this.classTypeDatas.length; i++) {
        if (this.classTypeData === this.classTypeDatas[i].classTypeId) {
          this.classLevelDatas = this.classTypeDatas[i].classLevels
        }
      }
    },
    // 根据班级层次选择班级
    classSlctClick () {
      this.$http.get('/api/v1/schoolexam/findClassAndSubjectByCondition',
        {
          params: {
            examParentId: this.examName,
            classPeriodId: this.gradeData,
            thetime: this.thetimeData,
            classTypeId: this.classTypeData,
            classLevelId: this.classLevelData
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.classDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据班级选择课程
    subjectSlctClick () {
      let subjectArr = []
      if (this.classData.length > 0) {
        for (let i = 0; i < this.classData.length; i++) {
          for (let j = 0; j < this.classDatas.length; j++) {
            if (this.classData[i] === this.classDatas[j].classId) {
              subjectArr.push(this.classDatas[j].subjects)
            }
          }
        }
      }

      let idList = []
      let examSubjectId = []
      subjectArr.forEach((val) => {
        examSubjectId = []
        val.forEach((ele) => {
          examSubjectId.push(ele.examSubjectId)
        })
        idList.push({
          examSubjectId: examSubjectId
        })
      })
      console.log(idList)
      let defCList = idList.reduce((acc, cur) => {
        if (acc.length === 0) {
          return cur.examSubjectId
        } else {
          let ans = []
          let curArr = cur.examSubjectId
          let accArr = acc
          curArr.forEach((val) => {
            if (accArr.indexOf(val) > -1) {
              ans.push(val)
            }
          })
          return ans
        }
      }, [])
      let courseLi = []
      subjectArr[0].forEach((val) => {
        if (defCList.indexOf(val.examSubjectId) > -1) {
          courseLi.push({
            examSubjectId: val.examSubjectId,
            examSubject: val.examSubject
          })
        }
      })
      courseLi.push({
        examSubjectId: 0,
        examSubject: '总分'
      })

      this.subjectDatas = courseLi
    },
    // 提交查询条件并将数据传递至表格
    findScoreRank () {
      this.hasTotal = false
      if (this.examName === '') {
        this.$message.error('考试名称不能为空')
        return false
      }
      if (this.gradeData === '') {
        this.$message.error('学段不能为空')
        return false
      }
      if (this.thetimeData === '') {
        this.$message.error('届次不能为空')
        return false
      }
      if (this.classTypeData === '') {
        this.$message.error('班级类型不能为空')
        return false
      }
      if (this.classData === '') {
        this.$message.error('班级不能为空')
        return false
      }
      if (this.subjectData.length <= 0) {
        this.$message.error('课程不能为空')
        return false
      }

      let updata = {
        parameters: {
          examParentId: this.examName,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          curriculumIdList: this.subjectData,
          classTypeId: this.classTypeData,
          classIdList: this.classData
        },
        'currentPage': this.inTotalcurrentPage,
        'pageSize': this.inTotalPageSize
      }
      this.$http.post('/api/v1/shortlisted/findShortlisted', updata)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              let arrlist = []
              let sbjctArr = []
              let totalArr = []
              let sbjctLevelArr = []
              let totalScoreArr = []

              for (let i = 0; i < res.data.data.content.length; i++) {
                if (res.data.data.content[i]) {
                  if (!res.data.data.content[i].classNumber) {
                    res.data.data.content[i].classNumber = '年级情况'
                  }
                  arrlist.push(res.data.data.content[i])
                  if (res.data.data.content[i].totalList) {
                    totalScoreArr.push(res.data.data.content[i].totalList)

                    res.data.data.content[i].totalList.forEach((val) => {
                      totalArr.push(val.gradeName)
                    })
                  }
                  if (res.data.data.content[i].shortlistedList) {
                    res.data.data.content[i].shortlistedList.forEach((val) => {
                      sbjctArr.push(val.subjectName)
                      val.gradeShorts.forEach((ele) => {
                        sbjctLevelArr.push(ele.gradeName)
                      })
                    })
                  }
                }
              }
              if (totalArr.length > 0) {
                this.hasTotal = true
                this.totalData = Array.from(new Set(totalArr))
                console.log('入围统计', this.totalData)
              }
              // 课程
              if (sbjctArr.length > 0) {
                this.hasSbjct = true
                this.sbjctData = Array.from(new Set(sbjctArr))
              }
              // 课程下的档次
              if (sbjctLevelArr.length > 0) {
                this.gradeShortsData = Array.from(new Set(sbjctLevelArr))
              }
              this.tableData = arrlist
              this.InTotalTotal = res.data.data.recordTotal
              // 入围贡献图
              let devoteChartData = []
              let tempObj = {}
              let classNames = []
              this.tableData.forEach((val) => {
                classNames.push(val.className)
              })
              // 科目
              this.sbjctData.forEach((val) => {
                devoteChartData.push({
                  classNames: classNames,
                  subjectName: val
                })
              })
              // 档次数组
              let levelData = []
              tempObj = {}

              devoteChartData.forEach((val) => {
                levelData = []
                this.gradeShortsData.forEach((val) => {
                  levelData.push({
                    levelName: val,
                    sData: [],
                    diffData: [],
                    taskData: []
                  })
                })
                arrlist.forEach((ele, count) => {
                  ele.shortlistedList.forEach((item) => {
                    if (val.subjectName === item.subjectName) {
                      // if (item.gradeShorts) {
                      //   while (item.gradeShorts.length < levelData.length) {
                      //     item.gradeShorts.push({

                      //     })
                      //   }
                      // }
                      if (item.gradeShorts) {
                        item.gradeShorts.forEach((arg, index) => {
                          levelData[index].sData.push({
                            value: arg.sshortNum,
                            name: classNames[count]
                          })
                          levelData[index].diffData.push(arg.zfDifferences)
                          levelData[index].taskData.push(arg.task)
                        })
                        return false
                      }
                    }
                  })
                })
                val.levelData = levelData
              })
              this.devoteChartData = devoteChartData
              // 总分

              let isKey = false
              let totalScoreDatas = []
              tempObj = {}
              if (this.hasTotal) {
                this.gradeShortsData.forEach((val) => {
                  tempObj = {}
                  tempObj.gradeName = val
                  tempObj.diffData = []
                  tempObj.taskData = []
                  totalScoreArr.forEach((ele) => {
                    ele.forEach((item) => {
                      if (val === item.gradeName) {
                        tempObj.diffData.push(item.zfDifferences)
                        tempObj.taskData.push(item.task)
                        isKey = true
                        return false
                      }
                    })
                    if (!isKey) {
                      tempObj.diffData.push(0)
                      tempObj.taskData.push(0)
                    }
                  })
                  tempObj.classNames = classNames
                  totalScoreDatas.push(tempObj)
                })
              }
              this.totalScoreDatas = totalScoreDatas

              console.log('入围统计', totalScoreDatas, totalScoreArr)
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    inTotalHandleSizeChange (val) {
      this.inTotalPageSize = val
      this.inTotalcurrentPage = 1
      this.findScoreRank()
    },
    // 更新页数并 更新表数据
    inTotalHandleCurrentChange (val) {
      this.inTotalcurrentPage = val
      this.findScoreRank()
    },
    // ----------------------- 任务差异图------------------------
    taskDiffClick () {
      this.taskDiffChartShow = true
    },
    taskDiffCloseClick () {
      this.taskDiffChartShow = false
    },
    taskDiffChartdrawLine () {
      var myChart
      this.$nextTick(() => {
        this.$refs.chart1.forEach((val, index) => {
          myChart = this.$echarts.init(val)
          // 绘制图表
          myChart.setOption({
            tooltip: {
              trigger: 'axis',
              axisPointer: { // 坐标轴指示器，坐标轴触发有效
                type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
              }
            },
            legend: {
              data: ['任务数', '差异数']
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            xAxis: [
              {
                type: 'category',
                axisTick: {show: false},
                data: this.totalScoreDatas[index].classNames.slice(0, -1)
              }
            ],
            yAxis: [{
              type: 'value'
            }],

            series: [
              {
                name: '任务数',
                type: 'bar',
                label: {
                  normal: {
                    show: true,
                    position: 'top'
                  }
                },
                data: this.totalScoreDatas[index].taskData.slice(0, -1)
              },
              {
                name: '差异数',
                type: 'bar',
                // stack: '总量',
                label: {
                  normal: {
                    show: true,
                    position: 'inside'
                  }
                },
                data: this.totalScoreDatas[index].diffData.slice(0, -1)
              }]
          })
        })
      })
    },

    // ------------------------ 入围贡献率图----------------------
    inTotalDevoteClick () {
      this.inTotalDevoteChartShow = true
    },
    inTotalDevoteCloseClick () {
      this.inTotalDevoteChartShow = false
    },
    /**
     *  循环遍历出各个课程的不同对比图
     *  sbjctData: [],
     */
    inTotalDevotedrawLine () {
      var myChart
      this.$nextTick(() => {
        this.$refs.chart.forEach((val, index) => {
          myChart = this.$echarts.init(val)
          let tempObj = {}
          let sizeArr = []
          let n = Math.floor(100 / (this.gradeShortsData.length + 1))
          this.devoteChartData[index].levelData.forEach((val, count) => {
            tempObj = {
              name: val.levelName + '档次',
              type: 'pie',
              radius: (n - 5) + '%',
              center: ['50%', (count + 1) * n + '%'],
              label: { // 饼图图形上的文本标签
                normal: {
                  show: true,
                  position: 'top', // 标签的位置
                  textStyle: {
                    fontWeight: 300,
                    fontSize: 14 // 文字的字体大小
                  },
                  formatter: '{b} : {c} ({d}%)'

                }
              },
              data: val.sData.slice(0, -1),
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }

            }
            sizeArr.push(tempObj)
          })
          // 绘制图表
          myChart.setOption({
            title: {
              text: this.devoteChartData[index].subjectName,
              x: 'center'
            },
            tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
            legend: {
              type: 'scroll',
              orient: 'vertical',
              right: 10,
              top: 20,
              bottom: 20,
              data: this.devoteChartData[index].classNames.slice(0, -1)
            },

            series: sizeArr
          })
        })
      })
    },

    // ------------------------ 单科入围统计 ---------------------
    sgInTotalClick () {
      this.sgInTotalChartShow = true
    },
    sgInTotalCloseClick () {
      this.sgInTotalChartShow = false
    },
    // ------------------------- 导出 ---------------------------
    inTotalExportClick () {
      let updata = {
        parameters: {
          examParentId: this.examName,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          curriculumIdList: this.subjectData,
          classTypeId: this.classTypeData,
          classIdList: this.classData
        }
      }
      this.$http({
        method: 'post',
        url: '/api/v1/shortlisted/shortlistedExport',
        data: updata,
        responseType: 'blob'
      })
        .then(res => {
          if (res.status === 200) {
            let blob = res.data
            let reader = new FileReader()
            reader.readAsDataURL(blob)
            reader.onload = (e) => {
              let a = document.createElement('a')
              a.download = '入围统计表.xls'
              a.href = e.target.result
              document.body.appendChild(a)
              a.click()
              document.body.removeChild(a)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    clsCourseClick () {

    },
    remoteMethod (query) {
      var flag = 1
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },

    // 初始化考试名称
    initExamName (query, flag) {
      if (flag === 1) {
        this.gradeData = ''
        this.thetimeData = ''
        this.classTypeData = ''
        this.classLevelData = ''
      }

      this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: query})
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.loading = false
              if (flag === 1) {
                this.examNames = res.data.data
              } else {
                this.compareExamDatas = res.data.data
              }
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    scoreRankClick () {
      let cope = {singelInput: '', seen: true}
      this.addInputs.push(cope)
      console.log(this.addInputs)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  },
  computed: {
    compareName: function () {
      return this.upscoredata + this.upline + this.score
    }

  },
  watch: {
    'inTotalDevoteChartShow' (cur, old) {
      if (cur) {
        this.inTotalDevotedrawLine()
      }
    },
    'taskDiffChartShow' (cur, old) {
      if (cur && this.hasTotal) {
        this.taskDiffChartdrawLine()
      }
    }
  },
  mounted () {
    this.remoteMethod()
  }
}
</script>

