   <template>
    <div class="classstdsec">
        <div class="classstdsecBox" id="classstdsecBox">
        </div>
         <div class="cstdSec" id="cstdSec">
            <div>
              <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
              <span>新增学段</span>
              <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
            </div>
                <div class="cstdSecmain">
                    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
                      <el-row>
                        <el-col :span="12">
                          <el-form-item label="学段名称:" prop="periodName">
                          <el-input v-model="form.periodName"></el-input>
                          </el-form-item>
                        </el-col>
                        <el-col :span="12">
                          <el-form-item label="入学年龄:" prop="periodAge">
                          <el-input v-model="form.periodAge"></el-input>
                          </el-form-item>
                        </el-col>
                      </el-row>
                      <el-row>
                        <el-col :span="12">
                          <el-form-item label="学制:" prop="periodSystem">
                          <el-input v-model.number="form.periodSystem" placeholder="输入1-100的数字" ref="perSys" @blur="createGradeByperSys"></el-input>
                          </el-form-item>
                        </el-col>
                        <el-col :span="12">
                        <el-form-item label="使用状态:" prop="state">
                          <template>
                            <el-select v-model="form.state" placeholder="请选择">
                              <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                              </el-option>
                            </el-select>
                          </template>
                        </el-form-item>
                        </el-col>
                      </el-row>
                    <div class="cstdSecfooter">
                      <div slot="footer" class="dialog-footer">
                        <el-button class="save" @click="submitForm('form')">保 存</el-button>
                        <el-button class="exit" @click="hideDialog">返 回</el-button>
                      </div>
                    </div>
                    </el-form>

                    <div class="stdsecmain">

                      <el-row class="stdsec-three">

                        <el-col :span="12">
                          <span>年级名称列表</span>
                        </el-col>
                        <el-col :span="12">
                        </el-col>
                      </el-row>
                      <div class="stdsec-scroll">
                        <template>
                          <el-table
                            :data="gradeList"
                            border
                           @cell-dblclick="inputEditToggle"
                            style="width: 300px; margin: 0 auto; margin-top: 20px">
                            <el-table-column
                              type=index
                              width="50">
                            </el-table-column>
                            <el-table-column
                              label="年级名称"
                              width="248">
                               <template slot-scope="scope">
                                 <input type="text" class="stdsec-scond" v-model="scope.row.gradeName" readonly>
                                 <!-- <div>{{scope.row}}</div> -->
                               </template>
                            </el-table-column>

                          </el-table>
                        </template>
                      </div>
                    </div>

          </div>
        </div>
      </div>
   </template>
<script>
export default {
  name: 'levelpopover',
  props: ['stdSecRowData'],
  data () {
    return {
      isUpdate: false,
      form: {
        periodName: '',
        periodSystem: '',
        periodAge: '',
        state: ''
      },
      rules: {
        periodName: [
          { required: true, message: '请输入学段名称', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        periodSystem: [
          { required: true, message: '学制不能为空且必须为数字值', trigger: 'blur',type: 'number'}
          // {type: 'number', message: '学制必须为数字值', trigger: 'blur'}
        ],
        periodAge: [
          { required: true, message: '请输入入学年龄', trigger: 'blur' },
          { min: 1, max: 10, message: '长度在 1 到 10 个字符', trigger: 'blur' }
        ],
        value: [
          { required: true, message: '请选择活动状态', trigger: 'change' }
        ]
      },

      gradeList: [
        {gradeName: '1年级'},
        {gradeName: '2年级'}
      ],
      options: [{
        value: '1',
        label: '正在使用'
      }, {
        value: '0',
        label: '已经停用'
      }],

      isAddShow: false,
      itemVal: '',
      liLen: 0,
      clsdf: [],
      preAddLi: [],
      isfocus: false,
      addData: {
        clstype: this.itemVal
      }
      // isUpdate: false
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
      if (this.isUpdate) {
        this.$emit('modify', this.updateList)
      }
      this.form.periodName=''
      this.form.periodAge=''
      this.form.periodSystem=''
      this.form.state=''
      this.gradeList = []
    },
    addComShow () {
      this.isAddShow = !this.isAddShow
      this.itemVal = ''
    },

    /** @augments
     * 双击input 获得焦点
     * input onblur 触发 修改
     */
    inputEditToggle (row, column, cell, event) {
      console.log('inputEditToggle', event.target.nodeName.toUpperCase() === 'INPUT')
      if (event.target && event.target.nodeName.toUpperCase() === 'INPUT') {
        let curTarget = event.target
        curTarget.removeAttribute('readonly')
        curTarget.focus()
        curTarget.style.backgroundColor = '#666'
        curTarget.style.color = 'white'

        curTarget.onblur = () => {
          curTarget.setAttribute('readonly', 'true')
          curTarget.style.backgroundColor = 'white'
          curTarget.style.color = '#999'
          console.log(this.gradeList)
        }
      }
    },
    /**
     *  根据学制动态生成默认年级名称
     */
    createGradeByperSys () {
      if (this.form.periodSystem === '') {
        this.gradeList = []
        return
      }
      console.log('create', typeof this.form.periodSystem, this.form.periodSystem > 0)
      if (this.form.periodSystem < 0 || this.form.periodSystem > 100) {
        this.gradeList = []
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '学制需要为1-100整数',
          type: 'success'
        })
      }
      let name = '年级'
      if (this.form.periodSystem > 0 && this.form.periodSystem <= 100) {
        this.gradeList = []
        let num = this.form.periodSystem
        while (num--) {
          this.gradeList.push({gradeName: `${num + 1}` + name})
        }
        this.gradeList.reverse()
      }
    },
    /**
     *  点击保存提交新增学段
     */
    submitForm (formName) {
      console.log(this.$refs[formName])
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('submit!')
          this.udStdSec()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    udStdSec () {
      let gdArr = []
      for (let li of this.gradeList) {
        gdArr.push(li.gradeName)
      }
      let updata = {
        periodName: this.form.periodName,
        periodAge: this.form.periodAge,
        periodSystem: this.form.periodSystem,
        state: this.form.state,
        addGradeName: gdArr

      }
      console.log('updata', updata)
      this.$http
        .post(
          '/api/v1/schoolperiod/addPeriodInfo',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'success'

              })
              this.isUpdate = true
              this.$emit('modify')
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })

        this.$emit('hideDialog')
        this.form.periodName=''
        this.form.periodAge=''
        this.form.periodSystem=''
        this.form.state=''
        this.gradeList = []
    }

  // -------------编辑逻辑---------------------------

  }, /* method end */

  watch: {
    stdSecRowData (curVal, oldVal) {
      console.log('子信息', curVal, oldVal, this.stdSecRowData)
    }
  },
  computed: {
    updateList () {
      return this.isUpdate
    }
  },
  mounted () {

  }
}
</script>
<style lang="scss">
.classstdsec {
.classstdsecBox {
  position: fixed;
  background: black;
  top: 0;
  opacity: 0.7;
  filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
  width: 100%;
  height: 100%;
  // height: 2000px;
  min-width: 960px;
  z-index: 100;
}
.cstdSec {
  background: white;
  position: absolute;
  width: 575px;
  height: 675px;
  border-radius: 4px;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 120;
}
.cstdSec > div:nth-of-type(1) {
  text-align: center;
  height: 40px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  line-height: 40px;
  background: #6bc08b;
}
.cstdSec > div:nth-of-type(1) > img {
  vertical-align: middle;
}
.cstdSecmain > div:nth-of-type(1) {
  margin-top: 20px;
  color: #b8b8b8;
  font-size: 16px;
}
.cstdSecmain > div:nth-of-type(1) > span {
  margin-left: 20px;
}
.cstdSecmain {
  color: #c3c3c3;
}

#project {
  border-radius: 5px;
  background: #f2f2f2;
  width: 90px;
  height: 35px;
  appearance: none;
  margin-left: 25px;
  margin-top: 10px;
  color: #c3c3c3;
}
.popoverinput {
  border-radius: 5px;
  background: #f2f2f2;
  width: 90px;
  height: 35px;
  margin-left: 18px;
  color: #c3c3c3;
}
.cstdSecfooter {
  position: absolute;
  left: 185px;
  bottom: 20px;
}

.stdsec-scond,
.stdsec-four {
  // font-size: 14px;
    font-size: 16px;
    line-height: 30px;
    padding: 0 5px;
    width: 248px;
    color: #999;
    margin-left: -10px;
    // .el-table td, .el-table th {
    //   padding: 0;
    // }
}
.stdsec-scroll .el-table td, .el-table th {
      padding: 0;
    }

  .stdsec-scond button,
  .stdsec-four button{
    padding: 7px 25px;

  }
  .stdsec-scroll {
    height: 300px;
    border:1px solid rgb(184, 171, 171);
    overflow: auto;
    margin-top: 10px;
  }

.dialog-footer > button {
  border: 1px solid #6bc08b;
  font-size: 14px;
  border-radius: 20px;
  height: 32px;
  line-height: 32px;
  padding: 0px;
  width: 112px;
  color: #6bc08b;
  background: white;
}
.dialog-footer > button:hover {
  background: #6bc08b;
  color: white;
}
.el-input__inner {
  height: 36px;
  line-height: 36px;
  width: 175px;
  background: #f2f2f2;
}
.el-form-item {
  color: #666;
  font-size: 14px;
  margin-top: 25px;
  margin-bottom: 0px;
}
.el-form-item.is-required .el-form-item__label:before {
  content: ''
}
.el-form-item__error {
  left: 17px;
}
}
</style>



