<template>
  <div class="clsCourse">
      <div class="clsCourseBox" >
      </div>
      <div class="cls-course">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
            <span>班级分配课程</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
        </div>
        <div class="cls-coursemain">
          <div>
            <template>
              <el-transfer
              v-model="activeCourseArr"
              :titles="['全部课程','班级课程']"
              :props="changeProps"
              filterable
              @change="handleChange"
              :data="AllCourseData">
              </el-transfer>
            </template>
          </div>

        </div>
          <div class="cls-coursefooter">
            <div slot="footer" class="dialog-footer">
              <button class="exit" @click="hideDialog">返 回</button>
            </div>
          </div>
      </div>
  </div>
  </template>
<script>
export default {
  name: 'clsCoursepopover',
  data () {
    return {
      clsAcData: null,
      AllCourseData: [],
      activeCourseArr: [],
      changeProps: {
        key: 'subjectId',
        label: 'subjectName'
      },
      classIdList: [],
      teacherInfos: [],
      loading: false,
      activeData: []
    }
  },
  props: ['clsAddCourseRowData'],
  methods: {
    hideDialog () {
      this.initData()
      if (this.isUpdate) {
        console.log(this.isUpdate)
        this.$emit('modify')
        this.isUpdate = false
      }
      this.$emit('hideDialog')
    },
    // 初始化考试名称
    // remoteMethod (query) {
    //   var flag = 1
    //   if (query !== '') {
    //     this.loading = true
    //     this.initExamName(query, flag)
    //   } else {
    //     this.initExamName(query, flag)
    //   }
    // },
    // // 初始化考试名称
    // initExamName (query, flag) {
    //   let updata = {
    //     parameters: {
    //       vague: query
    //     }
    //   }
    //   this.$http
    //     .post(
    //       '/api/v1/personnelManagement/selectAdminInfoByVague',
    //       updata
    //     )
    //     .then(res => {
    //       if (res.status === 200) {
    //         if (res.data.code === 1) {
    //           this.loading = false
    //           // this.teacherInfos = res.data.data
    //           let curData = res.data.data.content
    //           let tempArr = []
    //           for (let item of curData) {
    //             tempArr.push({
    //               staffNumber: item.staffNumber,
    //               fullName: item.fullName
    //             })
    //           }
    //           this.teacherInfos = tempArr
    //           console.log('老师信息', this.teacherInfos)
    //         } else {
    //           console.log(res.data.msg)
    //           this.$message({
    //             //  饿了么的消息弹窗组件,类似toast
    //             showClose: true,
    //             message: res.data.msg,
    //             type: 'error'
    //           })
    //         }
    //       }
    //     })
    //     .catch(err => {
    //       console.log(err)
    //     })
    // },

    initData () {
      this.activeCourseArr = []
    },
    handleChange (value, direction, movedKeys) {
      console.log(value, direction, movedKeys)

      this.updateSlctCourseFun()

      if (direction === 'right') {
        this.clsAddCourse(movedKeys)
      } else {
        this.clsDelCourse(movedKeys)
      }
    },
    clsAddCourse (keys) {
      let arr = Array.from(keys)
      let updata = {
        classId: this.classIdList,
        subjectId: arr
      }
      this.$http
        .post(
          '/api/v1/schoolsubject/SelectSubjectRight',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.$message({
                showClose: true,
                message: '操作成功',
                type: 'success'
              })
              this.isUpdate = true
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    clsDelCourse (keys) {
      let arr = Array.from(keys)
      let updata = {
        classId: this.classIdList,
        subjectId: arr
      }
      this.$http
        .post(
          '/api/v1/schoolsubject/SelectSubjectLeight',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.$message({
                showClose: true,
                message: '操作成功',
                type: 'success'
              })
              this.isUpdate = true
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    initCourseData () {
      this.AllCourseData = []
      this.$http
        .get(
          '/api/v1/schoolsubject/findSubjectInfo'
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              let data = res.data.data
              if (data.length > 0) {
                data.forEach((val) => {
                  this.AllCourseData.push(
                    {
                      subjectId: val.subjectId,
                      subjectName: val.subjectName
                    }
                  )
                })
              }
              // 更新
              this.updateSlctCourseFun()
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /** 初始化 激活的课程， 和班级的id集合 */
    getDfCouse (data) {
      let idList = []
      let subjectId = []
      let classIdList = []
      data.forEach((val) => {
        subjectId = []
        classIdList.push(val.classId)
        val.subject.forEach((ele) => {
          subjectId.push(ele.subjectId)
        })
        idList.push({
          subjectId: subjectId
        })
      })
      this.classIdList = classIdList
      console.log(idList)
      let defCList = idList.reduce((acc, cur) => {
        if (acc.length === 0) {
          return cur.subjectId
        } else {
          let ans = []
          let curArr = cur.subjectId
          let accArr = acc
          curArr.forEach((val) => {
            if (accArr.indexOf(val) > -1) {
              ans.push(val)
            }
          })
          return ans
        }
      }, [])
      return defCList
    },
    updateSlctCourseFun () {
      let AllCourseDataCopy = JSON.parse(JSON.stringify(this.AllCourseData))
      let ans = []
      for (let item of AllCourseDataCopy) {
        if (this.activeCourseArr.indexOf(item.subjectId) > -1) {
          item.teacherName = ''
          ans.push(item)
        }
      }
      this.activeData = ans
    }
  }, /** method */
  watch: {
    'clsAddCourseRowData' (cur, old) {
      this.clsAcData = cur
      this.activeCourseArr = this.getDfCouse(cur)
      console.log('this.clsAcData', cur)
      this.initCourseData()
    }

  },
  mounted () {
  }

}
</script>
<style lang="scss">
.clsCourse {
  .clsCourseBox {
    position: fixed;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    //  height: 2000px;
    min-width: 960px;
    z-index: 200;
  }
  .cls-course {
    background: white;
    position: absolute;
    width: 900px;
    height: 650px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 300;
  }
  .cls-course > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .cls-course > div:nth-of-type(1) > img {
    vertical-align: middle;
  }
  .cls-coursemain > div:nth-of-type(1) {
    margin-top: 28px;
    text-align: center;
    color: #999;
  }
  .cls-coursemain > div:nth-of-type(1) > span {
    margin-left: 20px;
  }
  .cls-coursemain {
    color: #c3c3c3;
    .el-transfer-panel {
      width: 337px;
      height: 498px;
      text-align: left;
    }

  }

  .popoverinput {
    border-radius: 5px;
    background: #f2f2f2;
    width: 90px;
    height: 35px;
    margin-left: 18px;
    color: #c3c3c3;
  }
  .cls-coursefooter {
    width: 1550px;
  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }
  .tc-c {
    display: inline-block;
    border: 1px solid #d8dce5;
    width: 282px;
    border-radius: 5px;
    line-height: 37px;
    font-size: 14px;
    color: #6d7382;
  }

}
</style>



