   <template>
    <div class="stdsecEdit">
      <div class="stdsecEditBox" id="stdsecEditBox">
      </div>
        <div class="stdsecEdit" id="stdsecEdit">
          <div>
            <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
            <span>修改学段</span>
            <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
          </div>
            <div class="stdsecEditmain">
              <el-form ref="form"  :model="parentData" label-width="80px">
                <el-row>
                  <el-col :span="12">
                    <el-form-item label="学段名称:" prop="periodName">
                    <el-input v-model="parentData.periodName" :disabled="true"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="12">
                    <el-form-item label="入学年龄:" prop="periodAge">
                    <el-input v-model="parentData.periodAge" :disabled="true"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <el-form-item label="学制:" prop="periodSystem">
                    <el-input v-model.number="parentData.periodSystem" placeholder="输入1-100的数字" ref="perSys" :disabled="true"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="12">
                  <el-form-item label="使用状态:" prop="state">
                    <template>
                      <el-select v-model="parentData.state" placeholder="请选择">
                        <el-option
                          v-for="item in options"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                      </el-select>
                    </template>
                  </el-form-item>
                  </el-col>
                </el-row>

            </el-form>
          <div class="stdsecmain">
            <el-row class="stdsec-three">
              <el-col :span="12">
                <span>年级名称列表</span>
              </el-col>
              <el-col :span="12">
              </el-col>
            </el-row>
            <div class="stdsec-scroll">
              <p style="color:red;font-size:12px">**双击表格修改表格信息</p>
              <template>
                <el-table
                  :data="parentData.gradeList"
                  border
                  @cell-dblclick="inputEditToggle"
                  style="width: 300px; margin: 0 auto; margin-top: 20px">
                  <el-table-column
                    type=index
                    width="50">
                  </el-table-column>
                  <el-table-column
                    label="年级名称"
                    width="248">
                      <template slot-scope="scope">
                        <input type="text" class="stdsec-scond" v-model="scope.row.gradeName" readonly>
                        <!-- <div>{{scope.row}}</div> -->
                      </template>
                  </el-table-column>
                </el-table>
              </template>
            </div>
          </div>
          <div class="stdsecEditfooter">
            <div slot="footer" class="dialog-footer">
              <button class="save" @click="updatePer">修 改</button>
              <button class="exit" @click="hideDialog">返 回</button>
            </div>
          </div>

        </div>
      </div>
      </div>
   </template>
<script>
export default {
  name: 'stdseceditpopover',
  props: ['stdSecRowData'],
  data () {
    return {
      parentData: {
        periodName: '',
        periodAge: '',
        periodSystem: '',
        state: '',
        gardeList: null
      },
      options: [{
        value: '1',
        label: '正在使用'
      }, {
        value: '0',
        label: '停用'
      }],
      isUpdate: false,
      isfocus: false
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
      if (this.isUpdate) {
        this.isUpdate = false
        this.$emit('modify')
      }
    },
    /**
     * 双击input 获得焦点
     * input onblur 触发 修改
     */
    inputEditToggle (row, column, cell, event) {
      console.log('inputEditToggle', event.target.nodeName.toUpperCase() === 'INPUT')
      if (event.target && event.target.nodeName.toUpperCase() === 'INPUT') {
        let curTarget = event.target
        curTarget.removeAttribute('readonly')
        curTarget.focus()
        curTarget.style.backgroundColor = '#666'
        curTarget.style.color = 'white'

        curTarget.onblur = () => {
          curTarget.setAttribute('readonly', 'true')
          curTarget.style.backgroundColor = 'white'
          curTarget.style.color = '#999'
          console.log('信息', this.gradeList)
        }
      }
    },
    /**
     *  修改异步请求
     */

    updatePer () {
      let updata = {
        periodId: this.parentData.periodId,
        gradeList: this.parentData.gradeList
      }
      console.log('信息updata', updata)
      this.$http
        .put(
          '/api/v1/schoolperiod/updatePartInfo',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                showClose: true,
                message: '修改成功',
                type: 'success'
              })
              this.isUpdate = true
              this.hideDialog()
            } else {
              console.log(res.data.msg)
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }

  }, /* method end */

  watch: {
    // stdSecRowData (curVal, oldVal) {
    //   this.parentData = curVal
    //   console.log('子信息', this.parentData)
    // }
    'stdSecRowData' (cur, old) {
      console.log('cur', cur, old)
      this.parentData = JSON.parse(JSON.stringify(cur))
    }
  },
  computed: {
    modifylist () {
      return this.parentData
    }

  }

}
</script>
<style lang="scss">
.stdsecEdit {
.stdsecEditBox {
  position: absolute;
  background: black;
  top: 0;
  opacity: 0.7;
  filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
  width: 100%;
  height: 100%;
  //  height: 2000px;
  min-width: 960px;
  z-index: 100;
}
.stdsecEdit {
  background: white;
  position: absolute;
  width: 575px;
  height: 675px;
  border-radius: 4px;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 120;
}
.stdsecEdit > div:nth-of-type(1) {
  text-align: center;
  height: 40px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  line-height: 40px;
  background: #6bc08b;
}
.stdsecEdit > div:nth-of-type(1) > img {
  vertical-align: middle;
}
.stdsecEditmain > div:nth-of-type(1) {
  margin-top: 20px;
  color: #b8b8b8;
  font-size: 16px;
}
.stdsecEditmain > div:nth-of-type(1) > span {
  margin-left: 20px;
}
.stdsecEditmain {
  color: #c3c3c3;
}

#project {
  border-radius: 5px;
  background: #f2f2f2;
  width: 90px;
  height: 35px;
  appearance: none;
  margin-left: 25px;
  margin-top: 10px;
  color: #c3c3c3;
}
.popoverinput {
  border-radius: 5px;
  background: #f2f2f2;
  width: 90px;
  height: 35px;
  margin-left: 18px;
  color: #c3c3c3;
}
.stdsecEditfooter {
  position: absolute;
  left: 185px;
  bottom: 20px;
}

.stdsec-scond,
.stdsec-four {
  // font-size: 14px;
    font-size: 16px;
    line-height: 30px;
    padding: 0 5px;
    width: 248px;
    color: #999;
    margin-left: -10px;
    // .el-table td, .el-table th {
    //   padding: 0;
    // }
}
.stdsec-scroll .el-table td, .el-table th {
      padding: 0;
    }

  .stdsec-scond button,
  .stdsec-four button{
    padding: 7px 25px;

  }
  .stdsec-scroll {
    height: 300px;
    border:1px solid rgb(184, 171, 171);
    overflow: auto;
    margin-top: 10px;
  }

.dialog-footer > button {
  border: 1px solid #6bc08b;
  font-size: 14px;
  border-radius: 20px;
  height: 32px;
  line-height: 32px;
  padding: 0px;
  width: 112px;
  color: #6bc08b;
  background: white;
}
.dialog-footer > button:hover {
  background: #6bc08b;
  color: white;
}
.el-input__inner {
  height: 36px;
  line-height: 36px;
  width: 175px;
  background: #f2f2f2;
}
.el-form-item {
  color: #666;
  font-size: 14px;
  margin-top: 25px;
  margin-bottom: 0px;
}
.el-form-item.is-required .el-form-item__label:before {
  content: ''
}
.el-form-item__error {
  left: 17px;
}
}
</style>



