   <template>
    <div class="export">
      <div class="exportBox" id="exportBox">
        </div>
          <div class="cExport" id="cExport">
            <div>
              <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
              <span>班级Excel导入</span>
               <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
            </div>
            <div class="cExportmain">
              <div class="cExport-scroll">
                   <el-upload
                    class="upload-demo"
                    ref="upload"
                    action="/web/api/uploadFile"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :on-change="handleChange"
                    :before-upload="beforeUpload"
                    :auto-upload="false"
                    :limit="1"
                    :multiple="false">
                    <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                    <div slot="tip" class="el-upload__tip">上传模板只能是 xls、xlsx格式!并且文件小于12M</div>
                    </el-upload>
              </div>
                <div class="cExportfooter">
                  <div slot="footer" class="dialog-footer">
                      <!-- <button class="save" @click="hideDialog">保 存</button> -->
                    <button  @click="submitUpload">导 入</button>

                      <button class="exit" @click="hideDialog">关 闭</button>
                  </div>
                </div>
            </div>
          </div>
        </div>
   </template>
<script>
export default {
  name: 'exportpopover',
  props: {},
  data () {
    return {
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
    },
    submitUpload () {
      this.$refs.upload.submit()
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    handleChange (file, fileList) {
      console.log(file)
      console.log(fileList)
    },
    beforeUpload: function (file) {
      const extension = file.name.split('.')[1] === 'xls'
      const extension2 = file.name.split('.')[1] === 'xlsx'
      const isLt2M = file.size / 1024 / 1024 < 10
      if (!extension && !extension2) {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '上传模板只能是 xls、xlsx格式!',
          type: 'error'
        })
        return false
      }
      if (!isLt2M) {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '上传模板大小不能超过 10MB!',
          type: 'error'
        })
        return false
      }
      if ((extension || extension2) && isLt2M) {
        console.log(file)
        // 这里是重点，将文件转化为formdata数据上传
        let fd = new FormData()
        fd.append('file', file)
        this.$http.post('/api/v1/schoolclass/bathImportInfo', fd).then((res) => {
          if (res.status === 200) {
            let arr = res.data.data
            console.log('arr', res)
            let arr2 = []
            arr.forEach((val) => {
              arr2.push(val + '<br>')
            })
            this.$alert(arr2.join(','), '导入信息', {
              dangerouslyUseHTMLString: true
            })
          }
        }, (err) => {
          console.log(err)
        })
      }
      return false
    }

  }
}
</script>
<style lang="scss">
.export {
    text-align: center !important;
  .exportBox {
    position: fixed;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    // height: 2000px;
    min-width: 960px;
    z-index: 200;
  }
  .cExport {
    background: white;
    position: absolute;
    width: 420px;
    height: 435px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 300;
  }
  .cExport > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .cExport > div:nth-of-type(1) > img {
    vertical-align: middle;
  }

  .cExportmain {
    color: #c3c3c3;
  }
  .cExport-first,
  .cExport-scond,
  .cExport-three,
  .cExport-four {
    text-align: left;
    margin-top :10px;
    margin-left: 20px;

  }
  .cExport-first {
    margin-top :20px;
    margin-left: 20px;
  }
  .cExport-first button {
    padding: 7px 50px;
  }
  .cExport-scond,
  .cExport-four {
    // font-size: 14px;
     input {
      font-size: 16px;
      line-height: 26px;
      padding: 0 5px;
      width: 190px;
      border:1px solid #6bc08b;
      color: #999

    }
  }
  .cExport-scond button,
  .cExport-four button{
    padding: 7px 25px;

  }
  .cExportfooter {
    position: absolute;
    bottom: 20px;
    width: 420px;
    margin: 0 auto;
  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }
  .cExport-scroll {
    height: 200px;
    overflow: auto;
    margin-top: 60px;
  }
    .el-upload-list__item {
    width: 300px;
    margin-left: 60px;
    }
  .el-upload__tip {
      font-size: 16px;
    }

}
</style>



