<template>
  <div class="examListPags">
    <v-header></v-header>
    <div class="examListBox">
      <div class="examAdd">
        <p>
          <router-link  :to="{path:'exam'}"> <i class="el-icon-plus"></i>新增考试</router-link>
        </p>
      </div>
      <div class="examListQuery">
        <div class="examListQueryTit">
          <p>查询条件</p>
        </div>
        <div class="examListQueryCont">
          <el-row :gutter="20">
            <el-form ref="examlistData" :model="examlistData" label-width="100px">

              <el-col :span="8">
                <el-form-item label="选择考试名称">
                <el-select v-model="examlistData.examName" placeholder="选择考试名称" @change="examListQuerySearchFun">
                  <el-option
                    v-for="item in examListNameSelect"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="输入考试类别">
                  <el-input v-model="examlistData.vague"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <p class="v-classSeeQueryBtn" @click="examListQuerySearchFun">
                  搜索
                </p>
              </el-col>
            </el-form>
          </el-row>
        </div>
      </div>
      <div class="examList">
        <p class="examListTit">
          考试列表
        </p>
        <div class="examListTab">
          <el-table
            :data="examlistTbaleData"
            border
            style="width: 100%">
            <el-table-column
              prop="serialNumber"
              label="序号"
              width="40">
            </el-table-column>
            <el-table-column
              prop="examName"
              label="考试名称"
              width="180">
            </el-table-column>
            <el-table-column
              prop="examTime"
              label="考试时间"
              width="154">
            </el-table-column>
            <el-table-column
              prop="examTypeName"
              label="考试类别"
              width="175">
            </el-table-column>
            <!--<el-table-column-->
              <!--prop="qkrs"-->
              <!--label="考试对象"-->
              <!--width="229">-->
            <!--</el-table-column>-->

            <el-table-column
              label="操作"
              width="200">
              <template slot-scope="scope">
                <p class="examdetail">
                  <router-link  :to="{path:'examDetail'}">详情</router-link>
                </p>
                <!-- <el-button @click="scoreTurn" type="text"><span class="aradModify">详情</span></el-button> -->
                <!-- <el-button @click="scoreTurn" type="text"><span class="aradModify">查看成绩</span></el-button> -->
                <!--<el-button type="text"><span class="aradModify">编辑</span></el-button>-->
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="deparRenConPags">
          <div class="block">

            <el-pagination
              @current-change="handleCurrentChange"
              :current-page="personPagObj.currentPage"
              :page-size="personPagObj.pageSize"
              layout="total, prev, pager, next, jumper"
              :total="personPagObj.recordCount">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <v-footer></v-footer>
    </div>
  </div>
</template>

<script>
import vHeader from '@/components/header'
import {formatDate} from '@/api/date.js'
import vFooter from '@/components/bottom'
export default {
  name: 'exam-list',
  components: {
    vFooter,
    vHeader
  },
  data () {
    return {
      personPagObj: { // 储存分页数据
        pageNum: 1,
        pageSize: 10,
        currentPage: 1,
        recordCount: 1
      },
      examListNameSelect: [], // 储存考试名称下拉
      value: '',
      examlistData: {
        name: '',
        radio: '1',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        vague:'',
        examName:''
      },
      examlistTbaleData: [{
        examName: '2017届期末考试',
        examTypeName: '期末考试',
        examTime: '2018-12-12',
        qkrs: '初一，初二，初三，高一，高二，高三'
      }],
      currentPage1: 5,
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 4,
      labelPosition: 'right'
    }
  },
  mounted () {
    let adminIdsObj = {
      'parameters': {},
      'currentPage': 1,
      'pageSize': 10,
      'examName': '考试'
    }
    this.examListQueryFun(adminIdsObj)
    this.examListQueryNameFun()
  },
  methods: {
    /**
     *分页函数
     */
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      // 初始化table
      let adminIdsObj = {
        'vague': '',
        'currentPage': val,
        'pageSize': 10,
        'examName': '考'
      }
      this.examListQueryFun(adminIdsObj)
    },

    /**
     * 查询函数
     *@param adminIdsObj
     */
    examListQueryFun (adminIdsObj) {
      this.$http.get('/api/v1/schoolexam/findExamOfVague', adminIdsObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              this.examlistTbaleData = []
              let tableDataAddobj = {}
              for (var i = 0; i < res.data.data.content.length; i++) {
                tableDataAddobj = {}
                tableDataAddobj.serialNumber = i + 1
                tableDataAddobj.examName = res.data.data.content[i].examName
                tableDataAddobj.examTypeName = res.data.data.content[i].examTypeName
                if (res.data.data.content[i].examTime) {
                  var date = new Date(res.data.data.content[i].examTime)
                  tableDataAddobj.examTime = formatDate(date, 'yyyy-MM-dd')
                }
                this.examlistTbaleData.push(tableDataAddobj)
                }
              }
              this.personPagObj.currentPage = res.data.data.currentPage
              this.personPagObj.pageSize = res.data.data.pageSize
              this.personPagObj.recordCount = res.data.data.pageTotal

            } else {
              this.$notify({
                title: '提示信息',
                message: '账号或密码错误',
                type: 'error'
              })
            }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 查询所有考试名字
     */
    examListQueryNameFun () {
      let examListNameArr = []
      let examListNameJson = {}
      this.$http.get('/api/v1/schoolexam/findExamName')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              // if(res.data.data.length>=1){
              //
              //
              //
              // }else {
              //
              // }
              for (var i = 0; i < res.data.data.length; i++) {
                examListNameJson = {}
                examListNameJson.value = res.data.data[i].examName
                examListNameJson.label = res.data.data[i].examName
                examListNameArr.push(examListNameJson)
              }
              console.log(examListNameArr)
              this.examListNameSelect = examListNameArr
            } else {
              this.$notify({
                title: '提示信息',
                message: '操作失败',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 查询事件
     */
    examListQuerySearchFun () {
      let adminIdsObj = {
        params: {
        'currentPage': 1,
        'pageSize': 10,
        'examName': this.examlistData.examName,
        'vague': this.examlistData.vague
        }

      }
      this.examListQueryFun(adminIdsObj)
    },
    scoreTurn(){
      this.$router.push({path: '/achievement'})
    }
    // examListTypeSearchFun(){

    //   // let searchRegexp=new RegExp(this.examlistData.vague)
    //   // let arr=[]
    //   // for(let i=0;i<this.examlistTbaleData.length;i++){
    //   //   if(searchRegexp.test(this.examlistTbaleData[i].examTypeName)){
    //   //     arr.push(this.examlistTbaleData[i])
    //   //   }
    //   // }
    //   // this.examlistTbaleData=arr
    // }
  }
}
</script>

<style lang="scss">
  .examListPags{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    .examListBox{
      flex: 1;
      width: 850px;
      margin: 30px auto;
      background-color: #fff;
      box-shadow: 0px 0px 6px #c4c8ca;
      border-radius: 10px;
      padding-bottom: 30px;
    }
    .examAdd{
      padding: 22px 50px;
      p{
        width: 120px;
        height: 30px;
        border-radius: 5px;
        border:1px solid #6bc08b;
        line-height: 30px;
        text-align: center;
        color: #6bc08b;
        font-size: 14px;
        cursor: pointer;
        a{
          color: #6bc08b;
        }
      }
    }
    .examListQuery{
      padding: 20px 50px 30px 50px;
      border-top: 1px solid #f2f2f2;
      border-bottom: 1px solid #f2f2f2;
    }
    .examListQueryTit{
      margin-bottom: 18px;

      p{
        width: 60px;
        height: 20px;
        background-color: #c49df8;
        color: #fff;
        font-size: 12px;
        line-height: 20px;
        text-align: center;
      }
    }
    .examListQueryCont{
      .el-input__inner{
        height: 35px;
        line-height: 35px;
        font-size: 12px;
      }
      .el-form-item__label{
        height: 35px;
        line-height: 35px;
        font-size: 12px;
      }
      .el-form-item__content{
        height: 35px;
        line-height: 35px;
        font-size: 12px;
      }
      .el-form-item{
        margin-bottom: 0;
      }
    }
    .v-classSeeQueryBtn{
      width: 120px;
      height: 35px;
      background-color: #6cc08c;
      color: #fff;
      font-size: 12px;
      border-radius: 5px;
      text-align: center;
      line-height: 35px;
      cursor: pointer;

    }
    .examList{
      padding: 20px 50px 40px 50px;
    }
    .examListTit{
      width: 60px;
      height: 20px;
      background-color: #f8cb3e;
      color: #fff;
      font-size: 12px;
      line-height: 20px;
      text-align: center;
      margin-bottom: 20px;
    }
    .el-table td, .el-table th{
      padding: 0;
      font-size: 12px;
    }
    .el-table thead th{
      padding: 5px 0;
      text-align: center;

    }
    .aradModify{
      font-size: 12px;
      color: #6cc08c;
    }
    .examdetail{
      width: 200px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #6bc08b;
      font-size: 14px;
      cursor: pointer;
      a{
        color: #6bc08b;
      }
    }
    .deparRenConPags{
      margin: 30px 0 0 0;
    }
    .el-table .cell, .el-table th div {
      padding: 0 !important;

    }
    .footer{
      flex: 0;
    }
  }

</style>



