                <template slot-scope="scope">
                  <el-button type="text" size="small" class="modiPer"  @click="teacherFileClick(scope.row)">教师档案</el-button>
                  <el-button  @click="popshow(2,scope.row)" type="text" size="small" class="modiPer">编辑</el-button>
                  <el-button type="text" v-show="scope.row.adminState==-1" size="small" class="modiPer"  @click="personBatchLocking(1,scope.row)">解锁</el-button>
                  <el-button type="text" v-show="scope.row.adminState==1" size="small" class="modiPer"  @click="personBatchLocking(-1,scope.row)">锁定</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="block">
              <el-pagination
