                :total="personPagObj.recordCount">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div>
      <popup :addfunFlaf.sync="addfunFlaf" :modify="modifyfun" ref="child" @refreshList="personNameQueryAllFun(adminIdsObj)"></popup>
      <!--<popup :addfunFlaf.sync="addfunFlaf" :modify="modifyfun"></popup>-->
      <importtemplate :importfunFlaf.sync="importfunFlaf"></importtemplate>
