    </div>
    <div class="exam-cls-mask" v-show="teaFileExamListShow">
      <div class="exam-cls">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
          <span>{{teaFileExamRowData.subjectName}}</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
           <p class="el-icon-circle-close-outline icon-close" @click="teaDetailPopCloseClick"></p>
        </div>
         <div class="stu-file-search">
            <div class="std-file1">
              <span>姓名:</span>
              <span>{{this.teaShFileData.fullName}}</span>
            </div>
            <div class="std-file2">
              <span>教职工编号:</span>
              <span>{{this.teaShFileData.staffNumber}}</span>
            </div>
            <div class="std-file2">
              <span>班级:</span>
              <span>{{getStdFileCls}}</span>
            </div>
          </div>
          <div class="std-file-down">
