          <div class="std-file-scoll">
             <el-table
              :data="teaFileExamListData"
              border
              style="width: 920px ;font-size:12px;margin: 0 auto;margin-top:5px; font-size:14px; margin-bottom:15px">
                <el-table-column
                type="selection"
              >
