<script>
// Vue.config.debug = true;
import vHeader from '@/components/header'
import vFooter from '@/components/bottom'
import {formatDate} from '@/api/date.js'
import popup from '@/views/department/popup'
import importtemplate from '@/views/department/importTemplate'
var querystring = require('querystring')

export default {
  name: 'department',
  components: {
    vHeader,
    vFooter,
    'popup': popup,
    'importtemplate': importtemplate
  },
  // props:['modify'],
  data () {
    return {
      personSessin: {// 所有储存对象集合
        tablePerson: [], // 储存table数据
        persontableChecked: [], // 储存多选框的值
        adminIdsArr: [], // 储存需要操作的人员id
        lockingFlag: false
      },
      personPagObj: { // 储存分页数据
        pageNum: 1,
        pageSize: 10,
        currentPage: 1,
        recordCount: 1
      },
      labelPosition: 'right',
      formLabelAlign: {
        fullName: '',
        id: ''
      },
      modifyfun: {
        title: '',
        flag: '',
        disabledFlag: false,
        adminInfperobj: {},
        theDepartmentSelect: null
      },
      addfunFlaf: false, // 控制新增弹窗显示
      importfunFlaf: false, // 控制导入弹窗显示
      adminIdsObj: {
        'parameters': {},
        'currentPage': 1,
        'pageSize': 10
      },
      teaFileShow: false,
      teaFileData: [],
      teaShFileData: {
        fullName: '',
        staffNumber: ''
      },
      teaFileExamListShow: false,
      teaFileExamRowData: {
        curriculumName: ''
      },
      teaFileExamListData: []

    }
  },
  mounted () {
    this.departList()
    // 初始化table
    this.personNameQueryAllFun(this.adminIdsObj)
  },
  methods: {
    teacherFileClick  (row) {
      this.teaFileShow = true
      this.teaShFileData = row
      this.findTeaFileFun()
    },
    findTeaFileFun () {
      let updata = {
        params: {
          staffNumber: this.teaShFileData.staffNumber
        }
      }
      this.$http
        .get(
          '/api/v1/teaching/findTeachingFile',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.teaFileData = res.data.data
              this.stdFileSbjctNameData = []
              var tempArr = []
              // this.stdFileData[0].examSubjectScores.forEach((val) => {
              //   tempArr.push(val.subjectName)
              // })
              // this.stdFileSbjctNameData = tempArr
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    PopCloseClick (row) {
      this.teaFileShow = false
      this.teaFileData = []
      this.teaShFileData = {
        fullName: '',
        staffNumber: ''
      }
    },

    teaFileDetailsClick (row) {
      this.teaFileExamRowData = row
      this.teaFileExamListShow = true
      this.findTeaFileDetailsFun()
    },

    findTeaFileDetailsFun () {
      let updata = {
        parameters: {
          'periodId': this.teaFileExamRowData.classPeriodId,
          'thetime': this.teaFileExamRowData.thetime,
          'curriculumId': this.teaFileExamRowData.subjectId,
          'classTypeId': this.teaFileExamRowData.classTypeId,
          'classId': this.teaFileExamRowData.classId
        }
      }
      this.$http
        .post(
          '/api/v1/teaching/findExamInfo',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.teaFileExamListData = res.data.data.content
              console.log(this.teaFileExamListData)
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    teaDetailPopCloseClick () {
      this.teaFileExamListShow = false
      this.teaFileExamRowData = {
        curriculumName: ''
      }
      this.teaFileExamListData = null
    },
    teaExportClick () {
      let updata = {
        parameters: {
          'periodId': this.teaFileExamRowData.classPeriodId,
          'thetime': this.teaFileExamRowData.thetime,
          'curriculumId': this.teaFileExamRowData.subjectId,
          'classTypeId': this.teaFileExamRowData.classTypeId,
          'classId': this.teaFileExamRowData.classId,
          'name': this.teaFileExamRowData.fullName,
          'adminId': this.teaFileExamRowData.staffNumber,
          'classType': this.teaFileExamRowData.classType,
          'className': this.teaFileExamRowData.className,
          'curriculumName': this.teaFileExamRowData.subjectName
        }
      }
      this.$http({
        method: 'post',
        url: '/api/v1/teaching/teachingFileExport',
        data: updata,
        responseType: 'blob'
      })
        .then(res => {
          if (res.status === 200) {
            let blob = res.data
            let reader = new FileReader()
            reader.readAsDataURL(blob)
            reader.onload = (e) => {
              let a = document.createElement('a')
              a.download = '入围统计表.xls'
              a.href = e.target.result
              document.body.appendChild(a)
              a.click()
              document.body.removeChild(a)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    /**
     * 搜索框按照部门查询
     */
    personnelQuerySelect () {
      var adminIdsObj = {
        'parameters': {
          theDepartment: this.formLabelAlign.id
        },
        'currentPage': 1,
        'pageSize': 10
      }
      this.personNameQueryAllFun(adminIdsObj)
    },
    /**
     * 输入框模糊查询
     */
    personnelQuery () {
      var adminIdsObj = {
        'parameters': {
          theDepartment: this.formLabelAlign.id,
          vague: this.formLabelAlign.fullName
        },
        'currentPage': 1,
        'pageSize': 10
      }
      this.personNameQueryAllFun(adminIdsObj)
    },
    // 新增部门弹窗控制
    popshow (flagL, row) {
      // this.modifyfun=this.modifyfun1
      // console.log(flagL)
      if (flagL === 1) {
        this.modifyfun.title = '新增人事'
        this.modifyfun.flag = 1
        this.modifyfun.disabledFlag = false
        this.$refs.child.initAdd() // 调用子组件函数
      } else if (flagL === 2) {
        this.modifyfun.title = '人事编辑'
        this.modifyfun.flag = 2
        console.log(row)
        this.$http.get('/api/v1/personnelManagement/selectAdminInfo?adminId=' + row.adminId)
          .then(res => {
            // console.log(res)
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.modifyfun.adminInfperobj = res.data.data
                this.modifyfun.adminInfperobj.theDepartmentStr = res.data.data.theDepartment
                console.log(this.modifyfun.adminInfperobj)

                this.$refs.child.init() // 调用子组件函数
              } else {
                this.$notify({
                  title: '提示信息',
                  message: res.data.code,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
        this.modifyfun.disabledFlag = true
      }
      this.addfunFlaf = true
    },
    importtempopShow () {
      this.importfunFlaf = true
    },
    hideFun () {
      this.addfunFlaf = false
    },
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },
    handleClick (row) {
      console.log(row)
    },
    /**
     *分页函数
     */
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      // 初始化table
      var adminIdsObj = {
        'parameters': {},
        'currentPage': val,
        'pageSize': 10
      }
      this.personNameQueryAllFun(adminIdsObj)
    },
    /**
     * 查询所有部门
     */
    departList () {
      // 获取所有部门

      this.$http.get('/api/v1/personnelManagement/findAllDepartmentInfo')
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            var departmentArr = []
            var departmentJson = {}
            if (res.data.code === 1) {
              for (var i = 0; i < res.data.data.length; i++) {
                departmentJson = {}
                departmentJson.value = +res.data.data[i].id
                departmentJson.label = res.data.data[i].dName
                departmentArr.push(departmentJson)
              }
              this.modifyfun.theDepartmentSelect = departmentArr
              console.log(' this.modifyfun.theDepartmentSelect', this.modifyfun.theDepartmentSelect)
            } else {
              this.$notify({
                title: '提示信息',
                message: res.data.code,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    /**
     * 查询所有教职工信息方法
     * @param name 姓名或者教职工编号
     * @param currentPage 页数
     * @param pageSize 条数
     */
    personNameQueryAllFun (adminIdsObj) {
      this.$http.post('/api/v1/personnelManagement/selectAdminInfoByVague', adminIdsObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              this.personSessin.tableDataDepart = []
              var tableDataAddobj = {}
              for (var i = 0; i < res.data.data.content.length; i++) {
                tableDataAddobj = {}
                tableDataAddobj.serialNumber = i + 1
                tableDataAddobj.adminId = res.data.data.content[i].adminId
                tableDataAddobj.adminInfoId = res.data.data.content[i].adminInfoId
                tableDataAddobj.fullName = res.data.data.content[i].fullName
                tableDataAddobj.theDepartment = res.data.data.content[i].theDepartment
                tableDataAddobj.mailBox = res.data.data.content[i].mailBox
                tableDataAddobj.mobilePhone = res.data.data.content[i].mobilePhone
                tableDataAddobj.staffNumber = res.data.data.content[i].staffNumber
                tableDataAddobj.theDepartment = res.data.data.content[i].theDepartment
                tableDataAddobj.adminState = res.data.data.content[i].adminState
                if (res.data.data.content[i].birthDate) {
                  var date = new Date(res.data.data.content[i].birthDate)
                  tableDataAddobj.birthDate = formatDate(date, 'yyyy-MM-dd')
                }
                tableDataAddobj.idCardNo = res.data.data.content[i].idCardNo
                if (Number(res.data.data.content[i].gender) === 1) {
                  tableDataAddobj.gender = '男'
                } else if (Number(res.data.data.content[i].gender) === 2) {
                  tableDataAddobj.gender = '女'
                } else if (Number(res.data.data.content[i].gender) === 0) {
                  tableDataAddobj.gender = '保密'
                }
                this.personSessin.tableDataDepart.push(tableDataAddobj)
              }
              this.personPagObj.currentPage = res.data.data.currentPage
              this.personPagObj.pageSize = res.data.data.pageSize
              this.personPagObj.recordCount = res.data.data.recordTotal
            } else {
              this.$notify({
                title: '提示信息',
                message: '账号或密码错误',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 批量添加储存点击事件函数
     * @param val
     */
    pladdChecked (val) {
      this.personSessin.persontableChecked = val
      console.log(this.personSessin.persontableChecked)
    },

    /**
     * 批量删除
     * @returns {boolean}
     */
    personBatchDelete () {
      // this.personSessin.persontableChecked=[];
      this.personSessin.adminIdsArr = []
      if (this.personSessin.persontableChecked.length < 1) {
        this.$message.error('请选择要删除的教职工')
        return false
      }
      for (var i = 0; i < this.personSessin.persontableChecked.length; i++) { // 获取教职工id
        this.personSessin.adminIdsArr.push(this.personSessin.persontableChecked[i].adminId)
      };
      console.log(this.personSessin.adminIdsArr)
      this.$confirm('此操作将删除选中人员, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        var deletePeasonobj = {
          'adminIds': this.personSessin.adminIdsArr
        }
        console.log(deletePeasonobj)
        this.$http.delete('/api/v1/admin/updateAdminsAndInfo', {data: deletePeasonobj})
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$message({
                  type: 'success',
                  message: '操作成功!'
                })
                this.personNameQueryAllFun(this.adminIdsObj)
              } else {
                this.$notify({
                  title: '提示信息',
                  message: '操作失败',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    /**
     * 锁定
     * @returns {boolean}
     */
    personBatchLocking (stae, row) {
      console.log(row)
      this.$confirm('此操作将锁定选中人员, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        var putPeasonobj = {
          'id': row.adminId,
          'adminState': stae
        }
        this.$http.put('/api/v1/admin/updateAdmin', querystring.stringify(putPeasonobj))
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$message({
                  type: 'success',
                  message: '操作成功!'
                })
                this.personNameQueryAllFun(this.adminIdsObj)
              } else {
                this.$notify({
                  title: '提示信息',
                  message: '操作失败',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        })
      })
    },
    /**
     * 修改密码
     * @param perarrObj 表单对象
     */
    personModifyPwass () {
      // this.personSessin.persontableChecked=[];
      var adminIdStr = ''
      this.personSessin.adminIdsArr = []
      if (this.personSessin.persontableChecked.length >= 2) {
        this.$message.error('只能选择一个额')
        return false
      }
      if (this.personSessin.persontableChecked.length < 1) {
        this.$message.error('请选择人员')
        return false
      }
      for (var i = 0; i < this.personSessin.persontableChecked.length; i++) { // 获取教职工id
        this.personSessin.adminIdsArr.push(this.personSessin.persontableChecked[i].adminId)
        adminIdStr = this.personSessin.persontableChecked[i].adminId
      };
      console.log(this.personSessin.adminIdsArr)
      this.$prompt('请输入密码', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/,
        inputErrorMessage: '密码设置为6-16位的数字和字母组合'
      }).then(({ value }) => {
        var putPeasonobj = {
          'adminId': Number(adminIdStr),
          'newPass': value
        }
        console.log(putPeasonobj)
        this.$http.put('/api/v1/admin/updateAdminPass', querystring.stringify(putPeasonobj))
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$message({
                  type: 'success',
                  message: '操作成功!'
                })
                this.personNameQueryAllFun(this.adminIdsObj)
              } else {
                this.$notify({
                  title: '提示信息',
                  message: '操作失败',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        })
      })
    },
    /**
     * 模块下载a链接拼接方式
     */
    clsModuleDownLoad () {
      // console.log(this.$http.defalut.baseUrl + '/api/v1/studentManagement/downloadStudentTemplate')
      let ans = '/api/v1/personnelManagement/downloadTeacherTemplate'
      if (this.$http.defaults.baseURL) {
        ans = this.$http.defaults.baseURL + '/api/v1/personnelManagement/downloadTeacherTemplate'
      }
      return ans
    },
    /** 公共方法 */
    handleHref (url) {
      const baseURL = this.$http.defaults.baseURL
      return baseURL ? baseURL + url : url
    }
  },
  computed: {
    getStdFileCls () {
      var ans = ''
      if (this.teaFileExamRowData) {
        ans = this.teaFileExamRowData.classPeriod + this.teaFileExamRowData.thetime + '届' + this.teaFileExamRowData.className
      }
      return ans
    },
    teaFileDownLoad () {
      let ans = '/api/v1/studentScoreArchives/exportAllExamScore' + '?registerNumber=' + this.stdShFileData.registerNumber
      console.log('nihao')
      return this.handleHref(ans)
    }
  },
  filters: {
    transDate (date) {
      date = new Date(date)
      return formatDate(date, 'yyyy-MM-dd')
    }
  }
}
</script>

