<template>
  <div class="cricount">
    <div class="scoreBox">
      <div class="scoreTit">
        <span>
          选择条件
        </span>
      </div>
      <div class="scoreQuery">
        <div class="scoreFirst">
          <span>选择考试</span>
          <div class="firstInput">
              <el-select
                v-model="examName"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="remoteMethod"
                :loading="loading"
                @change="examSlctClick">
                <el-option
                  v-for="item in examNames"
                  :key="item.examParentId"
                  :label="item.examName"
                  :value="item.examParentId">
                </el-option>
              </el-select>
          </div>
          <span>学段</span>
          <div class="inputStyle">
            <el-select v-model="gradeData" placeholder="高中" @change="gradeSlctClick">
            <el-option
              v-for="item in gradeDatas"
              :key="item.classPeriodId"
              :label="item.classPeriod"
              :value="item.classPeriodId">
            </el-option>
          </el-select>
          </div>
          <span>届次</span>
          <div class="inputStyle">
            <el-select v-model="thetimeData" placeholder="2018"  @change="classTypeSlctClick">
            <el-option
              v-for="item in thetimeDatas"
              :key="item.thetime"
              :label="item.thetime"
              :value="item.thetime">
            </el-option>
          </el-select>
          </div>
          <span>班级类型</span>
          <div class="inputStyle">
            <el-select v-model="classTypeData" placeholder="理科班"  @change="classLevelSlctClick">
            <el-option
              v-for="item in classTypeDatas"
              :key="item.classTypeId"
              :label="item.classType"
              :value="item.classTypeId">
            </el-option>
          </el-select>
          </div>
        </div>
        <div class="scoreSecond">
          <span>班级层次</span>
          <div class="secondInput">
            <el-select v-model="classLevelData" placeholder="精英班" @change="classSlctClick">
            <el-option
              v-for="item in classLevelDatas"
              :key="item.levelId"
              :label="item.levelName"
              :value="item.levelId">
            </el-option>
          </el-select>
          </div>
          <span>班级</span>
          <div class="inputStyle">
            <el-select v-model="classData" multiple placeholder="所有" @change="subjectSlctClick">
            <el-option
              v-for="item in classDatas"
              :key="item.classId"
              :label="item.className"
              :value="item.classId">
            </el-option>
          </el-select>
          </div>
          <span>课程</span>
          <div class="inputStyle">
            <el-select v-model="subjectData" multiple placeholder="总分">
            <el-option
              v-for="item in subjectDatas"
              :key="item.examSubjectId"
              :label="item.examSubject"
              :value="item.examSubjectId">
            </el-option>
          </el-select>
          </div>

        </div>
        <div class="scoreThird">
          <span class="upline">线上分数</span>
          <div class="singelInput">
            <el-input v-model="upline" placeholder="10"></el-input>
          </div>
          <span>线下分数</span>
          <div class="singelInput">
            <el-input v-model="downline" placeholder="10"></el-input>
          </div>
           <div class="btnclick">
            <p class="scoreQueryBtn" @click="findScoreRank">
              查询
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="scoreBoxTable">
      <div class="scoreButtonTit">
        <el-button type="primary" @click="cricountExportClick">导出</el-button>
      </div>
      <div class="scoreTab">
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          @selection-change="handleSelectionChange"
          >
          <el-table-column
            type="selection"
            width="50"
            >
          </el-table-column>
          <el-table-column
            label="班号"
            >
            <template slot-scope="scope">
              {{ scope.row.classNumber }}
            </template>
          </el-table-column>
          <el-table-column
            label="班级"
            fixed
            >
            <template slot-scope="scope">
              {{ scope.row.className }}
            </template>
          </el-table-column>
          <el-table-column
            label="班级类型"
            >
            <template slot-scope="scope">
              {{ scope.row.classType }}
            </template>
          </el-table-column>
          <el-table-column
            label="班级层次"
            >
            <template slot-scope="scope">
              {{ scope.row.classLevel }}
            </template>
          </el-table-column>
          <el-table-column
            label="班主任"
            fixed
            >
            <template slot-scope="scope">
              {{ scope.row.classHeadmaster }}
            </template>
          </el-table-column>
            <el-table-column
              v-if="hasTotal"
              label="总分"
              >
              <el-table-column
                v-for="(item, index3) in gradeShortsData" v-bind:key="index3"
                :label="item"
                >
               <el-table-column
                  :label="upscore"
                  >
                  <template slot-scope="scope">
                    <p>{{scope.row.totalList[index3].onlineNum}}</p>
                  </template>
                </el-table-column>
                <el-table-column
                  :label="downscore"
                  >
                  <template slot-scope="scope">
                    <p>{{scope.row.totalList[index3].offlineNum}}</p>
                  </template>
                </el-table-column>
              </el-table-column>
            </el-table-column>
          <el-table-column
              v-for="(child, index1) in scoreNumData" v-bind:key="index1"
              :label="child.subjectName"
              >
              <el-table-column
                v-for="(item, index2) in gradeShortsData" v-bind:key="index2"
                :label="item"
                >
                <el-table-column
                  :label="upscore"
                  >
                  <template slot-scope="scope">
                    <p>{{scope.row.shortlistedList[index1].gradeShorts[index2].onlineNum}}</p>
                  </template>
                </el-table-column>
                <el-table-column
                  :label="downscore"
                  >
                  <template slot-scope="scope">
                    <p>{{scope.row.shortlistedList[index1].gradeShorts[index2].offlineNum}}</p>
                  </template>
                </el-table-column>
              </el-table-column>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="deparRenConPags">
        <div class="block">
          <el-pagination
            @size-change="cricountHandleSizeChange"
            @current-change="cricountHandleCurrentChange"
            :current-page="cricountcurrentPage"
            :page-sizes="[10, 100, 1000]"
            :page-size="cricountPageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="cricountTotal">
          </el-pagination>
        </div>
    </div>

  </div>

</template>
<script>
export default {
  data () {
    return {
      examName: '',
      examNames: [], // 考试名称
      gradeData: '',
      gradeDatas: [], // 学段
      thetimeData: '',
      thetimeDatas: [], // 届次
      classTypeData: '',
      classTypeDatas: [], // 班级类型
      classLevelData: '',
      classLevelDatas: [], // 班级层次
      classData: '',
      classDatas: [], // 班级
      subjectData: '',
      subjectDatas: [], // 课程
      scoreNumData: [], // 动态添加表格列
      gradeShortsData: [], // 档次不一
      upline: '',
      downline: '',
      upscoredata: '线上',
      downscoredata: '线下',
      score: '分',
      value: '',
      multipleSelection: '',
      tableData: [],
      cricountcurrentPage: 1,
      cricountPageSize: 10,
      cricountTotal: '',
      hasTotal: false
    }
  },
  methods: {
    // 初始化考试名称
    // initExamName () {
    //   this.examName = ''
    //   this.gradeData = ''
    //   this.thetimeData = ''
    //   this.classTypeData = ''
    //   this.classLevelData = ''

    //   this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: this.examName})
    //     .then(res => {
    //       if (res.status === 200) {
    //         if (res.data.code === 1) {
    //           this.examNames = res.data.data
    //         } else {
    //           console.log(res.data.msg)
    //           this.$message({
    //             //  饿了么的消息弹窗组件,类似toast
    //             showClose: true,
    //             message: res.data.msg,
    //             type: 'error'
    //           })
    //         }
    //       }
    //     })
    //     .catch(err => {
    //       console.log(err)
    //     })
    // },
    // 选择考试名称查询学段等信息
    examSlctClick () {
      this.gradeData = ''
      this.gradeDatas = []

      this.thetimeData = ''
      this.thetimeDatas = []

      this.classTypeData = ''
      this.classTypeDatas = []

      this.classLevelData = ''
      this.classLevelDatas = []

      this.classData = []
      this.classDatas = []

      this.subjectData = []
      this.subjectDatas = []

      this.$http.get('/api/v1/schoolexam/findAllSchoolExamThetimeBySchoolExamParent',
        {
          params: {
            examParentId: this.examName
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.gradeDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据学段选择届次
    gradeSlctClick () {
      this.thetimeData = ''
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.gradeDatas.length; i++) {
        if (this.gradeData === this.gradeDatas[i].classPeriodId) {
          this.thetimeDatas = this.gradeDatas[i].thetimes
        }
      }
    },
    // 根据届次选择班级类型
    classTypeSlctClick () {
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.thetimeDatas.length; i++) {
        if (this.thetimeData === this.thetimeDatas[i].thetime) {
          this.classTypeDatas = this.thetimeDatas[i].classTypes
        }
      }
    },
    // 根据班级类型确定班级层次
    classLevelSlctClick () {
      this.classLevelData = ''

      for (let i = 0; i < this.classTypeDatas.length; i++) {
        if (this.classTypeData === this.classTypeDatas[i].classTypeId) {
          this.classLevelDatas = this.classTypeDatas[i].classLevels
        }
      }
    },
    // 根据班级层次选择班级
    classSlctClick () {
      this.$http.get('/api/v1/schoolexam/findClassAndSubjectByCondition',
        {
          params: {
            examParentId: this.examName,
            classPeriodId: this.gradeData,
            thetime: this.thetimeData,
            classTypeId: this.classTypeData,
            classLevelId: this.classLevelData
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.classDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据班级选择课程
    subjectSlctClick () {
      let subjectArr = []
      if (this.classData.length > 0) {
        for (let i = 0; i < this.classData.length; i++) {
          for (let j = 0; j < this.classDatas.length; j++) {
            if (this.classData[i] === this.classDatas[j].classId) {
              subjectArr.push(this.classDatas[j].subjects)
            }
          }
        }
      }

      let idList = []
      let examSubjectId = []
      subjectArr.forEach((val) => {
        examSubjectId = []
        val.forEach((ele) => {
          examSubjectId.push(ele.examSubjectId)
        })
        idList.push({
          examSubjectId: examSubjectId
        })
      })
      console.log(idList)
      let defCList = idList.reduce((acc, cur) => {
        if (acc.length === 0) {
          return cur.examSubjectId
        } else {
          let ans = []
          let curArr = cur.examSubjectId
          let accArr = acc
          curArr.forEach((val) => {
            if (accArr.indexOf(val) > -1) {
              ans.push(val)
            }
          })
          return ans
        }
      }, [])
      let courseLi = []
      subjectArr[0].forEach((val) => {
        if (defCList.indexOf(val.examSubjectId) > -1) {
          courseLi.push({
            examSubjectId: val.examSubjectId,
            examSubject: val.examSubject
          })
        }
      })
      courseLi.push({
        examSubjectId: 0,
        examSubject: '总分'
      })
      this.subjectDatas = courseLi
    },
    // 提交查询条件并将数据传递至表格
    findScoreRank () {
      if (this.examName === '') {
        this.$message.error('考试名称不能为空')
        return false
      }
      if (this.gradeData === '') {
        this.$message.error('学段不能为空')
        return false
      }
      if (this.thetimeData === '') {
        this.$message.error('届次不能为空')
        return false
      }
      if (this.classTypeData === '') {
        this.$message.error('班级类型不能为空')
        return false
      }
      if (this.classLevelData === '') {
        this.$message.error('班级层次不能为空')
        return false
      }
      if (this.classData.length <= 0) {
        this.$message.error('班级不能为空')
        return false
      }
      if (this.subjectData.length <= 0) {
        this.$message.error('课程不能为空')
        return false
      }
      if (this.upline === '') {
        this.$message.error('线上不能为空')
        return false
      }
      if (this.downline === '') {
        this.$message.error('线下不能为空')
        return false
      }

      let updata = {
        examParentId: this.examName,
        periodId: this.gradeData,
        thetime: this.thetimeData,
        curriculumIdList: this.subjectData,
        classTypeId: this.classTypeData,
        classLevelId: this.classLevelData,
        classIdList: this.classData,
        onlineScore: this.upline,
        offlineScore: this.downline
      }
      this.$http.post('/api/v1/critical/findCriticalRaw', {parameters: updata})
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              let arrlist = []

              for (let i = 0; i < res.data.data.content.length; i++) {
                if (res.data.data.content[i]) {
                  if (!res.data.data.content[i].classNumber) {
                    res.data.data.content[i].classNumber = '年级情况'
                  }
                  arrlist.push(res.data.data.content[i])
                }
              }
              this.tableData = arrlist
              if (res.data.data.content[0].totalList) {
                this.hasTotal = true
              }
              /// //////////////////////////////////////////////更改的
              this.scoreNumData = this.tableData[0].shortlistedList

              let gradeNameId = []
              let gradeShortsList = []
              arrlist.forEach((val) => {
                gradeNameId = []
                val.shortlistedList.forEach((ele) => {
                  ele.gradeShorts.forEach((it) => {
                    gradeNameId.push(it.gradeName)
                  })
                })
                gradeShortsList.push({
                  gradeNameId: gradeNameId
                })
              })
              console.log(gradeShortsList)
              let gradeNameData = gradeShortsList[0].gradeNameId
              for (let i = 1; i < gradeShortsList.length; i++) {
                for (let j = 0; j < gradeShortsList[i].gradeNameId.length; j++) {
                  if (gradeNameData.indexOf(gradeShortsList[i].gradeNameId[j]) == -1) {
                    gradeNameData.push(gradeShortsList[i].gradeNameId[j])
                  }
                }
              }
              console.log(gradeNameData)
              this.gradeShortsData = Array.from(new Set(gradeNameData))
              this.cricountTotal = res.data.data.recordTotal
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    cricountHandleSizeChange (val) {
      this.cricountcurrentPage = 1
      this.cricountPageSize = val
      this.findScoreRank()
    },
    // 更新页数并 更新表数据
    cricountHandleCurrentChange (val) {
      this.cricountcurrentPage = val
      this.findScoreRank()
    },

    // 导出
    cricountExportClick () {
      let updata = {
        parameters: {
          examParentId: this.examName,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          curriculumIdList: this.subjectData,
          classTypeId: this.classTypeData,
          classLevelId: this.classLevelData,
          classIdList: this.classData,
          onlineScore: this.upline,
          offlineScore: this.downline
        }
      }
      this.$http({
        method: 'post',
        url: '/api/v1/critical/criticalExport',
        data: updata,
        responseType: 'blob'
      })
        .then(res => {
          if (res.status === 200) {
            let blob = res.data
            let reader = new FileReader()
            reader.readAsDataURL(blob)
            reader.onload = (e) => {
              let a = document.createElement('a')
              a.download = '临界生统计表.xls'
              a.href = e.target.result
              document.body.appendChild(a)
              a.click()
              document.body.removeChild(a)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    remoteMethod (query) {
      var flag = 1
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },
  
    // 初始化考试名称
    initExamName (query, flag) {
      this.examName = ''
      this.gradeData = ''
      this.thetimeData = ''
      this.classTypeData = ''
      this.classLevelData = ''
      this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: query})
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.loading = false
              if (flag === 1) {
                this.examNames = res.data.data
              } else {
                this.comExamNames = res.data.data
              }
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    scoreRankClick () {
      let cope = {singelInput: '', seen: true}
      this.addInputs.push(cope)
      console.log(this.addInputs)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  },
  mounted () {
    this.remoteMethod()
  },
  computed: {
    // 计算传递给表格中的线上数据和线下数据
    upscore: function () {
      return this.upscoredata + this.upline + this.score
    },
    downscore: function () {
      return this.downscoredata + this.downline + this.score
    }
  }

}
</script>

<style lang="scss">
.cricount{
  .scoreBox{
    border-bottom: 1px solid #b8d6cd;
    padding: 20px 10px 20px 10px;
  }
  .scoreTit{
    text-align: center;
    margin-bottom: 6px;
    background-color: #6cc08c;
    width: 100px;
    height: 35px;
    line-height: 35px;
    border-radius: 5px;
      span{
        padding: 4px 8px;
        color: #fff;
        font-size: 14px;
      }

  }
  .scoreQuery{
    width:960px;
    .inputStyle{
      display: inline-block;
      margin-right: 20px;
      .el-input__inner{
        height: 30px;
        width:100px;
        line-height: 30px;
      }
    }
  }
  .scoreFirst{
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
    text-align: left;
    .firstInput{
      display: inline-block;
      margin-right: 20px;
      .el-input__inner{
      height: 30px;
      width:210px;
      line-height: 30px;
    }
    }
  }
  .scoreSecond{
    width: 100%;
    margin-bottom: 15px;
    text-align: left;
    .secondInput{
      display: inline-block;
      margin-right: 20px;
      .el-input__inner{
        height: 30px;
        width:210px;
        line-height: 30px;
      }
    }
  }
  .scoreThird{
    width: 100%;
    margin-bottom: 15px;
    text-align: left;
   .singelInput{
      display: inline-block;
      margin-right: 20px;
        .el-input{
          height: 30px;
          width: 60px;
          line-height: 30px;
        }
        .el-input__inner{
          height: 30px;
          width: 60px;
          line-height: 30px;
        }
    }
  }
  .btnclick{
    display: inline-block;
    .scoreQueryBtn{
      width: 105px;
      height: 30px;
      border-radius: 5px;
      text-align: center;
      color: #6cc08c;
      border: 1px solid #6cc08c;
      line-height: 30px;
      cursor: pointer;
      position:absolute;
      margin-left: 32px;
      margin-top: -18px;
    }
   }
  .scoreBoxTable{
    padding: 20px 10px 36px 10px;
    .scoreButtonTit{
      .el-button--primary{
        color: #fff;
        background-color: #c49df8;
        border-color: #c49df8;
        font-size: 14px;
        width: 100px;
        height: 35px;
        margin-left: 720px;
      }
    }
  }
  .scoreTab{
    padding: 30px 40px 30px 40px;
    .el-table td, .el-table th{
      padding: 0;
      font-size: 12px;
    }
    .el-table thead th>.cell{
      text-align: center;
    }
    .el-table thead th{
      padding: 8px 0;
    }
    .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed {
      background-color: #fafcfc;
    }
  }
}
</style>



