<template>
  <div class="auth">
      <div class="authBox" >
      </div>
        <div class="authshow">
          <div>
            <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
              <span>管理员详情</span>
            <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
          </div>
          <div class="authmain">
            <el-row>
              <el-col :span="24">
                管理员名称:
                <el-input v-model="authchildData.fullName" placeholder="张三" :disabled="true"></el-input>
              </el-col>
            </el-row>

             <el-row>
              <el-col :span="24">
                 管理员账号:
                <el-input placeholder="zhangsan" v-model="authchildData.adminName" :disabled="true"></el-input>
              </el-col>
            </el-row>

             <el-row>
              <el-col :span="24">
                选择角色:
                <el-select v-model="authchildData.roleId" placeholder="请选择">
                  <el-option
                    v-for="item in authRoles"
                    :key="item.id"
                    :label="item.roleName"
                    :value="item.id">
                  </el-option>
                </el-select>

              </el-col>
            </el-row>

             <el-row>
              <el-col :span="24">
                选择状态:
                <el-select v-model="authchildData.adminState" placeholder="请选择">
                  <el-option
                    v-for="item in states"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>

                </el-select>
              </el-col>
            </el-row>
              <el-button style="background:#f8a3c2;color:white; margin-top:80px" size="medium" @click="resetPwd">重置密码</el-button>
          </div>
            <div class="authfooter">
              <div slot="footer" class="dialog-footer">
                  <button class="save" @click="updateAuthClick">修 改</button>
                  <button class="exit" @click="hideDialog">返 回</button>
              </div>
            </div>
      </div>

  </div>
  </template>
<script>
export default {
  name: 'authpopover',
  props: [
    'parentData',
    'authRoles'
  ],
  data () {
    return {
      rolesVal: '',
      statesVal: '',
      authchildData: '',
      states: [{
        label: '激活',
        value: '1'
      },
      {
        label: '停用',
        value: '0'
      }
      ]

    }
  },
  methods: {
    hideDialog () {
      this.$emit('childUpdateTable', this.modifyList)
      this.$emit('hideDialog')
    },
    /**
     *  this.authchildData
     */
    updateAuthClick () {
      this.$confirm('是否修改该用户的角色信息?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // this.$message({
        //   type: 'success',
        //   message: '修改成功!'
        // })
        this.updateRole()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消修改'
        })
      })
    },
    updateRole () {
      let updata = {
        id: this.authchildData.id,
        roleId: this.authchildData.roleId,
        adminState: this.authchildData.adminState
      }

      console.log(updata, this.authchildData)

      this.$http
        .put(
          '/api/v1/admin/updateAdminRole',
          this.$qs.stringify(updata)
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
              // 根据roleId 逻辑修改表
              this.authRoles.forEach((val, index) => {
                if (val.id === this.authchildData.roleId) {
                  this.authchildData.roleName = val.roleName
                }
              })
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    resetPwd () {
      this.$http
        .put(
          '/api/v1/admin/updateAdminPass',
          this.$qs.stringify({adminId: this.authchildData.id})

        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: '新密码为123456',
                type: 'success'
              })
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  },
  watch: {
    'parentData' (cur, old) {
      console.log('子信息auth', this.authchildData)
      this.authchildData = cur
      // this.rolesVal = this.authchildData.roles
      // this.authchildData.adminState = this.authchildData.adminState === '1' ? '激活' : '停用'
      console.log('子信息watch', this.authchildData)
    },
    'authRoles' (cur, old) {
      console.log('子信息authRoles', this.authRoles, cur)
    }
  },
  computed: {
    modifyList () {
      return this.authchildData
    }

  }
}
</script>
<style lang="scss">
.auth {
  .authBox {
    position: absolute;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    min-width: 960px;
    z-index: 100;
  }
  .authshow {
    background: white;
    position: absolute;
    width: 425px;
    height: 550px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 200;
  }
  .authshow > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .authshow > div:nth-of-type(1) > img {
    vertical-align: middle;
  }
    .authmain {
    color: #999;
  }
  .authmain .el-input {
    width:auto;
    input {
        background: #f2f2f2;
    }

  }
  .authmain > div {
    margin-top: 35px;
    margin-left: 40px;
    text-align: left;
    color: #999;
    font-size: 16px;
  }

  .authmain > div:nth-of-type(1) > span {
    margin-left: 20px;
  }

  .authfooter {
    width: 100%;
    position: absolute;
    bottom:20px;

  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }

}
</style>



