          <span>班级类型</span>
          <div class="inputStyle">
            <el-select v-model="classTypeData" placeholder="理科班"  @change="classLevelSlctClick">
            <el-option
              v-for="item in classTypeDatas"
              :key="item.classTypeId"
              :label="item.classType"
