          </div>
        </div>
        <div class="scoreSecond">
          <span>班级层次</span>
          <div class="secondInput">
            <el-select v-model="classLevelData" placeholder="精英班" @change="classSlctClick">
            <el-option
              v-for="item in classLevelDatas"
              :key="item.levelId"
              :label="item.levelName"
              :value="item.levelId">
            </el-option>
          </el-select>
          </div>
          <span>班级</span>
          <div class="inputStyle">
            <el-select v-model="classData" multiple collapse-tags placeholder="所有" @change="subjectSlctClick">
            <el-option
              v-for="item in classDatas"
              :key="item.classId"
              :label="item.className"
              :value="item.classId">
            </el-option>
          </el-select>
          </div>
          <span>课程</span>
          <div class="inputStyle">
            <el-select v-model="subjectData" multiple collapse-tags placeholder="总分" >
            <el-option
              v-for="item in subjectDatas"
              :key="item.examSubjectId"
              :label="item.examSubject"
              :value="item.examSubjectId">
            </el-option>
          </el-select>
          </div>
          <div class="btnclick">
            <p class="scoreQueryBtn" @click="findScoreRank">
              查询
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="scoreBoxTable">
      <div class="scoreButtonTit">
        <ul class="intotal-nav">
          <li><span @click="clsSbjctChartClick">班级各科柱状图</span></li>
          <li><span @click="averageExport">导出</span></li>
        </ul>
      </div>
      <div class="scoreTab">
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          @selection-change="handleSelectionChange"
          >
          <el-table-column
              type="selection"
              width="50"
              fixed
              >
            </el-table-column>
            <el-table-column
              label="班号"
              fixed
              >
              <template slot-scope="scope">
                <p>{{scope.row.classNumber}}</p>
              </template>
            </el-table-column>
            <el-table-column
              label="班主任"
              fixed
              >
              <template slot-scope="scope">
                <p>{{scope.row.classHeadmaster}}</p>
              </template>
            </el-table-column>
              <el-table-column
              v-if="hasSbjct"
              label="总分"
              >
              <el-table-column
              label="实考人数"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.actuallyCome}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="缺考人数"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.notCome}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="平均分"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.avgScore}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="同层次排名"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.examSort}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="难度系数"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.degree}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="最高分"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.maxScore}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="最低分"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageTotal.minScore}}</p>
              </template>
              </el-table-column>
            </el-table-column>
            <el-table-column
              v-for="(child, index) in scoreNumData" v-bind:key="index"
              :label="child.subjectName"
              >
              <el-table-column
              label="教师"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].fullName}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="实考人数"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].actuallyCome}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="缺考人数"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].notCome}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="平均分"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].avgScore}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="同层次排名"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].examSort}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="难度系数"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].degree}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="最高分"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].maxScore}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="最低分"
              >
              <template slot-scope="scope">
                <p>{{scope.row.averageRspList[index].minScore}}</p>
              </template>
              </el-table-column>
            </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="deparRenConPags">
      <div class="block">
        <el-pagination
          @size-change="avgHandleSizeChange"
          @current-change="avgHandleCurrentChange"
          :current-page="avgcurrentPage"
          :page-sizes="[10, 100, 1000]"
          :page-size="avgPageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="avgTotal">
        </el-pagination>
      </div>
    </div>

  <!-- 柱状图 -->
  <div class="inAllPast-mask" v-show="clsSbjctChartShow">
      <div class="inAllPast">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
          <span>班级平均分柱状图</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
          <p class="el-icon-circle-close-outline icon-close" @click="clsSbjctCloseClick"></p>
        </div>
        <div class="exam">
          <div class="examMngQuery">
            <div>
              <div
                v-for="(item, index) in avgchartData"
                  :key="index"
                  ref="chart"
                  class="perAvg-chart-style"
              >
              </div>
            </div>
          </div>
         </div>
      </div>
    </div>
  </div>

</template>
<script>
export default {
  data () {
    return {
      examName: '',
      examNames: [], // 考试名称
      gradeData: '',
      gradeDatas: [], // 学段
      thetimeData: '',
      thetimeDatas: [], // 届次
      classTypeData: '',
      classTypeDatas: [], // 班级类型
      classLevelData: '',
      classLevelDatas: [], // 班级层次
      classData: '',
      classDatas: [], // 班级
      subjectData: '',
      subjectDatas: [], // 课程
      addInputs: [{singleInput: '', seen: false}, {singleInput: '', seen: true}, {singleInput: '', seen: true}, {singleInput: '', seen: true}], // 增加输入框
      scoreNumData: [], // 动态添加表格列
      totalShow: false, // 显示总分
      singleShow: false, // 显示单科
      value: '',
      multipleSelection: '',
      tableData: [],
      // 分页
      avgcurrentPage: 1,
      avgPageSize: 10,
      avgTotal: '',
      hasSbjct: false,
      // 平均分柱状图
      avgchartData: [],
      clsSbjctChartShow: false,
      gradeRowData: [],
      loading: false

    }
  },
  methods: {

    // 选择考试名称查询学段等信息
    examSlctClick () {
      this.gradeData = ''
      this.gradeDatas = []

      this.thetimeData = ''
      this.thetimeDatas = []

      this.classTypeData = ''
      this.classTypeDatas = []

      this.classLevelData = ''
      this.classLevelDatas = []

      this.classData = []
      this.classDatas = []

      this.subjectData = []
      this.subjectDatas = []

      this.tableData = []

      this.$http.get('/api/v1/schoolexam/findAllSchoolExamThetimeBySchoolExamParent',
        {
          params: {
            examParentId: this.examName
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.gradeDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据学段选择届次
    gradeSlctClick () {
      this.thetimeData = ''
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.gradeDatas.length; i++) {
        if (this.gradeData === this.gradeDatas[i].classPeriodId) {
          this.thetimeDatas = this.gradeDatas[i].thetimes
        }
      }
    },
    // 根据届次选择班级类型
    classTypeSlctClick () {
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.thetimeDatas.length; i++) {
        if (this.thetimeData === this.thetimeDatas[i].thetime) {
          this.classTypeDatas = this.thetimeDatas[i].classTypes
        }
      }
    },
    // 根据班级类型确定班级层次
    classLevelSlctClick () {
      this.classLevelData = ''

      for (let i = 0; i < this.classTypeDatas.length; i++) {
        if (this.classTypeData === this.classTypeDatas[i].classTypeId) {
          this.classLevelDatas = this.classTypeDatas[i].classLevels
        }
      }
    },
    // 根据班级层次选择班级
    classSlctClick () {
      this.$http.get('/api/v1/schoolexam/findClassAndSubjectByCondition',
        {
          params: {
            examParentId: this.examName,
            classPeriodId: this.gradeData,
            thetime: this.thetimeData,
            classTypeId: this.classTypeData,
            classLevelId: this.classLevelData
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.classDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据班级选择课程
    subjectSlctClick () {
      let subjectArr = []
      if (this.classData.length > 0) {
        for (let i = 0; i < this.classData.length; i++) {
          for (let j = 0; j < this.classDatas.length; j++) {
            if (this.classData[i] === this.classDatas[j].classId) {
              subjectArr.push(this.classDatas[j].subjects)
            }
          }
        }
      }

      let idList = []
      let examSubjectId = []
      subjectArr.forEach((val) => {
        examSubjectId = []
        val.forEach((ele) => {
          examSubjectId.push(ele.examSubjectId)
        })
        idList.push({
          examSubjectId: examSubjectId
        })
      })
      console.log(idList)
      let defCList = idList.reduce((acc, cur) => {
        if (acc.length === 0) {
          return cur.examSubjectId
        } else {
          let ans = []
          let curArr = cur.examSubjectId
          let accArr = acc
          curArr.forEach((val) => {
            if (accArr.indexOf(val) > -1) {
              ans.push(val)
            }
          })
          return ans
        }
      }, [])
      let courseLi = []
      subjectArr[0].forEach((val) => {
        if (defCList.indexOf(val.examSubjectId) > -1) {
          courseLi.push({
            examSubjectId: val.examSubjectId,
            examSubject: val.examSubject
          })
        }
      })
      courseLi.push({
        examSubjectId: 0,
        examSubject: '总分'
      })
      this.subjectDatas = courseLi
    },
    // 提交查询条件并将数据传递至表格
    findScoreRank () {
      this.hasSbjct = false
      if (this.examName === '') {
        this.$message.error('考试名称不能为空')
        return false
      }
      if (this.gradeData === '') {
        this.$message.error('学段不能为空')
        return false
      }
      if (this.thetimeData === '') {
        this.$message.error('届次不能为空')
        return false
      }
      if (this.classTypeData === '') {
        this.$message.error('班级类型不能为空')
        return false
      }
      if (this.classData === '') {
        this.$message.error('班级不能为空')
        return false
      }
      if (this.subjectData.length <= 0) {
        this.$message.error('课程不能为空')
        return false
      }
      let updata = {
        parameters: {
          examParentId: this.examName,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          curriculumIdList: this.subjectData,
          classTypeId: this.classTypeData,
          classIdList: this.classData
        },
        'currentPage': this.avgcurrentPage,
        'pageSize': this.avgPageSize
      }
      this.$http.post('/api/v1/findAverage/findAverageInfos', updata)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.tableData = res.data.data.content
              this.tableData[this.tableData.length - 1].classNumber = '年级情况'
              this.avgTotal = res.data.data.recordTotal
              this.scoreNumData = this.tableData[0].averageRspList
              if (res.data.data.content[0].averageTotal) {
                this.hasSbjct = true
              }

              // 重构直方图数据
              let YTitle = []
              let sbjctData = []
              let tempObj = {}
              let index = 0
              let totalDatas = []

              let tableDataCopy = JSON.parse(JSON.stringify(this.tableData)).slice(0, -1)

              tableDataCopy.forEach((val) => {
                YTitle.push(val.className)
                if (val.averageTotal) {
                  totalDatas.push(val.averageTotal.avgScore)
                }
                if (val.averageRspList) {
                  val.averageRspList.forEach((ele) => {
                    tempObj = {}
                    tempObj.subjectName = ele.subjectName
                    tempObj.subjectId = ele.subjectId
                    tempObj.avgScore = ele.avgScore

                    sbjctData.push(tempObj)
                  })
                }
              })
              index = this.tableData[0].averageRspList ? this.tableData[0].averageRspList.length : 0
              let ansData = []
              let ansObj = {}
              for (let i = 0; i < index; i++) {
                ansObj = {}
                ansObj.subjectId = sbjctData[i].subjectId
                ansObj.subjectName = sbjctData[i].subjectName
                ansObj.sbjctData = []
                sbjctData.forEach((val) => {
                  if (sbjctData[i].subjectId === val.subjectId) {
                    ansObj.sbjctData.push(val.avgScore)
                  }
                })
                ansObj.YTitle = YTitle
                ansData.push(ansObj)
              }
              let TotalData = [{
                subjectId: 0,
                subjectName: '总分',
                sbjctData: totalDatas,
                YTitle: YTitle
              }]
              this.avgchartData = TotalData.concat(ansData)

              // 得到年级情况值
              let gradeLastRowData = JSON.parse(JSON.stringify(this.tableData)).slice(-1)[0]
              let gradeRowData = []
              if (gradeLastRowData.averageTotal) {
                gradeRowData.push(gradeLastRowData.averageTotal.avgScore)
              }
              if (gradeLastRowData.averageRspList) {
                gradeLastRowData.averageRspList.forEach((val) => {
                  gradeRowData.push(val.avgScore)
                })
              }
              this.gradeRowData = gradeRowData
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    avgHandleSizeChange (val) {
      this.avgFindcurrentPage = 1
      this.avgFindPageSize = val
      this.findScoreRank()
    },
    // 更新页数并 更新表数据
    avgHandleCurrentChange (val) {
      this.avgFindcurrentPage = val
      this.findScoreRank()
    },
    // 平均分导出
    averageExport () {
      let updata = {
        parameters: {
          examParentId: this.examName,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          curriculumIdList: this.subjectData,
          classTypeId: this.classTypeData,
          classIdList: this.classData
        }
      }
      this.$http({
        method: 'post',
        url: '/api/v1/findAverage/AverageExport',
        data: updata,
        responseType: 'blob'
      })
        .then(res => {
          if (res.status === 200) {
            let blob = res.data
            let reader = new FileReader()
            reader.readAsDataURL(blob)
            reader.onload = (e) => {
              let a = document.createElement('a')
              a.download = '平均分表.xls'
              a.href = e.target.result
              document.body.appendChild(a)
              a.click()
              document.body.removeChild(a)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    // ------------------------------ 平均分柱状图--------------------------------
    /**
     * 班级各科柱状图
     */
    clsSbjctChartClick () {
      this.clsSbjctChartShow = true
    },
    /**
     * 更新柱状图
     */
    clsSbjctdrawLine () {
      var myChart
      this.$nextTick(() => {
        this.$refs.chart.forEach((val, index) => {
          myChart = this.$echarts.init(val)
          // 绘制图表
          myChart.setOption({
            title: {
              x: 'center',
              text: this.avgchartData[index].subjectName
            },
            tooltip: {},
            legend: {
              bottom: 5,
              left: 'center',
              data: ['班级考试平均分']
            },
            xAxis: {
              data: this.avgchartData[index].YTitle
            },
            yAxis: {
              name: '考试分数',
              type: 'value'
            },
            series: [
              {
                name: '学生考试平均分',
                type: 'bar',
                data: this.avgchartData[index].sbjctData,
                barWidth: 20,
                label: { // 饼图图形上的文本标签
                  normal: {
                    show: true,
                    position: 'top' // 标签的位置
                  }
                },
                markLine: {
                  itemStyle: {
                    normal: {
                      lineStyle: {
                        color: 'blue'
                      },
                      label: {
                        show: true
                      }
                    }
                  },
                  data: [
                    {
                      name: '年级情况',
                      yAxis: this.gradeRowData[index] ? this.gradeRowData[index] : 0
                    }
                  ]
                }

              }
            ]

          })
        })
      })
    },

    /**
     * 关闭柱状图
     */
    clsSbjctCloseClick () {
      this.clsSbjctChartShow = false
    },
    remoteMethod (query) {
      var flag = 1
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },
  
    // 初始化考试名称
    initExamName (query, flag) {
      this.examName = ''
      this.gradeData = ''
      this.thetimeData = ''
      this.classTypeData = ''
      this.classLevelData = ''
      this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: query})
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.loading = false
              if (flag === 1) {
                this.examNames = res.data.data
              } else {
                this.comExamNames = res.data.data
              }
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    scoreRankClick () {
      let cope = {singelInput: '', seen: true}
      this.addInputs.push(cope)
      console.log(this.addInputs)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  },
  computed: {
    compareName: function () {
      return this.upscoredata + this.upline + this.score
    },
    userInfo () {
      return JSON.parse(window.localStorage.getItem('userInfo'))
    }

  },
  watch: {
    'avgchartData' (cur, old) {
      if (cur) {
        this.clsSbjctdrawLine()
      }
    }
  },

  mounted () {
    this.remoteMethod()
  }
}
</script>

