<template>
  <div class="examMngPage">
    <!-- 考试管理 -->
    <div class="examListBox" v-show="examShow">
      <div class="examAdd">
        <p>
          <router-link  :to="{path:'exam'}"> <i class="el-icon-plus"></i>新增考试</router-link>
        </p>
      </div>
      <div class="examListQuery">
        <div class="examListQueryTit">
          <p>查询条件</p>
        </div>
        <div class="examListQueryCont">
          <el-row :gutter="20">
            <el-form ref="examlistData" :model="examlistData" label-width="100px">

              <el-col :span="12">
                <el-form-item label="考试时间">
                <!-- <el-select v-model="examlistData.examName" clearable placeholder="选择考试名称" @change="examListQuerySearchFun">
                  <el-option
                    v-for="item in examListNameSelect"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select> -->
                <div class="exam-time-style">
                  <!-- {{examlistData.examName}} -->
                  <el-date-picker
                    v-model="examlistData.examName"
                    format="yyyy-MM-dd"
                    type="daterange"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期">
                  </el-date-picker>
                </div>

                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="考试类别">
                  <el-input v-model="examlistData.vague"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="4">
                <p class="v-classSeeQueryBtn" @click="examListQuerySearchFun">
                  搜索
                </p>
              </el-col>
            </el-form>
          </el-row>
        </div>
      </div>
      <div class="examList">
        <p class="examListTit">
          考试列表
        </p>
        <div class="examListTab">
          <el-table
            :data="examlistTbaleData"
            border
            style="width:820px">
            <el-table-column
              type="index"
              label="序号"
              width="38">
            </el-table-column>
            <el-table-column
              prop="examName"
              label="考试名称"
              width="200">
            </el-table-column>
            <el-table-column
              prop="examTime"
              label="考试时间"
              width="200">
            </el-table-column>
            <el-table-column
              prop="examTypeName"
              label="考试类别"
              width="200">
            </el-table-column>
            <el-table-column
              label="操作"
              width="180">
              <template slot-scope="scope">
                <el-button type="text"><span class="aradModify" @click="examDetailClick(scope.row)">详情</span></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>

        <div class="deparRenConPags">
          <div class="block">
             <el-pagination
              @size-change="examMngHandleSizeChange"
              @current-change="examMngHandleCurrentChange"
              :current-page="examMngcurrentPage"
              :page-sizes="[10, 100, 1000]"
              :page-size="examMngPageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="examMngTotal">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 考试详情 -->
    <div class="examMngBox" v-show="examDetailsShow">
        <div class="examMngTit">
          <span>
            {{examDetailData.examName}}
          </span>
        </div>
        <div class="examMngQuery">
          <span>选择学段</span>
          <el-select v-model="examDetailperiodVal" placeholder="考试学段" @change="examDetailperiodSlct">
            <el-option
              v-for="item in examDetailFindExamInfoData"
              :key="item.classPeriodId"
              :label="item.classPeriod"
              :value="item.classPeriodId">
            </el-option>
          </el-select>
          <span>选择届次</span>
          <el-select v-model="examDetailThetimeVal" placeholder="考试届次" @change="examDetailThetimeBySlct">
            <el-option
              v-for="item in examDetailThetimeData"
              :key="item.thetime"
              :label="item.thetime"
              :value="item.thetime">
            </el-option>
          </el-select>
        </div>
          <div class="examMngQuery">
            <span>选择类型</span>
            <el-select v-model="examDetailTypeVal" placeholder="请选择" @change="examDetailTypeBySlct">
              <el-option
                v-for="item in examDetailTypeData"
                :key="item.classTypeId"
                :label="item.classType"
                :value="item.classTypeId">
              </el-option>
              </el-select>
              <span>选择层次</span>
              <el-select v-model="examDetailLevelVal" placeholder="请选择" @change="examUpdateSlctFun">
                <el-option
                  v-for="item in examDetailLevelData"
                  :key="item.levelId"
                  :label="item.levelName"
                  :value="item.levelId">
                </el-option>
              </el-select>
          </div>
      <div class="examMngBoxTable">
        <div class="examMng">
          <span>
            参数设置操作
          </span>
        </div>
        <div class="examMngTab">
          <div class="setting-navbar">
            <a class="daoBtn daoBtn1"  :href="settingDownLoad">模板下载</a>
            <span class="daoBtn daoBtn2"  @click="settingImportClick">模板导入</span>
            <span class="daoBtn daoBtn3"  @click="argsSettingClick">批量设置</span>
          </div>
       
          <el-table
            :data="clsAndSbjctData"
            style="width: 100%"
            @selection-change="clsAndSbjctHandleSelectChange"
             ref="multipleTable"
            border>
            <el-table-column
              type="selection"
              >
            </el-table-column>
            <el-table-column
              prop="className"
              label="班级"
              >
            </el-table-column>
            <el-table-column
              prop="classTypeName"
              label="类型"
              width="105"
              >
            </el-table-column>
            <el-table-column
              prop="classLevelName"
              label="层次"
              >
            </el-table-column>

            <el-table-column
              label="考试课程(此次考试)"
              >
            <template slot-scope="scope">
              {{scope.row.subjects | getSbjctStr}}
            </template>
            </el-table-column>

            <el-table-column
              label="编辑档次"
              >
              <template slot-scope="scope">
                <el-button type="text"><span class="aradModify" @click="examEditClick(scope.row)">编辑</span></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="ed-back">
        <span class="daoBtn" @click="examDetailBackClick">返回考试列表</span>
      </div>

    </div>
     <!-- 成绩导入弹窗 -->
    <div class="inAllPast-mask" v-show="settingImportPopoverShow">
      <div class="ach-popover">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
          <span>参数设置导入</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
        </div>
        <div class="cstdExportmain">
          <div class="cstdExport-scroll">
            <el-upload
            class="upload-demo"
            ref="upload"
            :before-upload="beforeUpload"
            :auto-upload="false"
            :limit="1"
            :multiple="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <!-- <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">导入</el-button> -->
            <div slot="tip" class="el-upload__tip">上传模板只能是 xls、xlsx格式!并且文件小于12M</div>
            </el-upload>
          </div>
          <div class="cstdExportfooter">
            <div slot="footer" class="dialog-footer">
              <button class="save" @click="submitUpload">导 入</button>
              <button class="exit" @click="hideDialog">关 闭</button>
            </div>
          </div>
        </div>

      </div>
    </div>
    <!-- 编辑学科档次 -->
    <div class="exam-level-mask" v-show="editSbjctShow">
      <div class="exam-level">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
          <span>{{examDetailData.examName}}</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
          <p class="el-icon-circle-close-outline icon-close" @click="PopCloseClick"></p>
        </div>
        <div class="exam">
          <div class="examMngQuery">
            <span>参数设置</span>
            <el-select v-model="examLevelVal" placeholder="请选择" @change="settingChange">
              <el-option
                v-for="item in settingTypeSlct"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="examMngQuery">
          <span>考试课程</span>
            <el-select v-model="examSbjctVal" placeholder="请选择">
              <el-option
                v-for="item in getExamSbjctArr"
                :key="item.examSubjectId"
                :label="item.examSubject"
                :value="item.examSubjectId">
              </el-option>
            </el-select>
            <span>(设置单科的考试参数)</span>
          </div>
          <div class="score-rank clearfix">
            <div class="score score-rank-style">
              <p>分数档次</p>
              <div>
                <span>划分</span>
                <input type="text" class="level-input" v-model="examLevelScoreRankVal" @keyup.enter="examLevelScoreRankBlur">
                <span>档</span>
              </div>
              <span>(本班本课程分数,满分除外)</span>
              <p v-for="item in examLevelScoreRankData"
                :key="item.value"
              >
                <input type="text" class="level-input" v-model="item.score" ref="score" disabled>
                <input type="text" class="level-input" v-model="item.scorePer" ref="scorePer">
                <span class="input-line"></span>
                <input type="text" class="level-input" v-model="item.scoreNext" ref="scoreNext">
                分
              </p>

            </div>
            <div class="rank score-rank-style">
              <p>名次档次</p>
               <div>
                <span>划分</span>
                <input type="text" class="level-input" v-model="examLevelRankVal" @keyup.enter="examLevelRankBlur"/>
                <span>档</span>
              </div>
              <span>(本班本课程分数,满分除外)</span>
              <p v-for="item in examLevelRankData"
                :key="item.value"
              >
                <input type="text" class="level-input" v-model="item.rank" ref="rank" disabled>
                <input type="text" class="level-input" v-model="item.rankPer" ref="rankPer">
                <span class="input-line"></span>
                <input type="text" class="level-input" v-model="item.rankNext" ref="rankNext">
               名
              </p>

            </div>
          </div>
          <div class="clearfix diff-adv-style">
            <div class="diff diff-adv">
              <p>难度系数</p>
              <p>(本班本课程难度系数)</p>
              <input type="text" class="diff-adv-input" v-model="diffVal" >
            </div>
            <div class="adv diff-adv">
              <p>优势分数线</p>
              <p>(本班本课程学生优势课程分数线)</p>
              <input type="text" class="diff-adv-input"  v-model="advanceVal">
            </div>
          </div>
          <div class="examMngQuery">
            <span>任务数/分数线</span>
          </div>
          <div>
            <ul class="rank-score-table">
              <li
              v-for="(item, index) in rankAndScoreData"
              :key="item.classId"
              >
                <input type="text"  class="table-input-style" v-model="rankAndScoreData[index].className" disabled>
                <input type="text"  class="table-input-style" v-model="rankAndScoreData[index].examSubject" disabled>
                  <span
                  v-for="(li, count) in examLevelScoreRankData"
                  :key="li.value"
                  class="exam-level-style"
                  >
                    <input type="text"  class="table-input-style" v-model="item.rankandscore[count].taskNum" :placeholder='li.score +"/任务数"'>
                    <input type="text"  class="table-input-style" v-model="item.rankandscore[count].scoreNum" :placeholder='li.score +"/分数线"'>
                  </span>
              </li>
            </ul>
          </div>
          <div class="classAddfooter">
            <div slot="footer" class="dialog-footer">
              <button class="save" @click="sbjctEditClick">确 定</button>
              <button class="exit" @click="sbjctBackClick">返 回</button>
            </div>
          </div>
        </div>
      </div>
    </div>


    <!--编辑总分档次-->
    <div class="exam-total-mask" v-show="editTotalShow">
      <div class="exam-total">
        <div>
            <img src="@/assets/img/details-arrow-right_03.png" alt="">
            <span>{{examDetailData.examName}}</span>
            <img src="@/assets/img/details-arrow-left_03.png" alt="">
            <p class="el-icon-circle-close-outline icon-close" @click="PopCloseClick"></p>

        </div>
        <div>
         <div class="examMngQuery">
            <span>参数设置</span>
            <el-select v-model="examLevelVal" placeholder="请选择" @change="settingChange">
              <el-option
                v-for="item in settingTypeSlct"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <!-- <div class="examMngQuery">
            <span>{{getstdsecAndper}}</span>

          </div> -->
          <div class="score-rank clearfix">
            <div class="score score-rank-style">
              <p>分数档次</p>
              <div>
                <span>划分</span>
                <input type="text" class="level-input" v-model="examTotalScoreVal" @keyup.enter="examTotalScoreRankBlur">
                <span>档</span>
              </div>

              <span>(本班本课程分数,满分除外)</span>
              <p v-for="item in examTotalScoreRankData"
                :key="item.value"
              >
                <input type="text" class="level-input" v-model="item.score" ref="score" disabled>
                <input type="text" class="level-input" v-model="item.scorePer" ref="scorePer">
                <span class="input-line"></span>
                <input type="text" class="level-input" v-model="item.scoreNext" ref="scoreNext">
                分
              </p>

            </div>
            <div class="rank score-rank-style">
                <p>名次档次</p>
               <div>
                <span>划分</span>
                <input type="text" class="level-input" v-model="examTotalRankVal" @keyup.enter="examTotalRankBlur">
                <span>档</span>
              </div>
              <span>(本班本课程分数,满分除外)</span>
              <p v-for="item in examTotalRankData"
                :key="item.value"
              >
                <input type="text" class="level-input" v-model="item.rank" ref="rank" disabled>
                <input type="text" class="level-input" v-model="item.rankPer" ref="rankPer">
                <span class="input-line"></span>
                <input type="text" class="level-input" v-model="item.rankNext" ref="rankNext">
               名
              </p>
            </div>
          </div>
          <div class="clearfix diff-adv-style">
            <div class="diff diff-adv" style="width:80%">
              <p>难度系数</p>
              <p>(本班本课程难度系数)</p>
              <input type="text" class="diff-adv-input" v-model="examTotalDiffVal" >
            </div>
          </div>
          <div class="examMngQuery">
            <span>总分分数线(本届此次考试)</span>
          </div>
          <div>
            <ul style="margin-bottom: 30px;">
              <li
                 v-for="li in examTotalScoreRankData"
                  :key="li.value"
              >
                <input type="text"  class="table-input-style" v-model="li.score" :placeholder='li.score +"/分数线"' disabled>
                <input type="text"  class="table-input-style" v-model="li.total" :placeholder='li.score +"/分数线"'>

              </li>
            </ul>
          </div>
      <div class="classAddfooter">
        <div slot="footer" class="dialog-footer">
          <button class="save" @click="totalClick">确 定</button>
          <button class="exit" @click="totalBackClick">返 回</button>
        </div>
      </div>

        </div>

      </div>

    </div>

    <!--编辑班级档次-->
    <div class="exam-cls-mask" v-show="editClsShow">
      <div class="exam-cls">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
          <span>{{examDetailData.examName}}</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
           <p class="el-icon-circle-close-outline icon-close" @click="PopCloseClick"></p>
        </div>
        <div class="examMngQuery">
          <span>参数设置</span>
          <el-select v-model="examLevelVal" placeholder="请选择" @change="settingChange">
            <el-option
              v-for="item in settingTypeSlct"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span>(批量设置班级的总分各档任务数)</span>
        </div>
        <div class="examMngQuery">
          <span>{{stdsecThetimeType}}</span>
        </div>
        <div>
         <p class="setting-tilte">班级总分任务数</p>
          <ul class="cls-table">
            <li
            v-for="(item, index) in rankAndScoreData"
            :key="item.classId"
            >
              <input type="text"  class="table-input-style" v-model="rankAndScoreData[index].className" disabled>
                <div
                v-for="(li, index) in examTotalScoreRankData"
                :key="li.value"
                class="exam-cls-style"
                >
                  <input type="text"  class="table-input-style" v-model="item.clsTaskNum[index].taskNum" :placeholder='li.score +"/任务数"'>
                </div>
            </li>
          </ul>
        </div>
        <div class="classAddfooter">
          <div slot="footer" class="dialog-footer">
            <button class="save" @click="clsEditClick">确 定</button>
            <button class="exit" @click="clsBackClick">返 回</button>
          </div>
        </div>

      </div>
    </div>
    <!-- 编辑年级档次 -->
    <div class="exam-grade-mask" v-show="editGradeShow">
      <div class="exam-grade">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
          <span>{{examDetailData.examName}}</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
           <p class="el-icon-circle-close-outline icon-close" @click="PopCloseClick"></p>
        </div>
          <div class="examMngQuery">
            <span>参数设置</span>
            <el-select v-model="examLevelVal" placeholder="请选择" @change="settingChange">
              <el-option
                v-for="item in settingTypeSlct"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="examMngQuery">
          {{stdsecThetimeType}}
          </div>
          <div >
            <p class="setting-tilte">年级单科优势分数线</p>

            <ul class="cls-table">
              <li
              v-for="item in examGradeAdvData"
              :key="item.examSubjectId"
              >
                <input type="text"  class="table-input-style" v-model="item.examSubject" disabled>
                  <div>
                    <input type="text"  class="table-input-style" v-model="item.scoreNum" placeholder='优势分数线'>
                  </div>
              </li>
            </ul>
          </div>
          <div class="classAddfooter">
            <div slot="footer" class="dialog-footer">
              <button class="save" @click="gradeEditClick">确 定</button>
              <button class="exit" @click="gradeBackClick">返 回</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {formatDate} from '@/api/date.js'

export default {
  data () {
    return {
      // 考试详情
      examDetailData: {},
      examDetailperiodVal: '',
      examDetailThetimeVal: '',
      examDetailFindExamInfoData: [],
      // examDetailperiodData: [],
      examDetailThetimeData: [],
      examDetailTypeData: [],
      examDetailTypeVal: '',
      examDetailLevelData: [],
      examDetailLevelVal: '',
      // 考试 班级与课程表格数据
      clsAndSbjctData: null,
      // 考试与课程 复选值
      clsAndSbjctSlctArr: [],
      // 考试课程数组
      // examSbjctSlct: [],

      // 编辑学科档次
      examLevelVal: '单科',
      settingTypeSlct: [
        {
          value: '单科',
          label: '单科'
        }, {
          value: '总分',
          label: '总分'
        }, {
          value: '班级',
          label: '班级'
        }, {
          value: '年级',
          label: '年级'
        }
      ],
      examSbjctVal: '',
      examLevelScoreRankVal: '',
      examLevelScoreRankData: [],
      // 名次档次
      examLevelRankVal: '',
      examLevelRankData: [],
      // 难度系数值
      diffVal: '',
      // 优势分数线
      advanceVal: '',
      // 任务数/ 分数线
      // rankAndScoreData: [],
      // 总分档次设置模块
      editTotalShow: false,
      examTotalScoreVal: '',
      examTotalScoreRankData: [],
      examTotalRankVal: '',
      examTotalRankData: [],
      examTotalDiffVal: '',
      TotalArgSettingVal: '总分',

      // 编辑班级档次
      clsSettingVal: '班级',
      editSbjctShow: false,
      // 班级档次相关
      editClsShow: false,
      // 年级档次相关
      editGradeShow: false,
      gradeSettingVal: '年级',

      examDetailsShow: false,
      examShow: true,
      personPagObj: { // 储存分页数据
        pageNum: 1,
        pageSize: 10,
        currentPage: 1,
        recordCount: 1
      },
      examListNameSelect: [], // 储存考试名称下拉
      examlistData: {
        name: '',
        radio: '1',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        vague: '',
        examName: []
      },
      examlistTbaleData: [],
      examMngcurrentPage: 1,
      examMngPageSize: 10,
      examMngTotal: '',
      settingImportPopoverShow: false

    }
  },
  methods: {
    // 考试管理
    // /**
    //  *分页函数
    //  */
    // handleCurrentChange (val) {
    //   console.log(`当前页: ${val}`)
    //   // 初始化table
    //   let adminIdsObj = {
