          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data.content)
              this.examlistTbaleData = []
              let tableDataAddobj = {}
              for (var i = 0; i < res.data.data.content.length; i++) {
                tableDataAddobj = {}
                tableDataAddobj.serialNumber = i + 1
                tableDataAddobj.examName = res.data.data.content[i].examName
                tableDataAddobj.examTypeName = res.data.data.content[i].examTypeName
                tableDataAddobj.examParentId = res.data.data.content[i].examParentId
                if (res.data.data.content[i].examTime) {
                  var date = new Date(res.data.data.content[i].examTime)
                  tableDataAddobj.examTime = formatDate(date, 'yyyy-MM-dd')
                }
                this.examlistTbaleData.push(tableDataAddobj)
              }
            }
            this.personPagObj.currentPage = res.data.data.currentPage
            this.personPagObj.pageSize = res.data.data.pageSize
            this.personPagObj.recordCount = res.data.data.pageTotal
            this.examMngTotal = res.data.data.recordTotal
          } else {
            this.$notify({
              title: '提示信息',
              message: '操作失败',
              type: 'error'
