      * 查询班级信息
      * 届次 类型 层次 下拉 change事件更新表格数据
     */
    examDetailThetimeBySlct () {
      this.examDetailFindExamInfoData.forEach((val) => {
        val.thetimes.forEach((ele) => {
          if (ele.thetime === this.examDetailThetimeVal) {
          // 更新类型
            this.examDetailTypeData = val.thetimes.classTypes
            this.examDetailTypeVal = val.thetimes.classTypes[0].classTypeId
            // 更新层次
            this.examDetailLevelData = this.examDetailTypeData[0].classLevels

            this.examDetailLevelVal = this.examDetailLevelData[0].classLevelId
          }
        })
      })
      this.examUpdateSlctFun()
    },
    examDetailTypeBySlct () {
      this.examDetailFindExamInfoData.forEach((val) => {
        val.thetimes.forEach((ele) => {
          if (ele.thetime === this.examDetailThetimeVal) {
            ele.classTypes.forEach((item) => {
              if (item.classTypeId === this.examDetailTypeVal) {
                this.examDetailLevelData = item.classLevels
                this.examDetailLevelVal = this.examDetailLevelData[0].levelId
              }
            })
          }
        })
      })
      this.examUpdateSlctFun()
    },

