    /**
     * 单科成绩提交
     */
    sbjctEditClick () {
      // 班级
      var classIds = []
      // 任务数/分段数
      var taskList = []
      var tempObj = {}
      // 名次档次
      // 分数档次list
      var gradeList = []
      var isEnd = false
      this.examLevelScoreRankData.forEach((val) => {
        if (val.scorePer === '') {
          this.$message({
            showClose: true,
            message: '分数档次' + val.score + '最小值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }

        if (val.scoreNext === '') {
          this.$message({
            showClose: true,
            message: '分数档次' + val.score + '最大值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }

        gradeList.push({
          type: 1,
          gradeName: val.score,
          numMax: val.scoreNext,
          numMin: val.scorePer
        })
      })

      if (isEnd) {
        return false
      }

      this.examLevelRankData.forEach((val) => {
        if (val.rankPer === '') {
          this.$message({
            showClose: true,
            message: '名次档次' + val.rank + '最小值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }

        if (val.rankNext === '') {
          this.$message({
            showClose: true,
            message: '名次档次' + val.rank + '最大值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }

        gradeList.push({
          type: 2,
          gradeName: val.rank,
          numMax: val.rankNext,
          numMin: val.rankPer
        })
      })
      if (isEnd) {
        return false
      }
      this.rankAndScoreData.forEach((val) => {
        classIds.push(val.classId)
        tempObj = {
          classId: val.classId
        }
        tempObj.info = []
        val.rankandscore.forEach((ele) => {
          if (ele.taskNum === '') {
            this.$message({
              showClose: true,
              message: '请补全任务数数值',
              type: 'error'
            })
            isEnd = true
            return false
          }
          if (ele.scoreNum === '') {
            this.$message({
              showClose: true,
              message: '请补全分数线数值',
              type: 'error'
            })
            isEnd = true
            return false
          }
          tempObj.info.push({
            taskNum: ele.taskNum,
            numScore: ele.scoreNum
          })
        })
        this.examLevelScoreRankData.forEach((val, index) => {
          tempObj.info[index].gradeName = val.score
        })
        taskList.push(tempObj)
      })
      if (isEnd) {
        return true
      }

      // 课程
      var subjectName = ''
      if (this.examSbjctVal === '') {
        this.$message({
          showClose: true,
          message: '请选择一门课程',
          type: 'error'
        })
        return false
      }
      this.getExamSbjctArr.forEach((ele) => {
        if (this.examSbjctVal === ele.examSubjectId) {
          subjectName = ele.examSubject
          return false
        }
      })
      if (this.diffVal === '') {
        this.$message({
          showClose: true,
          message: '难度系数不能为空',
          type: 'error'
        })
        return false
      }
      if (this.advanceVal === '') {
        this.$message({
          showClose: true,
          message: '优势分数线不能为空',
          type: 'error'
        })
        return false
      }

      let updata = {
        examParentId: this.examDetailData.examParentId,
        // examParentId: 14,
        periodId: this.examDetailperiodVal,
        thetime: this.examDetailThetimeVal,
        subjectName: subjectName,
        advantageScore: this.advanceVal,
        degree: this.diffVal,
        classIds: classIds,
        gradeList: gradeList,
        taskList: taskList
      }
      this.$http
        .post(
          '/api/v1/examSettings/addParametersBySubject',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo 单科跳转到总分
              this.editSbjctShow = false
              this.examLevelVal = '总分'
              this.editTotalShow = true
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

