      var nameArr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'
      var examTotalRankVal = parseInt(this.examTotalRankVal)

      if (this.examTotalRankVal && !isNaN(examTotalRankVal) && typeof parseInt(this.examTotalRankVal) === 'number') {
        if (examTotalRankVal <= 0 || examTotalRankVal > 26) {
          this.$message({
            showClose: true,
            message: '请输入1-26之间的数值',
            type: 'error'
          })
          return false
        }
        for (let i = 0; i < this.examTotalRankVal; i++) {
          var tempObj = {
            label: Math.random() + i,
            value: Math.random() + i
          }

          tempObj['rank'] = nameArr[i]
          tempObj['rankPer'] = ''
          tempObj['rankNext'] = ''
          this.examTotalRankData.push(tempObj)
        }
        console.log('总分名次档次划分Data', this.examTotalRankData)
      } else {
        this.$message({
          showClose: true,
          message: '请输入1-26之间的数值',
          type: 'error'
        })
      }
    },
    /**
     * 总分确认button
     * 提交信息并跳转到班级档次设置
     * examDetailTypeVal
     */
    totalClick () {
      // this.editTotalShow = false
      var gradeList = []
      var isEnd = false
      this.examTotalScoreRankData.forEach((val) => {
        if (val.scorePer === '') {
          this.$message({
            showClose: true,
            message: '分数档次' + val.score + '最小值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }

        if (val.scoreNext === '') {
          this.$message({
            showClose: true,
            message: '分数档次' + val.score + '最大值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }
        if (val.total === '') {
          this.$message({
            showClose: true,
            message: '档次' + val.score + '总分分数线不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }
        gradeList.push({
          type: 1,
          gradeName: val.score,
          numMax: val.scoreNext,
          numMin: val.scorePer,
          numScore: val.total
        })
      })
      if (isEnd) {
        return false
      }
      this.examTotalRankData.forEach((val) => {
        if (val.rankPer === '') {
          this.$message({
            showClose: true,
            message: '名次档次' + val.rank + '最小值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }

        if (val.rankNext === '') {
          this.$message({
            showClose: true,
            message: '名次档次' + val.rank + '最大值不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }
        gradeList.push({
          type: 2,
          gradeName: val.rank,
          numMax: val.rankNext,
          numMin: val.rankPer
        })
      })
      if (isEnd) {
        return false
      }
      if (this.examTotalDiffVal === '') {
        this.$message({
          showClose: true,
          message: '总分难度系数不能为空',
          type: 'error'
        })
        return false
      }

      let updata = {
        examParentId: this.examDetailData.examParentId,
        // examParentId: 14,
        periodId: this.examDetailperiodVal,
        thetime: this.examDetailThetimeVal,
        classTypeId: this.examDetailTypeVal,
        degree: this.examTotalDiffVal,
        gradeList: gradeList
      }
      this.$http
        .post(
          '/api/v1/examSettings/addParametersByTotal',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.editTotalShow = false
              this.examLevelVal = '班级'
              this.editClsShow = true
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    totalBackClick () {
      this.editTotalShow = false
      this.editSbjctShow = true
    },
    /**
     * 班级设置总分参数设置
     */
    clsEditClick () {
      // this.editClsShow = false
      // 班级
      var classIds = []
      // 任务数/分段数
      var taskList = []
      var tempObj = {}
      var isEnd = false
      this.rankAndScoreData.forEach((val) => {
        classIds.push(val.classId)
        tempObj = {
          classId: val.classId
        }
        tempObj.info = []
        this.examTotalScoreRankData.forEach((ele, index) => {
          if (val.clsTaskNum.length > 0) {
            val.clsTaskNum.forEach((item) => {
              if (item.taskNum === '') {
                this.$message({
                  showClose: true,
                  message: '班级任务数不能为空',
                  type: 'error'
                })
                isEnd = true
                return false
              }
              if (ele.score === '') {
                this.$message({
                  showClose: true,
                  message: '班级分数段不能为空',
                  type: 'error'
                })
                isEnd = true
                return false
              }
              tempObj.info.push({
                type: '1',
                gradeName: ele.score,
                taskNum: item.taskNum
              })
            })
          }
        })
        taskList.push(tempObj)
      })
      if (isEnd) {
        return false
      }
      let updata = {
        examParentId: this.examDetailData.examParentId,
        // examParentId: 14,
        periodId: this.examDetailperiodVal,
        thetime: this.examDetailThetimeVal,
        classTypeId: this.examDetailTypeVal,
        classIds: classIds,
        taskList: taskList
      }

      this.$http
        .post(
          '/api/v1/examSettings/addParametersByTask',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.editClsShow = false
              this.examLevelVal = '年级'
              this.editGradeShow = true
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    clsBackClick () {
      this.editClsShow = false
      this.editTotalShow = true
    },
    // 年级优势分数线 点击事件

    gradeEditClick () {
      // this.editGradeShow = false
      var gradeList = []
      var isEnd = false
      this.examGradeAdvData.forEach((ele) => {
        if (ele.scoreNum === '') {
          this.$message({
            showClose: true,
            message: ele.examSubject + '优势分数线不能为空',
            type: 'error'
          })
          isEnd = true
          return false
        }
        gradeList.push({
          subjectId: ele.examSubjectId,
          subjectName: ele.examSubject,
          advantageScore: ele.scoreNum

        })
      })
      if (isEnd) {
        return false
      }
      let updata = {
        examParentId: this.examDetailData.examParentId,
        // examParentId: 14,
        periodId: this.examDetailperiodVal,
        thetime: this.examDetailThetimeVal,
        classTypeId: this.examDetailTypeVal,
        gradeList: gradeList

      }
      this.$http
        .post(
          '/api/v1/examSettings/addParametersScore',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.editGradeShow = false
              this.this.examLevelVal = '单科'
              // 重置复选班级
              this.$refs.multipleTable.clearSelection()
              this.examDetailsShow = true
              this.initSbjctPage()
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 关闭年级弹窗,更新参数设置为单科， 重置所有数据项
     */
    gradeBackClick () {
      this.editGradeShow = false
      this.examLevelVal = '单科'
      this.initSbjctPage()
    },

    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },

    tableDbEdit (event) {
      console.log(event)
      let curTarget = event.target

      curTarget.removeAttribute('readonly')
      curTarget.focus()

      curTarget.onblur = function () {
        console.log('curTarget', curTarget.value, curTarget)
        curTarget.setAttribute('readonly', 'true')
      }
    },
     handleHref (url) {
      const baseURL = this.$http.defaults.baseURL
      return baseURL ? baseURL + url : url
    }

  },
  filters: {
    getSbjctStr (sbjcts) {
      console.log('考试详情', sbjcts)
      var sbStr = []
      sbjcts.forEach((val) => {
        sbStr.push(val.examSubject)
      })
      return sbStr.join(',')
    }
  },
  computed: {
    settingDownLoad () {
      let ans = '/api/v1/examSettings/downLoadExcel'
      return this.handleHref(ans)
    },
    getstdsecAndper () {
      var stdsecAndPer = []
      this.examDetailFindExamInfoData.forEach((val) => {
        if (val.classPeriodId === this.examDetailperiodVal) {
          stdsecAndPer.push(val.classPeriod)
          return false
        }
      })
      stdsecAndPer.push(this.examDetailThetimeVal)
      stdsecAndPer.push('届')
      return stdsecAndPer.join('')
    },
    stdsecThetimeType () {
      var ans = []
      this.examDetailFindExamInfoData.forEach((val) => {
        if (val.classPeriodId === this.examDetailperiodVal) {
          ans.push(val.classPeriod)
          return false
        }
      })
      ans.push(this.examDetailThetimeVal)
      ans.push('届/')
      this.examDetailLevelData.forEach((val) => {
        if (this.examDetailLevelVal === val.levelId) {
          ans.push(val.levelName)
          return false
        }
      })
      return ans.join('')
    },
    getExamSbjctArr () {
      var allSbjct = []
      this.clsAndSbjctSlctArr.forEach((val) => {
        allSbjct.push(val.subjects)
      })
      allSbjct = allSbjct.reduce((arr, cur) => {
        arr = arr.concat(cur)
        return arr
      }, [])

      var ans = []
      var temp = {}
      allSbjct.forEach((ele) => {
        if (!temp[ele.examSubjectId]) {
          temp[ele.examSubjectId] = 1
          ans.push(ele)
        }
      })
      return ans
    },
    /**
     * 封装 表数据
     * 班级 clsAndSbjctSlctArr
     * 课程 examSbjctVal getExamSbjctArr
     * 档次 examLevelScoreRankData
     */
    rankAndScoreData () {
      var ans = []
      var ansObj
      this.clsAndSbjctSlctArr.forEach((val) => {
        // 班级
        ansObj = {
          classId: val.classId,
          className: val.className
        }
        // 课程
        this.getExamSbjctArr.forEach((ele) => {
          if (this.examSbjctVal === ele.examSubjectId) {
            ansObj['examSubjectId'] = ele.examSubjectId
            ansObj['examSubject'] = ele.examSubject
            return false
          }
        })
        // // 档次
        //   ansObj.rankandscore = []

        ans.push(ansObj)
      })
      console.log('考试表Data', ans)
      return ans
    },
    examGradeAdvData () {
      var ans = JSON.parse(JSON.stringify(this.getExamSbjctArr))
      ans.forEach((val) => {
        val.scoreNum = ''
      })
      return ans
    }

  },
  watch: {
    'rankAndScoreData' (cur, old) {
      console.log('考试表数据更新', cur, old)
    },
    'examLevelScoreRankData' (cur, old) {
      // console.log('考试表分数档次Data',cur, old)
    }
  },
  mounted () {
    let adminIdsObj = {
      'currentPage': this.examMngcurrentPage,
      'pageSize': this.examMngPageSize
    }
    this.examListQueryFun(adminIdsObj)
    this.examListQueryNameFun()
  }
}
