   .examListBox{
      flex: 1;
      // width: 850px;
      // margin: 30px auto;
      background-color: #fff;
      // box-shadow: 0px 0px 6px #c4c8ca;
      border-radius: 10px;
      padding-bottom: 30px;
    }
    .examAdd{
      padding: 22px 50px;
      p{
        width: 120px;
        height: 30px;
        border-radius: 5px;
        border:1px solid #6bc08b;
        line-height: 30px;
        text-align: center;
        color: #6bc08b;
        font-size: 14px;
        cursor: pointer;
        a{
          color: #6bc08b;
        }
      }
    }
    .examListQuery{
      padding: 20px 50px 30px 50px;
      border-top: 1px solid #f2f2f2;
      border-bottom: 1px solid #f2f2f2;
    }
    .examListQueryTit{
      margin-bottom: 18px;

      p{
        width: 60px;
        height: 20px;
        background-color: #c49df8;
        color: #fff;
        font-size: 12px;
        line-height: 20px;
        text-align: center;
      }
    }
    .examListQueryCont{
      .el-input__inner{
        height: 35px;
        line-height: 35px;
        font-size: 12px;
      }
      .el-form-item__label{
        height: 35px;
        line-height: 35px;
        font-size: 12px;
      }
      .el-form-item__content{
        height: 35px;
        line-height: 35px;
        font-size: 12px;
      }
      .el-form-item{
        margin-bottom: 0;
      }
    }
    .v-classSeeQueryBtn{
      width: 120px;
      height: 35px;
      background-color: #6cc08c;
      color: #fff;
      font-size: 12px;
      border-radius: 5px;
      text-align: center;
      line-height: 35px;
      cursor: pointer;

    }
    .examList{
      padding: 20px 50px 40px 50px;
    }
    .examListTit{
      width: 60px;
      height: 20px;
      background-color: #f8cb3e;
      color: #fff;
      font-size: 12px;
      line-height: 20px;
      text-align: center;
      margin-bottom: 20px;
    }
    .el-table td, .el-table th{
      padding: 0;
      font-size: 12px;
    }
    .el-table thead th{
      padding: 5px 0;
      text-align: center;

    }
    .aradModify{
      font-size: 12px;
      color: #6cc08c;
    }
    .examdetail{
      width: 200px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #6bc08b;
      font-size: 14px;
      cursor: pointer;
      a{
        color: #6bc08b;
      }
    }
    .deparRenConPags{
      margin: 30px 0 0 0;
    }
    .el-table .cell, .el-table th div {
      padding: 0 !important;

    }
    .footer{
      flex: 0;
    }
    .exam-time-style {
      .el-date-editor .el-range-separator {
          // padding: 0px 18px;
          line-height: 32px;
          width: 7%;
          color: #2d2f33;
      }
      .el-input__inner {
        height: 40px;
      }
    }

}

</style>



