<template>
  <div class="alivenum">
    <div class="scoreBox">
      <div class="scoreTit">
        <span>
          选择条件
        </span>
      </div>
      <div class="scoreQuery">
        <div class="scoreFirst">
          <span>选择考试</span>
          <div class="firstInput">
              <el-select
                v-model="examName"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="remoteMethod"
                :loading="loading"
                @change="examSlctClick">
                <el-option
                  v-for="item in examNames"
                  :key="item.examParentId"
                  :label="item.examName"
                  :value="item.examParentId">
                </el-option>
              </el-select>
          </div>
          <span>学段</span>
          <div class="inputStyle">
            <el-select v-model="gradeData" placeholder="高中" @change="gradeSlctClick">
            <el-option
              v-for="item in gradeDatas"
              :key="item.classPeriodId"
              :label="item.classPeriod"
              :value="item.classPeriodId">
            </el-option>
          </el-select>
          </div>
          <span>届次</span>
          <div class="inputStyle">
            <el-select v-model="thetimeData" placeholder="2018"  @change="classTypeSlctClick">
            <el-option
              v-for="item in thetimeDatas"
              :key="item.thetime"
              :label="item.thetime"
              :value="item.thetime">
            </el-option>
          </el-select>
          </div>
          <span>班级类型</span>
          <div class="inputStyle">
            <el-select v-model="classTypeData" placeholder="理科班"  @change="classLevelSlctClick">
            <el-option
              v-for="item in classTypeDatas"
              :key="item.classTypeId"
              :label="item.classType"
              :value="item.classTypeId">
            </el-option>
          </el-select>
          </div>
        </div>
        <div class="scoreSecond">
          <span>对比考试</span>
          <div class="secondInput">
          <el-select
                v-model="compareExamData"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="comRemoteMethod"
                :loading="loading"
                >
                <el-option
                  v-for="item in compareExamDatas"
                  :key="item.examParentId"
                  :label="item.examName"
                  :value="item.examParentId">
                </el-option>
              </el-select>
          </div>
          <span>线上分数</span>
          <div class="singelInput">
            <el-input v-model="upline" placeholder="10"></el-input>
          </div>
          <span>线下分数</span>
          <div class="singelInput">
            <el-input v-model="downline" placeholder="10"></el-input>
          </div>
          <div class="btnclick">
            <p class="scoreQueryBtn" @click="findScoreRank">
              查询
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="scoreBoxTable">
      <div class="scoreButtonTit">
        <a :href="alivenumExportDownload">导出</a>
      </div>
      <div class="scoreTab">
        <el-table
          :data="tableData"
          style="width: 100%"
          border
          @selection-change="handleSelectionChange"
          >
          <el-table-column
            type="selection"
            width="50"
            fixed
            >
          </el-table-column>
          <el-table-column
            label="班级"
            fixed
            >
            <template slot-scope="scope">
              {{ scope.row.classNumber }}
            </template>
          </el-table-column>
          <el-table-column
            label="班级类型"
            fixed
            >
            <template slot-scope="scope">
              {{ scope.row.classType }}
            </template>
          </el-table-column>
          <el-table-column
            label="班级层次"
            fixed
            >
            <template slot-scope="scope">
              {{ scope.row.classLevel }}
            </template>
          </el-table-column>
          <el-table-column
            label="班主任"
            fixed
            >
            <template slot-scope="scope">
              {{ scope.row.classHeadmaster }}
            </template>
          </el-table-column>
          <el-table-column
              v-for="(child, index) in scoreNumData" v-bind:key="index"
              :label="child.gradeName"
              >
              <el-table-column
              label="市内"
              >
              <template slot-scope="scope">
                <p>{{scope.row.surviveGradeList[index].inCity}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="市外"
              >
              <template slot-scope="scope">
                <p>{{scope.row.surviveGradeList[index].outCity}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="本校升入"
              >
              <template slot-scope="scope">
                <p>{{scope.row.surviveGradeList[index].theSchool}}</p>
              </template>
              </el-table-column>
              <el-table-column
              label="临界生"
              >
              <template slot-scope="scope">
                <p>{{scope.row.surviveGradeList[index].criticalRaw}}</p>
              </template>
              </el-table-column>
            </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- <div class="deparRenConPags">
         <div class="block">
            <el-pagination
              @size-change="aliveHandleSizeChange"
              @current-change="aliveHandleCurrentChange"
              :current-page="alivecurrentPage"
              :page-sizes="[10, 100, 1000]"
              :page-size="alivePageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="aliveTotal">
            </el-pagination>
          </div>
    </div> -->

  </div>

</template>
<script>
export default {
  data () {
    return {
      examName: '',
      examNames: [], // 考试名称
      gradeData: '',
      gradeDatas: [], // 学段
      thetimeData: '',
      thetimeDatas: [], // 届次
      classTypeData: '',
      classTypeDatas: [], // 班级类型
      compareExamData: '',
      compareExamDatas: [], // 对比考试
      scoreNumData: [], // 动态添加表格列
      upline: '', // 线上
      downline: '', // 线下
      value: '',
      multipleSelection: '',
      tableData: [],
      alivecurrentPage: 1,
      aliveTotal: '',
      alivePageSize: 10

    }
  },
  methods: {
    // 初始化考试名称
    // initExamName () {
    //   this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: this.examName})
    //     .then(res => {
    //       if (res.status === 200) {
    //         if (res.data.code === 1) {
    //           this.examNames = res.data.data

    //         } else {
    //           console.log(res.data.msg)
    //           this.$message({
    //             //  饿了么的消息弹窗组件,类似toast
    //             showClose: true,
    //             message: res.data.msg,
    //             type: 'error'
    //           })
    //         }
    //       }
    //     })
    //     .catch(err => {
    //       console.log(err)
    //     })
    // },
    // 选择考试名称查询学段等信息
    examSlctClick () {
      this.gradeData = ''
      this.gradeDatas = []

      this.thetimeData = ''
      this.thetimeDatas = []

      this.classTypeData = ''
      this.classTypeDatas = []

      this.tableData = []
      this.$http.get('/api/v1/schoolexam/findAllSchoolExamThetimeBySchoolExamParent',
        {
          params: {
            examParentId: this.examName
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.gradeDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据学段选择届次
    gradeSlctClick () {
      this.thetimeData = ''
      this.classTypeData = ''
      for (let i = 0; i < this.gradeDatas.length; i++) {
        if (this.gradeData === this.gradeDatas[i].classPeriodId) {
          this.thetimeDatas = this.gradeDatas[i].thetimes
        }
      }
    },
    // 根据届次选择班级类型
    classTypeSlctClick () {
      this.classTypeData = ''
      for (let i = 0; i < this.thetimeDatas.length; i++) {
        if (this.thetimeData === this.thetimeDatas[i].thetime) {
          this.classTypeDatas = this.thetimeDatas[i].classTypes
        }
      }
    },
    // 提交查询条件并将数据传递至表格
    findScoreRank () {
      if (this.examName === '') {
        this.$message.error('当前考试名称不能为空')
        return false
      }
      if (this.compareExamData === '') {
        this.$message.error('对比考试名称不能为空')
        return false
      }
      if (this.gradeData === '') {
        this.$message.error('学段不能为空')
        return false
      }
      if (this.thetimeData === '') {
        this.$message.error('届次不能为空')
        return false
      }
      if (this.classTypeData === '') {
        this.$message.error('班级类型不能为空')
        return false
      }
      if (this.upline === '') {
        this.$message.error('线上分数不能为空')
        return false
      }
      if (this.downline === '') {
        this.$message.error('线下分数不能为空')
        return false
      }

      this.$http.get('/api/v1/survive/findSurvive',
        {
          params: {
            newExamParentId: this.examName,
            oldExamParentId: this.compareExamData,
            periodId: this.gradeData,
            thetime: this.thetimeData,
            classTypeId: this.classTypeData,
            upScore: this.upline,
            downScore: this.downline
          }
        })
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.tableData = res.data.data.data
              this.scoreNumData = this.tableData[0].surviveGradeList
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    aliveHandleSizeChange (val) {
      this.alivecurrentPage = 1
      this.alivePageSize = val
      this.findScoreRank()
    },
    // 更新页数并 更新表数据
    aliveHandleCurrentChange (val) {
      this.alivecurrentPage = val
      this.findScoreRank()
    },
    remoteMethod (query) {
      var flag = 1
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },
    comRemoteMethod (query) {
      var flag = 2
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },

    // 初始化考试名称
    initExamName (query, flag) {
      if (flag === 1) {
        this.gradeData = ''
        this.thetimeData = ''
        this.classTypeData = ''
        this.classLevelData = ''
      }
      this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: query})
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.loading = false
              if (flag === 1) {
                this.examNames = res.data.data
              } else {
                this.compareExamDatas = res.data.data
              }
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    scoreRankClick () {
      let cope = {singelInput: '', seen: true}
      this.addInputs.push(cope)
      console.log(this.addInputs)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    /** 公共方法 */
    handleHref (url) {
      const baseURL = this.$http.defaults.baseURL
      return baseURL ? baseURL + url : url
    }
  },
  computed: {
    alivenumExportDownload () {
      // newExamParentId: this.examName,
      //     oldExamParentId: this.compareExamData,
      //     periodId: this.gradeData,
      //     thetime: this.thetimeData,
      //     classTypeId: this.classTypeData,
      //     upScore: this.upline,
      //     downScore: this.downline
      let arr = [
        '?newExamParentId=' + this.examName,
        '&oldExamParentId=' + this.compareExamData,
        '&periodId=' + this.gradeData,
        '&thetime=' + this.thetimeData,
        '&classTypeId=' + this.classTypeData,
        '&upScore=' + this.upline,
        '&downScore=' + this.downline
      ]
      let ans = '/api/v1/survive/exportSurvive' + arr.join('')
      return this.handleHref(ans)
    }
  },
  mounted () {
    this.remoteMethod()
    this.comRemoteMethod()
  }
}
</script>

<style lang="scss">
.alivenum{
  .scoreBox{
    border-bottom: 1px solid #b8d6cd;
    padding: 20px 10px 20px 10px;
  }
  .scoreTit{
    text-align: center;
    margin-bottom: 6px;
    background-color: #6cc08c;
    width: 100px;
    height: 35px;
    line-height: 35px;
    border-radius: 5px;
      span{
        padding: 4px 8px;
        color: #fff;
        font-size: 14px;
      }

  }
  .scoreQuery{
    width:960px;
    .inputStyle{
      display: inline-block;
      margin-right: 20px;
      .el-input__inner{
        height: 30px;
        width:100px;
        line-height: 30px;
      }
    }
  }
  .scoreFirst{
     width: 100%;
    margin-top: 15px;
    margin-bottom: 32px;
    text-align: left;
    margin-bottom: 32px;
    .firstInput{
      display: inline-block;
      margin-right: 20px;
      .el-input__inner{
      height: 30px;
      width:210px;
      line-height: 30px;
    }
    }
  }
  .scoreSecond{
     width: 100%;
    margin-top: 15px;
    margin-bottom: 32px;
    text-align: left;
    margin-bottom: 32px;
    .secondInput{
      display: inline-block;
      margin-right: 20px;
      .el-input__inner{
        height: 30px;
        width:210px;
        line-height: 30px;
      }
    }
    .singelInput{
      display: inline;
      margin-left: 12px;
      margin-right: 20px;
        .el-input{
          height: 30px;
          width: 60px;
          line-height: 30px;
        }
        .el-input__inner{
          height: 30px;
          width: 60px;
          line-height: 30px;
        }
    }
  }
  .btnclick{
    display: inline-block;
    .scoreQueryBtn{
      width: 105px;
      height: 30px;
      border-radius: 5px;
      text-align: center;
      color: #6cc08c;
      border: 1px solid #6cc08c;
      line-height: 30px;
      cursor: pointer;
      position:absolute;
      margin-left: 55px;
      margin-top: -18px;
    }
   }
  .scoreBoxTable{
    padding: 20px 10px 36px 10px;
    .scoreButtonTit{
      a{
        display: inline-block;
        color: #fff;
        background-color: #c49df8;
        border-color: #c49df8;
        font-size: 14px;
        width: 100px;
        border-radius: 5px;
        line-height: 35px;
        margin-left: 720px;
        cursor: pointer;
      }
    }
  }
  .scoreTab{
    padding: 30px 40px 30px 40px;
    .el-table td, .el-table th{
      padding: 0;
      font-size: 12px;
    }
    .el-table thead th>.cell{
      text-align: center;
    }
    .el-table thead th{
      padding: 8px 0;
    }
    .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed {
      background-color: #fafcfc;
    }
  }
}
</style>



