<template>
  <div class="deploy">
      <div class="deployBox">
      </div>

      <div class="cdeploy">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
            <span>班级调配</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
        </div>
        <div class="cdeploymain">
          <div>
            班主任:
            <el-input v-model="clsData.fullName"
            :disabled="true"
            >
            </el-input>
          </div>
          <div>
            <span>班级号:</span>
             <el-select v-model="clsData.classId" placeholder="请选择">
              <el-option
                v-for="item in pendingClsData"
                :key="item.classId"
                :label="item.classNumber"
                :value="item.classId">
              </el-option>
            </el-select>
          </div>

        </div>
        <div class="deployfooter">
          <div slot="footer" class="dialog-footer">
              <button class="save" @click="clsDpClick">修 改</button>
              <button class="exit" @click="hideDialog">返 回</button>
          </div>
        </div>
      </div>

  </div>
  </template>
<script>
export default {
  name: 'deploypopover',
  props: ['clsdeployData'],
  data () {
    return {
      pendingClsData: [],
      clsData: {
        fullName: '',
        classId: ''
      },
      isUpdate: false

    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
      if (this.isUpdate) {
        this.$emit('upTable', 'getModifyData')
      }
    },
    initPendingData () {
      this.$http
        .get(
          '/api/v1/schoolclass/findClassNoHeadmaster'
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.pendingClsData = res.data.data
              console.log('this.pendingClsData', this.pendingClsData)
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    clsDpClick () {
      this.$confirm('确定修改该班主任所带的班级?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.clsDpUpdate()
        })
        .catch(() => {
          this.$message({ type: 'info', message: '已取消删除' })
        })
    },
    clsDpUpdate () {
      let updata = {
        adminId: this.clsData.adminId,
        classId: this.clsData.classId,
        fullName: this.clsData.fullName
      }
      console.log('updata', updata)
      this.$http
        .put(
          '/api/v1/schoolclass/updateHeadmaster',
          this.$qs.stringify(updata)
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
              this.isUpdate = true
              this.pendingClsData.forEach((cVal, Cindex) => {
                if (this.clsData.classId === cVal.classId) {
                  this.clsData.classNumber = cVal.classNumber
                  this.clsData.className = cVal.className
                }
              })
              console.log('clsData', this.clsData)
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }, /** method end */
  watch: {
    'clsdeployData' (cur, old) {
      console.log('clsdeployData', cur)
      this.clsData = cur
    }
  },
  computed: {
    getModifyData () {
      return this.clsData
    }
  },

  mounted () {
    this.initPendingData()
  }
}
</script>
<style lang="scss">
.deploy {
  .deployBox {
    position: fixed;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    // height: 2000px;
    min-width: 960px;
    z-index: 100;
  }
  .cdeploy {
    background: white;
    position: absolute;
    width: 420px;
    height: 435px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 200;
  }
  .cdeploy > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .cdeploy > div:nth-of-type(1) > img {
    vertical-align: middle;
  }
  .cdeploymain > div {
    margin-top: 28px;
    margin-left: 40px;
    text-align: left;

    color: #999;
    font-size: 16px;
  }
  .cdeploymain > div:nth-of-type(1) > span {
    margin-left: 20px;
  }
  .cdeploymain {
    color: #c3c3c3;
    .el-input {
      width:220px;
    }
  }

  #project {
    border-radius: 5px;
    background: #f2f2f2;
    width: 90px;
    height: 35px;
    appearance: none;
    margin-left: 25px;
    margin-top: 10px;
    color: #c3c3c3;
  }
  .popoverinput {
    border-radius: 5px;
    background: #f2f2f2;
    width: 90px;
    height: 35px;
    margin-left: 18px;
    color: #c3c3c3;
  }
  .deployfooter {
    width: 420px;
    position: absolute;
    bottom: 20px;
  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }

}
</style>



