<template>
  <div class="v-classSeePags">
    <div class="v-classSeeBox">
    <div class="v-classSeeTit">
        <span>
          选择条件
        </span>
    </div>
    <div class="after v-classSeeQuery">
      <el-row :gutter="20">

        <el-col :span="6">
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="6">
          <p class="v-classSeeQueryBtn">
            搜索
          </p>
        </el-col>
      </el-row>
      </div>
    </div>
    <div class="after gradeTabBox classSeeTabBox">
      <div class="after gradSeeTit">
          <span>
            班级成绩单
          </span>
      </div>

      <div class="after gradSeeBtnBox vclassSeeBtn" >
        <button class="downBtn">查看分数</button>
        <button class="dcSeeBtn">查看档次</button>
        <button class="daoBtn">历次考试均分趋势图</button>
        <button class="bodBtn">波动学生</button>
        <button class="daochuBtn">导出</button>
      </div>
      <div class="gradeTab">
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column
            prop="bj"
            label="班级"
            width="90">
          </el-table-column>
          <el-table-column
            prop="xk"
            label="学科"
            width="90">
          </el-table-column>
          <el-table-column
            prop="skrs"
            label="实考人数"
            width="90">
          </el-table-column>
          <el-table-column
            prop="qkrs"
            label="缺考人数"
            width="90">
          </el-table-column>
          <el-table-column
            prop="pjf"
            label="平均分"
            width="87">
          </el-table-column>

          <el-table-column
            prop="zdf"
            label="最低分"
            width="85">
          </el-table-column>
          <el-table-column
            prop="zgf"
            label="最高分"
            width="85">
          </el-table-column>
          <el-table-column
            prop="pm"
            label="排名"
            width="85">
          </el-table-column>
          <el-table-column
            label="操作"
            width="96">
            <template slot-scope="scope">
              <el-button type="text"><span class="aradModify">详情</span></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="deparRenConPags">
        <div class="block">

          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-size="100"
            layout="total, prev, pager, next, jumper"
            :total="1200">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'class-see',
  data () {
    return {
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      value: '',
      tableData: [{
        bj: '8.5班',
        skrs: '88',
        xk: '语文',
        qkrs: '2',
        pjf: '120',
        zdf: '20',
        zgf: '150',
        pm: '2'
      }],
      currentPage1: 5,
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 4,
      labelPosition: 'right'
    }
  },
  methods: {
    onSubmit () {
      console.log('submit!')
    },
    handleClick (row) {
      console.log(row)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    }
  }
}
</script>

<style lang="scss">
  .v-classSeeBox{
    border-bottom: 1px solid #b8d6cd;
    padding: 20px 10px 36px 10px;
  }
  .v-classSeeTit{
    text-align: left;
    margin-bottom: 6px;
    span{
      padding: 4px 8px;
      background-color: #6cc08c;
      color: #fff;
      font-size: 12px;
    }

  }
  .v-classSeeQuery{
    padding-left: 100px;
    .el-input__inner{
      height: 30px;
      line-height: 30px;
    }
  }
  .v-classSeeQueryBtn{
    width: 80px;
    height: 30px;
    border-radius: 5px;
    text-align: center;
    color: #6cc08c;
    border: 1px solid #6cc08c;
    line-height: 30px;
    cursor: pointer;
  }
  .classSeeTabBox{
    .gradSeeBtnBox{
      margin-bottom: 30px;
    }
  }
  .vclassSeeBtn{
    padding-left: 110px;
    button{
      margin-right: 40px;
    }
    button:last-child{
      margin-right: 0;
    }
  }
  .bodBtn{
    background-color: #81e5c3;
  }
</style>



