            periodId: this.perSelected
          }
        })
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            this.umgdItems = res.data.data
            if (this.umgdItems.length > 0) {
              this.slctGrade = this.umgdItems[0].thetime
              this.updateClsTable()
            } else {
              this.slctGrade = ''
              this.updateClsTable()
            }
            console.log('umgdItems', this.umgdItems, this.slctGrade)
          } else {
            console.log(res.data.msg)
          }
        })
        .catch(err => {
          console.log(err)
        })
