      this.$confirm('此操作将永久删除该班级, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.delClsInfo(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    delClsInfo (row) {
      let updata = {
        params: {
          classId: row.classId
        }
      }
      this.$http
        .delete(
          '/api/v1/schoolclass/updateClassInfoDel',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.$message({
                showClose: true,
                message: '删除成功',
                type: 'success'
              })

              this.clsData.forEach((val, index) => {
                if (row.classId === val.classId) {
                  this.clsData.splice(index, 1)
                }
              })
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
