      this.sbjctDpTeaShow = true
      this.activeData = row.subject
      // 初始时，将已经有的值放入筛选中
      let tempArr = []
      this.activeData.forEach((val) => {
        tempArr.push({
          fullName: val.fullName,
          staffNumber: val.staffNumber
        })
      })
      var hash = {}
      tempArr = tempArr.reduce((item, next) => { 
        if (!hash[next.staffNumber]) {
          hash[next.staffNumber] = true
          item.push(next)
        }
        return item
      }, [])
      this.teacherInfos = tempArr
    },
    // 初始化考试名称
    remoteMethod (query) {
      var flag = 1
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },
    // 初始化考试名称
    initExamName (query, flag) {
      let updata = {
        pageSize: 20,
        parameters: {
          vague: query
        }
      }
      this.$http
        .post(
          '/api/v1/personnelManagement/selectAdminInfoByVague',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.loading = false
              // this.teacherInfos = res.data.data
              let curData = res.data.data.content
              let tempArr = []
              for (let item of curData) {
                tempArr.push({
                  staffNumber: item.staffNumber,
                  fullName: item.fullName
                })
              }
              this.teacherInfos = tempArr
              console.log('老师信息', this.teacherInfos)
            } else {
              console.log(res.data.msg)
