          if (res.status === 200) {
            if (res.data.code === 1) {
              this.sbjctData = res.data.data
              if (this.sbjctData) {
                this.sbjctData.forEach((val) => {
                  this.$set(val, 'show', false)
                  this.$set(val, 'sbjctShow', false)
                })
              }
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
