      console.log('nihao', this.perIdSelected, this.theTimeGrade, this.slctClass)
      for (let i = 0; i < this.umPerIdItems.length; i++) {
        if (this.perIdSelected === this.umPerIdItems[i].classPeriodId) {
          if (this.umPerIdItems[i].thisYear) {
