    // 学生信息初始化查询
    initStudentTable () {
      this.$http.get('/api/v1/studentManagement/findAllPeriodAndThetimeAndClassBySchoolId')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.umPerIdItems = res.data.data
              if (this.umPerIdItems.length > 0) {
                console.log('this.umPerItems', this.umPerItems)
                this.perIdSelected = this.umPerIdItems[0].classPeriodId
              }

              for (let i = 0; i < this.umPerIdItems.length; i++) {
                if (this.perIdSelected === this.umPerIdItems[i].classPeriodId) {
                  this.theTimeItems = this.umPerIdItems[i].thisYear
                  if (this.theTimeItems.length > 0) {
                    console.log('this.theTimeItems', this.theTimeItems)
                    this.theTimeGrade = this.theTimeItems[0].thetiem
                  }
                }
              }
              for (let i = 0; i < this.theTimeItems.length; i++) {
                if (this.theTimeGrade === this.theTimeItems[i].thetiem) {
                  this.classItems = this.theTimeItems[i].classInfo
                  if (this.classItems.length > 0) {
                    console.log('this.classItems', this.classItems)
                    this.slctClass = this.classItems[0].classId
                  }
                }
              }
              console.log('=================', this.umPerIdItems, this.theTimeItems, this.classItems)
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
