  filters: {
    transDate (date) {
      date = new Date(date)
      return formatDate(date, 'yyyy-MM-dd')
    },
    getGradeList (li) {
      let gdArr = []
      li.forEach(val => {
        gdArr.push(val.gradeName)
      })
      li = gdArr.join(',')
      return li
    },
    getSubjectList (li) {
      let gdArr = []
      li.forEach(val => {
        gdArr.push(val.subjectName)
      })
      li = gdArr.join(',')
      return li
    },
    getsbjctName (val) {
      let li = []
      val.forEach(ele => {
        li.push(ele.subjectName)
      })
      return li.join(',')
    },
    judgeGender (gender) {
      let ans = ''
      if (gender === '0') {
        ans = ''
      } else if (gender === '1') {
        ans = '男'
      } else {
        ans = '女'
      }
      return ans
    },
    getTheTime (time) {
      time = new Date(time)
      return formatDate(time, 'yyyy')
    }
  }, /** filters end */
