<template>
  <div class="examPags">
    <v-header></v-header>
    <div class="examBox">
      <el-form ref="examAddForm" :model="examAddForm" label-width="130px">
        <div class="examContBox">
          <p class="examTit">
            考试参数设置
          </p>
          <el-row :gutter="50">
            <el-col :span="12">
              <el-form-item label="考试类别">
                <el-input v-model="examAddForm.examTypeName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="考试时间">
                <el-date-picker type="date" placeholder="选择日期" v-model="examAddForm.examTime" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="考试名称">
                <el-input v-model="examAddForm.examName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="考试年级">
                <el-select v-model="examAddForm.examObject" multiple placeholder="请选择" @change="examQuerygrade">
                  <el-option
                    v-for="item in examGradeIdSelect"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
        <div class="examGradeBox">
          <div class="examGradeCont">
            <p class="examTit">
              操作
            </p>
            <el-table
              :data="examtablegrad"
              border
              style="width: 100%;text-align: center">
              <el-table-column
                label="年级列表"
                prop="gradeNameStr"
                text-alegn="left"
              >
              </el-table-column>
              <el-table-column type="expand">
                <template slot-scope="props">
                  <div class="examGradeTab">
                    <div class="exam-header">
                      <el-form-item label="操作">
                        <!-- <el-input v-model="getClsSlctData[props.$index].clsVagueShVal"  placeholder="层次查询" @keyup.enter.native="clsVagueShClick(props.row, props.$index)"></el-input> -->
                        <el-button @click="batchEditCourseClick">批量编辑考试课程</el-button>
                      </el-form-item>
                    </div>
                    <el-table
                      :data="props.row.classInfo"
                      border
                      ref="multipleTable"
                      @selection-change="clsSelectedChange"
                      style="width: 100%;text-align: center">
                       <el-table-column
                        width="50"
                        type="selection"
                       >
                       </el-table-column>
                      <el-table-column
                        label="班级"
                        width="100">
                        <template slot-scope="scope">
                          {{ scope.row.className }}
                        </template>
                      </el-table-column>
                       <el-table-column
                        prop="classType"
                        label="类型"
                        :filters="getClsTypeNames(props.row.classInfo)"
                        :filter-method="filterTag"
                        width="100">
                        <template slot-scope="scope">
                          {{ scope.row.classType }}
                        </template>
                      </el-table-column>
                      <el-table-column
                        label="考试课程"
                        width="275">
                        <template slot-scope="scope">
                          {{ scope.row.subjectList.length === 0 ? '' : scope.row.subjectList }}
                        </template>
                      </el-table-column>
                      <el-table-column
                        label="操作"
                        width="80">
                        <template slot-scope="scope">
                          <el-button type="text" @click="examQuerySubject(scope.row)"><i @click="singleSelectedChange([scope.row])" class="addIcon el-icon-edit-outline"></i></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </template>
              </el-table-column>

            </el-table>

          </div>
          <!--</el-table>-->
        </div>
        <div class="examBtnBox">
          <div class="BtnSubmit" @click="examSubForm">确定</div>
         <!-- <button @click="examSubForm">确定</button> -->
        </div>
      </el-form>
    </div>
    <!--修改班级考试科目-->
    <div class="examMidfyBox" v-show="examsessios.examPopUpFlag">
      <div class="examMidfy">
        <div class="departmentPopTitBox">
          <div class="departmentPopTit">
            <img src="@/assets/left.png" alt="">
            <span>编辑考试课程</span>
            <img src="@/assets/right.png" alt="">
          </div>
          <div class="examMidfyCont">
            <el-form ref="examObj" :model="examObj" label-width="80px">
              <el-form-item label="课程列表">
                <el-input type="textarea" v-model="getExamCourse" ></el-input>
              </el-form-item>
              <el-form-item label="考试课程">
                <el-select v-model="examObj.subjectList" multiple collapse-tags placeholder="请选择">
                  <el-option
                    v-for="item in examSubjectSelect"
                    :key="item.subjectId"
                    :label="item.subjectName"
                    :value="item.subjectId + '_' + item.subjectName">
                  </el-option>
                </el-select>
              </el-form-item>
              <div class="examMidfyBtnBox">
                <el-button @click="examSubjectUpdateClick">
                  确认
                </el-button>
                <el-button @click="examSubjectHideClick">
                  返回
                </el-button>
              </div>
            </el-form>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <v-footer></v-footer>
    </div>
  </div>
</template>

<script>
import vHeader from '@/components/header'
import vFooter from '@/components/bottom'
import { formatDate } from '@/api/date.js'

export default {
  name: 'exam',
  components: {
    vHeader,
    vFooter
  },
  data () {
    return {
      examAddForm: {
        examName: '',
        examTypeName: '', // 考试类别
        examTime: '', // 考试时间
        examObject: '',
        params: []
      },
      examsessios: {
        examPopUpFlag: false // 控制弹窗显示
      },
      examObj: {// 储存一些公用变量
        classList: '',
        subject: '',
        subjectList: []
      },
      examClassIdSelect: [],
      examSubjectSelect: [], // 存储科目
      clsSelectedArr: [],
      examGradeIdSelect: [],
      examobjCs: {},
      examtablegrad: [], // 储存table数据
      dataShow: [], // 表格展示数据
      examtablegradCody: []
    }
  },
  mounted () {
    this.examQueryAverygrade()
  },
  methods: {
    /**
       * 考试年级绑定事件
       */

    examQuerygrade () {
      var examGradeArr = []
      if (this.examAddForm.examObject) {
        console.log('nihao1')
        for (var i = 0; i < this.examAddForm.examObject.length; i++) {
          if (this.examAddForm.examObject[i].indexOf(',') > 0) {
            var examGradeObj = {}
            var result = this.examAddForm.examObject[i].split(',')
            examGradeObj.periodId = result[0]
            examGradeObj.thetime = result[1]
            examGradeArr.push(examGradeObj)
          }
        }
        console.log(examGradeArr)
        if (examGradeArr.length <= 0) {
          this.examtablegrad = []
          return false
        }
        this.$http.post('/api/v1/schoolexam/findAllClass', examGradeArr)
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                console.log(res.data.data)
                var subData = res.data.data
                var subjectArr = []

                for (var i = 0; i < subData.length; i++) {
                  var subjectJson = {}
                  subjectJson.periodId = subData[i].periodId
                  subjectJson.periodName = subData[i].periodName
                  subjectJson.thetime = formatDate(new Date(subData[i].thetime), 'yyyy-MM-dd')
                  subjectJson.gradeNameStr = subData[i].periodName + formatDate(new Date(subData[i].thetime), 'yyyy') + '届'
                  subjectJson.classInfo = []
                  if (subData[i].classInfo === null) {
                    subData[i].classInfo = []
                  }
                  for (var j = 0; j < subData[i].classInfo.length; j++) {
                    subjectJson.classInfoObj = {}
                    subjectJson.classInfoObj.classId = subData[i].classInfo[j].classId
                    subjectJson.classInfoObj.className = subData[i].classInfo[j].className
                    subjectJson.classInfoObj.classType = subData[i].classInfo[j].classType
                    subjectJson.classInfoObj.isShow = false
                    subjectJson.classInfoObj.subjectList = []
                    subjectJson.classInfoObj.subject = []
                    subjectJson.classInfo.push(subjectJson.classInfoObj)
                  }
                  subjectArr.push(subjectJson)
                }
                console.log(subjectArr)
                this.examtablegrad = subjectArr
                this.examtablegradCody = JSON.parse(JSON.stringify(this.examtablegrad))

                this.dataShow = this.examtablegrad[0].classInfo
              } else {
                this.$notify({
                  title: '提示信息',
                  message: '操作失败',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
            this.$notify({
              title: '提示信息',
              message: 'err.msg',
              type: 'error'
            })
          })
      }
    },
    /**
       * 查询所有年级
       */
    examQueryAverygrade () {
      var gradeJson = {}
      var gradeArr = []
      this.examGradeIdSelect = []
      this.$http.get('/api/v1/schoolexam/findGrade')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              if (res.data.data.length >= 1) {
                for (var i = 0; i < res.data.data.length; i++) {
                  gradeJson = {}
                  gradeJson.value = res.data.data[i].periodId + ',' + res.data.data[i].thetime
                  gradeJson.label = res.data.data[i].periodName + res.data.data[i].thetime + '届'
                  gradeArr.push(gradeJson)
                }
                this.examGradeIdSelect = gradeArr
                console.log(this.examGradeIdSelect)
              } else {

              }
            } else {
              this.$notify({
                title: '提示信息',
                message: '操作失败',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     * 编辑课程返回点击
     **/
    examSubjectHideClick () {
      this.examsessios.examPopUpFlag = false
      this.examObj.subjectList = []
    },
    /**
     * 本地模糊查询
     */
    clsVagueShClick (item, index) {
      // if (this.clsVagueShVal && (this.clsVagueShVal !== '')) {
      //   let searchRegexp = new RegExp(this.clsVagueShVal)
      //   let arr1 = []
      //   // let arr2=[]
      //   for (let i = 0; i < item.classInfo.length; i++) {
      //     if (searchRegexp.test(item.classInfo[i].classType)) {
      //       arr1.push(item.classInfo[i])
      //     }
      //   }
      //   item.classInfo = arr1
      // } else {
      //   item.classInfo = this.examtablegrad[0].classInfo
      // }
      var clsVagueShVal = ''
      this.getClsSlctData.forEach((val, count) => {
        if (parseInt(index) === count) {
          clsVagueShVal = val.clsVagueShVal
          return false
        }
      })
      let Reg = new RegExp(clsVagueShVal)
      console.log('新增考试clsVagueShVal', clsVagueShVal)

      let arr1 = []
      let classInfo = []
      this.examtablegradCody.forEach((val) => {
        if (val.gradeNameStr === item.gradeNameStr) {
          classInfo = JSON.parse(JSON.stringify(val.classInfo))
          return false
        }
      })
      if (clsVagueShVal && clsVagueShVal !== '') {
        console.log('新增考试classInfo', clsVagueShVal)
        for (let i = 0; i < classInfo.length; i++) {
          if (Reg.test(classInfo[i].classType)) {
            arr1.push(classInfo[i])
          }
        }
        item.classInfo = arr1
      } else {
        item.classInfo = classInfo
      }
    },
    /**
     * 编辑考试课程确定按钮点击
     * 保存临时数据
     */
    examSubjectUpdateClick () {
      let subject = []
      this.examObj.subjectList.forEach((val) => {
        let obj = {}
        obj = {
          subjectId: val.split('_')[0],
          subjectName: val.split('_')[1]
        }
        subject.push(obj)
      })
      this.clsSelectedArr.forEach((val) => {
        val.subject = subject
        let subjectList = []
        val.subject.forEach((ele) => {
          subjectList.push(ele.subjectName)
        })
        val.subjectList = subjectList.toString()
      })
      console.log('this.clsSelectedArr', this.clsSelectedArr)
      this.examsessios.examPopUpFlag = false

      // 重置弹出框的课程列表
      this.examObj.subjectList = []
    },
    /**
     * 查询科目弹窗关闭
     */
    examSubjectClose () {
      var examsubjectName = []
      var examsubjectid = []
      var examsubjectObj = {}
      if (this.examObj.subjectList) { // 判断选择科目没有
        for (var i = 0; i < this.examObj.subjectList.length; i++) {
          examsubjectObj = {}
          if (this.examObj.subjectList[i].indexOf(',') > 0) {
            var result = this.examObj.subjectList[i].split(',')
            examsubjectName.push(result[0])
            examsubjectObj.subjectId = Number(result[1])
            examsubjectObj.subjectName = result[0]
            examsubjectid.push(examsubjectObj)
          }
        }
        for (var i = 0; i < this.examtablegrad.length; i++) {
          for (var j = 0; j < this.examtablegrad[i].classInfo.length; j++) {
            if (this.examobjCs.classId === this.examtablegrad[i].classInfo[j].classId) {
              this.examtablegrad[i].classInfo[j].subjectList = examsubjectName.toString()
              this.examtablegrad[i].classInfo[j].subject = examsubjectid
            }
          }
        }
      }
      this.examsessios.examPopUpFlag = false
    },
    /**
     * 多选
     **/
    clsSelectedChange (val) {
      this.clsSelectedArr = val
      console.log('this=================', this.clsSelectedArr)
    },
    singleSelectedChange (val) {
      this.$refs.multipleTable.clearSelection()
      this.clsSelectedArr = val
    },
    /**
     * 批量编辑考试课程
     * 查找相同的课程并显示
     **/
    batchEditCourseClick () {
      let idLi = []
      let nameLi = []
      if (this.clsSelectedArr.length <= 0) {
        this.$message({
          showClose: true,
          message: '请复选至少一个班级',
          type: 'error'
        })
        return false
      }
      this.clsSelectedArr.forEach((val) => {
        idLi.push(val.classId)
        nameLi.push(val.className)
      })
      this.examObj.classList = nameLi.join(',')
      // let updata = idLi
      this.$http
        .post(
          '/api/v1/schoolexam/findSubjectInfo',
          idLi
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              console.log(res.data.data)
              let data = res.data.data
              let idList = []
              let subjectId = []
              data.forEach((val) => {
                subjectId = []
                val.subject.forEach((ele) => {
                  subjectId.push(ele.subjectId)
                })
                idList.push({
                  subjectId: subjectId
                })
              })
              console.log(idList)
              let defCList = idList.reduce((acc, cur) => {
                if (acc.length === 0) {
                  return cur.subjectId
                } else {
                  let ans = []
                  let curArr = cur.subjectId
                  let accArr = acc
                  curArr.forEach((val) => {
                    if (accArr.indexOf(val) > -1) {
                      ans.push(val)
                    }
                  })
                  return ans
                }
              }, [])
              let courseLi = []
              data[0].subject.forEach((val) => {
                if (defCList.indexOf(val.subjectId) > -1) {
                  courseLi.push({
                    subjectId: val.subjectId,
                    subjectName: val.subjectName
                  })
                }
              })
              this.examsessios.examPopUpFlag = true
              console.log(courseLi)
              this.examSubjectSelect = courseLi
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    examSubClose () {
      this.examsessios.examPopUpFlag = false
    },
    /**
    * 操作
    */
    examQuerySubject (item) {
      let idLi = []
      let nameLi = []
      idLi.push(item.classId)
      this.$http.post('/api/v1/schoolexam/findSubjectInfo', idLi)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
            // todo
              let data = res.data.data
              let idList = []
              let subjectId = []
              data.forEach((val) => {
                subjectId = []
                val.subject.forEach((ele) => {
                  subjectId.push(ele.subjectId)
                })
                idList.push({
                  subjectId: subjectId
                })
              })
              console.log(idList)
              let courseLi = []
              data[0].subject.forEach((val) => {
                courseLi.push({
                  subjectId: val.subjectId,
                  subjectName: val.subjectName
                })
              })
              this.examsessios.examPopUpFlag = true
              console.log(courseLi)
              this.examSubjectSelect = courseLi
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
    },

    /**
       * 提交考试参数
       */

    examSubForm () {
      if (this.examAddForm.examName === '') {
        this.$message.error('考试名称不能为空')
        return false
      }
      if (this.examAddForm.examTypeName === '') {
        this.$message.error('考试类别不能为空')
        return false
      }
      if (this.examAddForm.examTime === '') {
        this.$message.error('考试时间不能为空')
        return false
      }
      delete this.examAddForm.examObject

      console.log(this.examtablegrad)
      var examArrSess = []
      var examArrSessJson = {}
      for (var i = 0; i < this.examtablegrad.length; i++) {
        for (var j = 0; j < this.examtablegrad[i].classInfo.length; j++) {
          examArrSessJson = {}
          examArrSessJson.classId = this.examtablegrad[i].classInfo[j].classId
          examArrSessJson.subject = this.examtablegrad[i].classInfo[j].subject
          examArrSess.push(examArrSessJson)
          if (this.examtablegrad[i].classInfo[j].subject.length >= 1) {

          }
        }
      }
      this.examAddForm.params = examArrSess
      console.log(examArrSess)
      this.$http.post('/api/v1/schoolexam/addExamInfo', this.examAddForm)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$notify({
                title: '提示信息',
                message: '操作成功',
                type: 'success'
              })
              this.$router.push({path: '/achievement'})
            } else {
              this.$notify({
                title: '提示信息',
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    filterTag (value, row) {
      // console.log(row.classType)
      this.clearSelection()
      return row.classType === value
    },
    // 得到类型的集合来筛选
    getClsTypeNames (classInfo) {
      var ans = []
      classInfo.forEach((val) => {
        ans.push(val.classType)
      })
      ans = Array.from(new Set(ans))
      let arr = []
      ans.forEach((val) => {
        arr.push({
          text: val,
          value: val
        })
      })
      return arr
    },
    clearSelection () {
      this.$refs.multipleTable.clearSelection()
    }

  },
  computed: {
    getExamCourse () {
      return this.examObj.subjectList.toString()
    },
    getClsSlctData () {
      console.log(this.examtablegrad)
      var ans = []
      this.examtablegrad.forEach((val) => {
        ans.push({
          clsVagueShVal: ''
        })
      })
      console.log('新增加考试', ans)
      return ans
    },
    examClsData () {
      var ans = []
      this.examtablegrad.forEach((val) => {
        ans.push(val.classInfo)
      })
      return ans
    }
  }
}
</script>

