      bottom: 0;
      z-index: 10;
      /*display: none;*/
      .examMidfy{
        width: 400px;
        height: 350px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -175px -200px;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 6px #c4c8ca;
        .examMidfyCont{
          padding: 20px;
        }
      }
    }
    .examMidfyBtnBox{
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding-bottom: 30px;
      .el-button{
        width: 100px;
        height: 30px;
        background-color: #6bc08b;
        border-radius: 25px;
        color: #fff;
        font-size: 14px;
        cursor: pointer;
        padding: 0
      }

    }
    .el-select__tags{
      overflow: hidden !important;
      text-overflow: ellipsis !important;
      white-space: nowrap !important;
    }
    .el-select{
      width: 100%;
      height: 40px;
    }
    .el-select>.el-input{
      height: 40px;
    }
    .el-select .el-input.is-focus .el-input__inner{
      height: 40px !important;
    }
    .footer{
      flex: 0;
    }
    .examGradeCont {
      .el-icon-arrow-down:before {
        content: "\E603";
        color: #3e3939;
        font-size: 16px;
      }
    }

  }
</style>



