    .privateinfo {
      /*height: 643px;*/
      width: 620px;
      border: 1px solid #eee;
      /*margin: 27px auto;*/
    }
    .privateinfo label {
      width: 120px !important;
    }
    .privateinfo > h1 {
      width: 80px;
      background: blue;
      height: 20px;
      font-size: 12px;
      text-align: center;
      line-height: 20px;
      color: white;
