    }

    .addstufooter {
      position: absolute;
      bottom: 30px;
      width: 670px;
    }
    .dialog-footer > button {
      border: 1px solid #6bc08b;
      font-size: 14px;
      border-radius: 20px;
      height: 32px;
      line-height: 32px;
