    }
    .departmentPopImgbox {
      background-color: #fafcfc;
      padding: 10px 15px;
      p{
        width: 80px;
        height: 25px;
        border-radius: 5px;
        border: 1px solid #6bc08b;
        display: block;
        background-color: transparent;
        color: #6bc08b;
        font-size: 12px;
        margin: auto;
        cursor: pointer;
      }
    }
    .departmentPopImg {
      width: 140px;
      height: 140px;
      line-height: 140px;
      text-align: center;
      font-size: 28px;
      border: 1px dashed #d9d9d9;
      border-radius: 50%;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      margin: 15px auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }

    }
    .avatar-uploader {
      text-align: center !important;
      button: {
        border: none !important;
      }
    }
    .el-cascader{
      line-height: 25px;
    }
