<template>
  <div class="login-box">
    <div class="login-conter">
      <ul>
        <li>
          <img src="@/assets/header/header-icon.png" alt="">
        </li>
        <li>
          智慧校园系统
        </li>
        <li>|</li>
        <li>
          泸州老窖天府中学
        </li>
      </ul>
    </div>
    <div class="login-conterBox">
      <div class="login-imgBox">
        <img src="@/assets/login/login.png" alt="" class="img-my">
      </div>
      <div class="login-form">
        <div class="login-tit">
          <img src="@/assets/login/logintit.png" alt="" class="img-my">
        </div>
        <el-form :model="login" status-icon :rules="loginRules" ref="login" label-width="100px" class="demo-ruleForm">
          <el-form-item prop="adminName">
            <el-input type="text" v-model="login.adminName" auto-complete="off" placeholder="用户名"></el-input>
          </el-form-item>
          <el-form-item prop="adminPass">
            <el-input type="password" v-model="login.adminPass" auto-complete="on" placeholder="密码"  @keyup.enter.native="submitForm('login')"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
            type="primary"
            @click="submitForm('login')"

            >
            登录
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
// v-loading.fullscreen.lock="fullscreenLoading"
//       element-loading-text="拼命加载中"
//       element-loading-spinner="el-icon-loading"
//       element-loading-background="rgba(0, 0, 0, 0.8)"
export default {
  self: this,
  name: 'login',
  data: function () {
    return {
      // fullscreenLoading: false,
      login: {
        adminName: '',
        adminPass: ''
      },
      loginRules: {
        adminName: [
          { required: true, message: '用户名不能为空', trigger: 'blur' }
          // { min: 2, max: 10, message: '长度在 2 到 16 个字符', trigger: 'blur' }
        ],
        adminPass: [
          { required: true, message: '密码不能为空', trigger: 'blur' }
        ]
      },
      adminName: '',
      adminPass: '',
      adminType: null,
      adminId: null,
      info: {}
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.doLogin()
        } else {
          return false
        }
      })
    },
    doLogin () {
      // this.fullscreenLoading = true
      var userData = {
        adminName: this.login.adminName,
        adminPass: this.login.adminPass
      }

      this.$http.post('/api/v1/admin/ifLogin', this.$qs.stringify(userData))
        .then(res => {
          // console.log(res)
          console.log('res.data', res)
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.adminType = res.data.data.adminRoleType

              this.adminId = res.data.data.adminId
              this.initUserAuthority()
            } else {
              // this.fullscreenLoading = false

              this.$notify({
                title: '提示信息',
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    initUserInfo () {
      let url = ''
      if (this.adminType < 4) {
        url = '/api/v1/personnelManagement/selectAdminInfo'
      } else {
        url = '/api/v1/studentManagement/selectStudentOsaas'
      }
      this.$http
        .get(url, {params: {adminId: this.adminId}})
        .then(res2 => {
          if (res2.status === 200) {
            if (res2.data.code === 1) {
              this.userInfo = res2.data.data
              console.log('userInfo', this.userInfo)
              this.info = {
                fullName: this.userInfo.fullName,
                photoUrl: this.userInfo.photoUrl ? this.userInfo.photoUrl : (this.userInfo.adminHead ? this.userInfo.adminHead : 'static/img/gly.png'),
                loginTime: new Date(),
                roleId: this.adminType
              }
              console.log('info', this.info)
              window.localStorage.setItem('userInfo', JSON.stringify(this.info))
              // this.fullscreenLoading = false
              this.$router.push({path: '/index'})
            } else {
              // this.fullscreenLoading = false
              this.$notify({
                title: '提示信息',
                message: res2.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    initUserAuthority () {
      let authUrl = '/api/v1/admin/loginSuccess'
      this.$http
        .get(authUrl)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.authList = res.data.data.catas
              console.log('authority', this.authList)

              this.initUserInfo()
            } else {
              // this.fullscreenLoading = false

              this.$notify({
                title: 'authority',
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style  lang="scss">
  .login-box{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: -webkit-linear-gradient(right,#b9b6ff,#7d8aff);
    background: -o-linear-gradient(right,#b9b6ff,#7d8aff);
    background: -moz-linear-gradient(right,#b9b6ff,#7d8aff);
    background: -mos-linear-gradient(right,#b9b6ff,#7d8aff);
    background: linear-gradient(right,#b9b6ff,#7d8aff);
    display: flex;
    display: -webkit-flex;
    min-width: 960px;

    .el-form-item__content {
      margin-left:0 !important
    }
    .el-input__inner {
      background: #f2f2f2;
      height: 30px;
    }
    .el-button {
      width: 200px;
      background: #6bc08b;
      border-radius: 15px;
      padding: 8px 20px;
      margin-top: 20px;
      border: 1px solid transparent;

    }
    .el-button--primary:focus, .el-button--primary:hover {
      background: #6bc0bb;
    }
    .el-input.is-active .el-input__inner, .el-input__inner:focus {
      border-color: #409eff;
      outline: 0;
}
  }
.login-conter{
  width: 100%;
  background-color: #477e66;
  height: 63px;
  line-height: 63px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}
.login-conter ul{
  width: 1000px;
  margin: auto;
}
.login-conter ul li{
  float: left;
  font-size: 14px;
  color: #ffffff;
  height:  63px ;
}
.login-conter ul li:first-child{
   display: flex;
   display: -webkit-flex;
   padding-left: 20px;
   padding-right: 15px;
 }
.login-conter ul li:nth-child(3){
  padding-left: 10px;
  padding-right: 10px;
}
.login-conter ul li img{
  display: block;
  margin: auto;
}
  .login-conterBox{
    width: 1000px;
    height: 440px;
    margin: auto;

  }
.login-imgBox{
  float: left;
  width: 520px;
  height: 430px;

}
  .login-form{
    float: right;
    margin-right: 100px;
    width: 300px;
    height: 370px;
    background-color: #ffffff;
    border-radius: 5px;
    margin-top: 30px;
    padding: 40px 50px;
    box-sizing: border-box;
  }
.login-tit{
  margin-bottom: 30px;
}
</style>



