  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      }
    }
  },
  methods: {
    onSubmit () {
      console.log('submit!')
    }
  }
}

</script>

<style lang="scss">
body{
  background-color: #eeeeee;
}
.schoolInforBox{
  .schoolInforContionr{
    width: 700px;
    padding: 30px 120px;
    margin:30px auto;
    border-radius: 10px;
    box-shadow: 0px 0px 6px #c4c8ca;
    background-color: #ffffff;
  }
/*.bottom{*/
  /*position: fixed;*/
  /*right: 0;*/
  /*left: 0;*/
  /*bottom: 0;*/
/*}*/
.schoolInTit{
  margin-bottom: 30px;
  img{
    margin: auto;
  }
}
  .schoolInTitP{
    width: 117px;
    line-height: 35px;
    height: 35px;
    color: #fff;
    font-size: 14px;
    margin: 0 0 10px 0;
    border-radius: 4px;
  }
}

</style>



