import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/login'
import Index from '@/views/index/index'
import Achievement from '@/views/achievement/achievement'
import examList from '@/views/exam/examList'
import Personnel from '@/views/department/personnel'
import Exam from '@/views/achievement/exam'


import schoolInformation from '@/views/schoolInformation/schoolInformation'

import Classinfo from '@/views/classInfo/classinfo'
import Details from '@/views/classInfo/classdetails'
import Popover from '@/views/classInfo/classtylepopover'
import StdSecEdit from '@/views/classInfo/stdseceditpopover'
import LevelPopover from '@/views/classInfo/levelpopover'

import Test from '@/components/test'
import Test2 from '@/components/test2';

import Authority from '@/views/authority/authority'
import Department from '@/views/department/department'

import SchoolInfo from '@/views/schoolInformation/schoolInformation'

import StdSecPopover from '@/views/classInfo/classstdsecaddpopover'
import ClsExportPopover from '@/views/classInfo/classexportpopover'
import TeacherDeploy from "@/views/teacherDeploy/teacherDeploy"



Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: "/",
      name: "login",
      component: Login
    },
    {
      path: "/index",
      name: "index",
      meta: {
        requireAuth: true
      },
      component: Index
    },
    {
      path: "/achievement",
      name: "achievement",
      component: Achievement,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/classinfo",
      name: "classinfo",
      component: Classinfo,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/teacherdeploy",
      name: "classinfo1",
      component: TeacherDeploy,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/examList",
      name: "examList",
      component: examList,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/exam",
      name: "exam",
      component: Exam,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/details",
      name: "details",
      component: Details,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/popover",
      name: "popover",
      component: Popover,
      meta: {
        requireAuth: true
      }
    },

    {
      path: "/levelpopover",
      name: "levelpopover",
      component: LevelPopover,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/clsexport",
      name: "clsexportpopver",
      component: ClsExportPopover,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/authority",
      name: "authority",
      component: Authority,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/department",
      name: "department",
      component: Department,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/personnel",
      name: "personnel",
      component: Personnel,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/schoolinfo",
      name: "schoolinfo",
      component: SchoolInfo,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/stdsec",
      name: "stdsec",
      component: StdSecPopover,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/schoolInformation",
      name: "schoolInformation",
      component: schoolInformation,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "/test",
      name: "test",
      component: Test
    },
    {
      path: "/test2",
      name: "test2",
      component: Test2
    },
    {
      path: "/stdsecedit",
      component: StdSecEdit,
      meta: {
        requireAuth: true
      }
    },
    {
      path: "*",
      redirect: "/index",
      meta: {
        requireAuth: true
      }
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.matched.some(res => res.meta.requireAuth)) {
    // // 判断是否需要登录权限
    // if (userInfo) {
    //   // 判断是否登录
    //   let lifeTime = userInfo.loginTime

    //   let nowTime = (new Date()).getTime(); // 当前时间的时间戳
    //   if (nowTime < lifeTime) {
    //    reSetLoginTime(dateCycle)
    //     next();
    //   } else {
    //   //调用注册接口 实现前端控制超时登录功能
    //     Message({
    //       showClose: true,
    //       message: "登录状态信息过期,请重新登录",
    //       type: "error"
    //     });
    //     next({
    //       path: "/login"
    //     });
    //   }
    // } else {
    // 没登录则跳转到登录界面
    if(window.localStorage.getItem('userInfo')) {
      next()
    } else{
      next({
      path: '/'
    })
    }
    
  } else {
    next()
  }
})

export default router

