   <template>
    <div class="classstyle">
      <div class="classtyleBox" id="classtyleBox">
        </div>
          <div class="cstyle" id="cstyle">
            <div>
              <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
              <span>班级类型</span>
               <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
            </div>
            <div class="cstylemain">
              <el-row class="cstyle-first">
                <el-col :span="24">
                  <el-button type="primary" @click="addComShow">新增</el-button>
                </el-col>
              </el-row >
              <el-row v-show="isAddShow" class="cstyle-scond">
                <el-col :span="12">
                  <input type="text" v-model="itemVal">
                </el-col>
                <el-col :span="12">
                  <el-button @click="addItem" type="primary" >确定</el-button>
                </el-col>
              </el-row>
              <el-row class="cstyle-three">
                <el-col :span="12">
                  <span>班级类型名称</span>
                </el-col>
                <el-col :span="12">
                </el-col>
              </el-row>
              <div class="cstyle-scroll">
                <ul class="cls-list">
                    <li class="cstyle-four">
                      <el-row>
                        <el-col :span="12">
                          <input type="text" class="classtyleBoxinput" v-model="clsdf[0]"  readonly>
                        </el-col>
                        <el-col :span="12">
                        </el-col>
                      </el-row>
                    </li>
                    <li class="cstyle-four">
                      <el-row>
                        <el-col :span="12">
                          <input type="text" class="classtyleBoxinput" v-model="clsdf[1]" readonly>
                        </el-col>
                        <el-col :span="12">
                        </el-col>
                      </el-row>
                    </li><li class="cstyle-four">
                      <el-row>
                        <el-col :span="12">
                          <input type="text" class="classtyleBoxinput" v-model="clsdf[2]" readonly>
                        </el-col>
                        <el-col :span="12">
                        </el-col>
                      </el-row>
                    </li>
                  <li v-for="clsItem in clsItems" :key="clsItem.classTypeId">
                  <el-row class="cstyle-four">
                    <el-col :span="12">
                      <input type="text" v-model="clsItem.classtypeName" class="classtyleBoxinput" :id="clsItem.classTypeId" readonly>
                    </el-col>
                    <el-col :span="12">
                      <el-button type="warning" @click="changeEditable(clsItem.classTypeId,$event)" >修改</el-button>
                      <el-button type="danger" @click="delClsType(clsItem.classTypeId)">删除</el-button>
                    </el-col>
                  </el-row>
                  </li>
                </ul>
              </div>
                <div class="cstylefooter">
                  <div slot="footer" class="dialog-footer">
                      <button class="save" @click="addClsType">保 存</button>
                      <button class="exit" @click="hideDialog">取 消</button>
                  </div>
                </div>
            </div>
          </div>
        </div>
   </template>
<script>
export default {
  name: 'levelclasstyleBox',
  props: {},

  data () {
    return {
      isAddShow: false,
      itemVal: '',
      clsItems: [],
      liLen: 0,
      clsdf: [],
      preAddLi: [],
      testarr: 'nihao',
      isfocus: false,
      curId: null,
      addData: {
        clstype: this.itemVal
      }
    }
  },
  methods: {
    showData () {
      console.log('preAddLi', this.preAddLi)
    },
    hideDialog () {
      this.$emit('hideDialog')
    },
    addComShow () {
      this.isAddShow = !this.isAddShow
      this.itemVal = ''
    },

    addItem () {
      if (this.itemVal !== '') {
        let item = {
          'classtypeName': this.itemVal,
          'classTypeId': '$' + this.liLen,
          'isEdit': '1'
        }
        this.preAddLi.push(item)
        this.clsItems.push(item)
        console.log('perAddLi', this.preAddLi)
        console.log('clsItems', this.clsItems)
      }
    },
    /**
     * 如果数据perAddLi 不为空
     * 添加数据到数据库
     */
    addClsType () {
      if (this.preAddLi.length !== 0) {
        let updata = []
        for (let clstp of this.preAddLi) {
          updata.push(clstp.classtypeName)
        }
        this.preAddLi = []

        this.$http
          .post(
            '/api/v1/schoolclass/addClassType',
            updata
          )
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$message({
                  showClose: true,
                  message: res.data.msg,
                  type: 'success'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    /**
     *  判断是否是假数据，
     * 如果是假数据就直接修改，如果是真数据在提交数据库成功后修改
     */
    upClsType (id) {
      console.log('typeof', typeof id)
      let itemId = document.getElementById(id)

      if (typeof id === 'string') {
        for (let clstp of this.preAddLi) {
          if (clstp.classTypeId === id) {
            clstp.classtypeName = itemId.value
          }
        }
        console.log('perAddLi', this.preAddLi)
      }
      if (typeof id === 'number') {
        let updata = {
          'classTypeId': id,
          'classTypeName': itemId.value
        }
        this.$http
          .put(
            '/api/v1/schoolclass/updateClassType',
            this.$qs.stringify(updata)
          )
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                console.log('upClsType')
                this.$message({
                //  饿了么的消息弹窗组件,类似toast
                  showClose: true,
                  message: res.data.msg,
                  type: 'success'
                })
              } else {
                console.log(res.data.msg)
                this.$message({
                //  饿了么的消息弹窗组件,类似toast
                  showClose: true,
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    changeEditable (id, event) {
      let itemId = document.getElementById(id)
      console.log('itemId', itemId.value)
      console.log('itemId', itemId)

      if (!this.isfocus) {
        itemId.focus()
        itemId.removeAttribute('readonly')
        this.curId = id
        event.target.innerHTML = '保存'
        this.isfocus = !this.isfocus
        itemId.style.backgroundColor = '#666'
        itemId.style.color = 'white'
      } else {
        if (this.curId !== id) {
          this.$message({
            //  饿了么的消息弹窗组件,类似toast
            showClose: true,
            message: '请先保存上一次更改',
            type: 'error'
          })
          return
        }
        itemId.blur()
        this.curId = null
        itemId.setAttribute('readonly', 'true')
        itemId.style.backgroundColor = 'white'
        itemId.style.color = '#999'
        event.target.innerHTML = '修改'
        this.isfocus = !this.isfocus
        // console.log('changeEditable', this.clsItems)
        this.upClsType(id)
      }
    },
    /**
     * 根据id删除数据
     * 1.新增数据删除 更新 preAddLi和clsItems
     * 2.数据库数据删除 异步执行成功 更新clsItems
     */
    delClsType (id) {
      // 异步调用 利用ID删除
      // let itemId = document.getElementById(id)
      let index = 0
      if (typeof id === 'string') {
        for (let clstp of this.preAddLi) {
          if (clstp.classTypeId === id) {
            this.preAddLi.splice(index, 1)
            index++
          }
        }
        index = 0
        for (let clstp of this.clsItems) {
          if (clstp.classTypeId === id) {
            this.clsItems.splice(index, 1)
            index++
          }
          console.log('delete', this.clsItems)
          this.$message({
            //  饿了么的消息弹窗组件,类似toast
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        }
      }

      if (typeof id === 'number') {
        console.log('delete id', this.$qs.stringify({ classTypeId: id }))
        this.$http.delete('/api/v1/schoolclass/updateClassTypeDel',
          {
            params: {
              classTypeId: id
            }
          }
        ).then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.msg)
              index = 0
              for (let clstp of this.clsItems) {
                if (clstp.classTypeId === id) {
                  this.clsItems.splice(index, 1)
                  index++
                }
              }
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
          .catch(err => {
            console.log(err)
          })
      }
    },
    /**
     * 查询班级类型
     */
    initClstype () {
      this.$http.get('/api/v1/schoolclass/findType')
        .then(res => {
          if (res.status === 200) {
            console.log(res.data)
            if (res.data.code === 1) {
              console.log('clsItems', res.data.data)
              this.clsItems = res.data.data
              this.liLen = this.clsItems.length
              let isEdit = ''
              let y = 0
              for (let i = 0, len = this.clsItems.length; i < len; i++) {
                isEdit = this.clsItems[i].isEdit
                if (isEdit === '0') {
                  this.clsdf[y++] = this.clsItems[i].classtypeName
                  this.clsItems.splice(i, 1)
                  i--
                }
              }
              console.log('defVal', this.clsItems)
            } else {
              console.log(res.data.msg)
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  },
  mounted () {
    this.initClstype()
  }
}
</script>
<style lang="scss">
.classstyle {
  .classtyleBox {
    position: fixed;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    //  height: 2000px;
    min-width: 960px;
    z-index: 100;
  }
  .cstyle {
    background: white;
    position: absolute;
    width: 420px;
    height: 435px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 200;
  }
  .cstyle > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .cstyle > div:nth-of-type(1) > img {
    vertical-align: middle;
  }

  .cstylemain {
    color: #c3c3c3;
  }
  .cstyle-first,
  .cstyle-scond,
  .cstyle-three,
  .cstyle-four {
    text-align: left;
    margin-top :10px;
    margin-left: 20px;

  }
  .cstyle-first {
    margin-top :20px;
    margin-left: 20px;
  }
  .cstyle-first button {
    padding: 7px 50px;
  }
  .cstyle-scond,
  .cstyle-four {
    // font-size: 14px;
     input {
      font-size: 16px;
      line-height: 26px;
      padding: 0 5px;
      width: 190px;
      border:1px solid #6bc08b;
      color: #999

    }
  }
  .cstyle-scond button,
  .cstyle-four button{
    padding: 7px 25px;

  }
  .cstylefooter {
    position: absolute;
    bottom: 20px;
    width: 420px;
    margin: 0 auto;
  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }
  .cstyle-scroll {
    height: 200px;
    border:1px solid rgb(184, 171, 171);
    overflow: auto;
    margin-top: 10px;
  }

}
</style>



