<template>
  <div class="authorityBox">
    <v-header></v-header>
    <el-tabs type="border-card" class="class-tabs">
      <el-tab-pane label="角色管理">
        <div class="tabs">
          <div class="auth-search">
            <h1>查询条件</h1>
            <div>
              <el-form :inline="true" class="demo-form-inline">
                <el-form-item label="选择角色:">

                  <el-select v-model="authRoleSlct" placeholder="请选择">
                    <el-option
                      v-for="item in authRoles"
                      :key="item.id"
                      :label="item.roleName"
                      :value="item.id">
                    </el-option>
                    <el-option label="全部..." value="" key="全部..."></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="输入查找内容:">
                  <el-input v-model="authVagueVal" placeholder="模糊名称或帐号"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button @click="authShClick">搜索</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="auth-list">
            <h1>列表查询</h1>
              <el-table
              ref="multipleTable"
              :data="roleData"
              style="width: 880px; margin: auto; margin-top: 30px"
              border
              @selection-change="handleSelectionChange">
                <el-table-column
                type="index"
                width="55"
                label="序列">
                </el-table-column>

                <el-table-column
                  label="名称"
                  width="120">
                  <template slot-scope="scope">{{ scope.row.sFullName?scope.row.sFullName: scope.row.tFullName }}</template>
                </el-table-column>
                <el-table-column
                  label="账号"
                >
                  <template slot-scope="scope">{{ scope.row.adminName }}</template>
                </el-table-column>
                <el-table-column
                  label="角色"
                  >
                  <template slot-scope="scope">{{ scope.row.roleName }}</template>
                </el-table-column>
                <el-table-column
                  label="状态"
                  >
                  <template slot-scope="scope">{{ scope.row.adminState ==='1'? '激活': '停用' }}</template>
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="操作"
                  >
                  <template slot-scope="scope">
                    <i class="el-icon-edit-outline" style="text-align:center" @click="editRoleClick(scope.$index, scope.row)"></i>
                  </template>
                </el-table-column>
            </el-table>
             <el-pagination
                @current-change="authHandleCurrentChange"
                :current-page="authCurPage"
                :page-size="10"
                layout="total, prev, pager, next, jumper"
                :total="authPageTotal">
              </el-pagination>

          </div>
        </div>
      </el-tab-pane>

      <el-tab-pane label="角色权限">
        <div class="tabs">
          <div class="rolemanage clearfix">
            <div class="rolemanage-left rolemanage-style">
              <h1>角色管理</h1>
              <el-table
              :data="authRoles"
              border
              ref="multipleTable"
              style="width: 295px;  font-size: 14px;overflow:auto;margin: 40px auto">
               <el-table-column
                    type="index"
                    label="序号"
                    width="50">
                  </el-table-column>
                  <el-table-column
                    prop="roleName"
                    label="角色管理"
                    width="148">
                  </el-table-column>
                  <el-table-column
                    prop="manage"
                    label="操作"
                    width="95">
                    <template slot-scope="scope">
                    <i class="el-icon-edit-outline" @click="roleMngClick(scope.$index, scope.row)"></i>
                    </template>
                  </el-table-column>
              </el-table>
            </div>
            <div class="rolemanage-right rolemanage-style">
            <h1>设置权限</h1>
            <div class="scollbar">
              <el-tree
                :data="authData"
                show-checkbox
                default-expand-all
                node-key="id"
                ref="tree"
                highlight-current
                :default-checked-keys="defaultSlctAuth"
                :props="defaultProps">
              </el-tree>

            </div>
              <div class="role-footer" v-show="authBtnshow">
                <button @click="updateModularClick" >修改角色下的权限</button>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <auth-edit-popover @hideDialog="authhide"  @childUpdateTable="initauthTable" v-show="authvisible" :authRoles=authRoles :parentData=authTableData ></auth-edit-popover>
  </div>
</template>
<script>
import vFooter from '@/components/bottom'
import vHeader from '@/components/header'
import AuthEditPopover from '@/views/authority/autheditpopover'

export default {
  name: 'authority',
  components: {
    vHeader,
    vFooter,
    AuthEditPopover
  },
  data () {
    return {
      roleData: [],
      multipleSelection: null,
      authRoleSlct: '',
      authCurPage: 1,
      authPageTotal: null,
      authVagueVal: '',
      authvisible: false,
      authTableData: '',
      authRoles: [],
      isUpdateRole: false,
      authBtnshow: false,
      authData: [],
      defaultProps: {
        children: 'children',
        label: 'pageName'
      },
      defaultSlctAuth: [],
      selectedRoleId: null,
      parentmenu: []

    }
  },

  methods: {

    authshow () {
      this.authvisible = true
    },
    authhide () {
      this.authvisible = false
    },
    /** @augments curpage
     *  this.authCurPage
     *   分页
     */
    authHandleCurrentChange (curPage) {
      console.log('authCurPage', curPage)
      this.authCurPage = curPage
      this.initauthTable()
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    /**
     *  更新 roleAdmin 表
     * isUpdateRole 判断是点击查询更新，还是分页跳转
     */
    initauthTable () {
      if (this.isUpdateRole) {
        this.authCurPage = 1
        this.isUpdateRole = false
      }
      let updata = {
        currentPage: this.authCurPage,
        pageSize: 10,
        parameters: {
          'roleId': this.authRoleSlct,
          'adminName': this.authVagueVal
          // 'fullName': this.authVagueVal
        }
      }
      console.log('更新表信息', updata)
      this.$http
        .post(
          '/api/v1/admin/findAllAdmin',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.roleData = res.data.data.content
              this.authPageTotal = res.data.data.recordTotal
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**
     *  初始化下拉菜单
     *  authRoles
     */
    initRoleDownMenu () {
      this.$http
        .get(
          '/api/v1/admin/findAllRole'
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.authRoles = res.data.data
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    authShClick () {
      this.isUpdateRole = true
      this.initauthTable()
    },
    editRoleClick (index, row) {
      this.authTableData = row
      this.authshow()
    },

    // ---------------------角色权限------------------
    roleMngClick (index, row) {
      console.log('roleid', row.id)
      this.selectedRoleId = row.id
      this.$http
        .get(
          '/api/v1/admin/findRoleModulars',
          {
            params: {
              roleId: row.id
            }
          }
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.authData = [res.data.data.catas]
              // this.authData.push(res.data.data.catas)
              this.defaultSlctAuth = res.data.data.modular
              this.parentmenu = [1, 2, 3, 40, 44, 46, 59, 81, 88, 91, 99, 112,127,130,133,136,150]
              this.defaultSlctAuth = this.defaultSlctAuth.filter(val => {
                return this.parentmenu.indexOf(val) === -1
              })
              console.log('this.defaultSlctAuth', this.defaultSlctAuth, res.data.data)
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
      this.authBtnshow = true
    },
    updateModularClick () {
      console.log(this.$refs.tree.getCheckedKeys())

      let updata = {
        roleId: this.selectedRoleId,
        modulars: this.$refs.tree.getCheckedKeys()
      }
      this.$http
        .put(
          '/api/v1/admin/updateRoleModular',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }

  }, /* method end */
  mounted () {
    this.initauthTable()
    this.initRoleDownMenu()
  }
}
</script>

<style lang="scss">
.authorityBox{
 width: 100%;
  min-width: 960px;
  .el-tabs__item {
  height: 56px;
  line-height: 56px;
  width: 150px;
}
.class-tabs {
  width: 960px;
  margin: 0 auto;
  color: #afafaf;
  font-size: 12px;
}
.el-tabs--border-card > .el-tabs__header .el-tabs__item.is-active {
  color: #666666;
  font-size: 16px;
  font-weight: bold;
}
.el-tabs--border-card > .el-tabs__header .el-tabs__item:hover {
  color: #afafaf;
}
.tabs {
  width: 960px !important;
  margin-left: -16px;
  color: #999;
}
.auth-signin {
  height: 75px;
  border-top: 1px solid #eee;
  font-size: 12px;
  text-align: left;
  line-height: 75px;
}
.auth-search {
  height: 124px;
  border-top: 1px solid #eee;
  font-size: 12px;
  text-align: left;

}
.auth-signin input,
.auth-signin select,
.auth-search input,
.auth-search select
 {
  vertical-align: middle;
  width: 138px;
  height: 30px;
  color: #666;
  border:1px solid #c6c6c6;
  margin-left: 6px;
}
.auth-list {
  border-top: 1px solid #eee;
  font-size: 12px;
  text-align: left;
 i {
   font-size: 24px;
   cursor: pointer;
 }
  .el-table .cell {
    text-align: center;
}
}
.siginin>span {
  margin-left: 30px;
}
.siginin>span:first-child {
  margin-left: 30px;

}

.siginin>button {
  width: 88px;
  height: 28px;
  border: 1px solid #6bc08b;
  background: #fabc05;
  box-sizing: border-box;
  border-radius: 5px;
  margin-left: 30px;
  color: white;
}
.siginin>button:hover {
  color:#fabc05;
  background: white;
}
.auth-search>h1,
.auth-list>h1{
  color: white;
  background: #6cc08c;
  width:62px;
  height:18px;
  font-size: 12px;
  margin-top:12px;
  margin-left: 30px;
  text-align: center;
}
.auth-search>div {
  margin-left: 30px;
  margin-top:20px;
  height: 50px;
}
.auth-search>div>select,
.auth-search>div>input
{
  margin-right: 30px;
}
.auth-search {
  .el-button {
    padding: 0
  }
}

.auth-search button {
  height: 32px;
  background: white;
  color: #6bc08b;
  width: 112px;
}
.auth-search button:hover {
  background:#6bc08b;
  color:white;

}
.el-pagination {
  text-align: center;
  margin-top: 40px;
}
.rolemanage {
  width: 900px;
  margin: 0 auto;
  .el-checkbox {
    margin-left: 20px;
 }
i {
   font-size: 20px;
   cursor: pointer;
 }
  .el-table .cell {
    text-align: center;
}
}

.rolemanage-style {
  // height: 500px;
  float: left;
   background: #fafcfc;

}
.rolemanage-left {
  width: 400px;
  margin-right: 40px;

}
.rolemanage-right {
  text-align: left;
  width: 460px;
  overflow-x: scroll;
}

.rolemanage-style>h1 {
  background: #c49df8;
  color: white;
  font-size: 12px;
  width: 62px;
  height: 20px;
  margin-bottom: 40px;
}
.clearfix::after{
  content: '.';
  display: block;
  line-height: 0;
  clear: both;
}
.scollbar {
  width: 600px;
  min-height: 268px;
  overflow: auto;
  font-size: 16px !important;

  .el-checkbox__label {
    font-size: 16px;
  }
  .el-tree{
    background: #fafcfc;
  }

}
.scollbar label{
 display: block;

}
// .el-form-item__content  {
//   line-height: 1.5;
//   font-size: 14px !important;
// }
.role-footer {
  margin-top:20px;
  width: 460px;
  text-align: center;
}
.role-footer>button {
  border:1px solid #6bc08b;
  font-size: 14px;
  border-radius:5px;
  height: 32px;
  line-height: 32px;
  padding:0px;
  width: 200px;
  color:#6bc08b;
  background:white;
}
.role-footer>button:hover {
  background: #6bc08b;
  color:white;
}
 .el-tabs--border-card{
  background: #fff;
  border: none;
  -webkit-box-shadow: none;
  box-shadow:none;
  margin-top:10px;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active{
  border-bottom: 57px solid #fff;
  border-left: 31px solid transparent;
  border-right: 31px solid transparent;
  height: 0;
  /*width: 137px;*/
  color: #666666;
  font-size: 14px;
  background-color: transparent;

}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:first-child.is-active{
  border-left:none;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item{
  border: none;
  -webkit-transition: all 0s cubic-bezier(.645,.045,.355,1);
  transition: all 0s cubic-bezier(.645,.045,.355,1);
}
.el-tabs--border-card>.el-tabs__header{
  background-color: #f2f2f2;
  border-bottom: none;

}
.el-tabs__nav-scroll{
  line-height: 56px;
  border-top: 1px solid #e5e5e5;
  /*border-right: 1px solid #e5e5e5;*/
  /*border-left: 1px solid #e5e5e5;*/
}
.el-tabs--top.el-tabs--border-card .el-tabs__item:nth-child(2){
  padding: 0;
}
.el-tabs__item{
  line-height: 56px;
  width: 139px;
  text-align: center;
  padding: 0;
  height: 56px;
  font-size: 12px;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item:hover{
  color: #6cc08c;
}
.el-tabs--border-card>.el-tabs__header .el-tabs__item.is-active:hover{
  color: #666666;
}
.el-tabs__content{
  padding: 10px 20px;
}
}

</style>



