      // 历次考试总分排名趋势图
      inAllPastTotalRankShow: false,
      inAllPastTotalRankVal: null,
      inAllPostTotalRankXAxis: [],
      inAllPastPersonalTotalRankData: [],
      inAllPastClsTotalRankData: [],
      inAllPastClsTotalRankChart: null,
      // 平均分对比折线图
