  methods: {

    // 选择考试名称查询学段等信息
    examSlctClick () {
      this.gradeData = ''
      this.gradeDatas = []

      this.thetimeData = ''
      this.thetimeDatas = []

      this.classTypeData = ''
      this.classTypeDatas = []

      this.classLevelData = ''
      this.classLevelDatas = []

      this.classData = []
      this.classDatas = []

      this.subjectData = []
      this.subjectDatas = []

      this.studentNum = ''

      this.tableData = []

      this.$http.get('/api/v1/schoolexam/findAllSchoolExamThetimeBySchoolExamParent',
        {
          params: {
            examParentId: this.examName
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.gradeDatas = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据学段选择届次
    gradeSlctClick () {
      this.thetimeData = ''
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.gradeDatas.length; i++) {
        if (this.gradeData === this.gradeDatas[i].classPeriodId) {
          this.thetimeDatas = this.gradeDatas[i].thetimes
        }
      }
    },
    // 根据届次选择班级类型
    classTypeSlctClick () {
      this.classTypeData = ''
      this.classLevelData = ''

      for (let i = 0; i < this.thetimeDatas.length; i++) {
        if (this.thetimeData === this.thetimeDatas[i].thetime) {
          this.classTypeDatas = this.thetimeDatas[i].classTypes
        }
      }
    },
    // 根据班级类型确定班级层次
    classLevelSlctClick () {
      this.classLevelData = ''

      for (let i = 0; i < this.classTypeDatas.length; i++) {
        if (this.classTypeData === this.classTypeDatas[i].classTypeId) {
          this.classLevelDatas = this.classTypeDatas[i].classLevels
        }
      }
      // 更新班级
      this.classSlctClick()
    },
    // 根据班级层次选择班级
    classSlctClick () {
      this.$http.get('/api/v1/schoolexam/findAllSchoolExamClassAndSubject',
        {
          params: {
            examParentId: this.examName,
            classPeriodId: this.gradeData,
            thetime: this.thetimeData,
            classTypeId: this.classTypeData
          }
        }
      )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.classDatas = res.data.data[0].examClassNames
              this.classDatas.unshift({
                classId: 0,
                className: '所有'
              })
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 根据班级选择课程
    subjectSlctClick () {
      let subjectArr = []
      this.subjectData = ''
      this.subjectDatas = []

      if (this.classData === 0) {
        this.classDatas.forEach((val, index) => {
          if (index !== 0) {
            subjectArr.push(val.examSubjects)
          }
        })
      } else {
        for (let j = 0; j < this.classDatas.length; j++) {
          if (this.classData === this.classDatas[j].classId) {
            subjectArr.push(this.classDatas[j].examSubjects)
          }
        }
      }

      let idList = []
      let examSubjectId = []
      subjectArr.forEach((val) => {
        examSubjectId = []
        val.forEach((ele) => {
          examSubjectId.push(ele.examSubjectId)
        })
        idList.push({
          examSubjectId: examSubjectId
        })
      })
      console.log(idList)
      let defCList = idList.reduce((acc, cur) => {
        if (acc.length === 0) {
          return cur.examSubjectId
        } else {
          let ans = []
          let curArr = cur.examSubjectId
          let accArr = acc
          curArr.forEach((val) => {
            if (accArr.indexOf(val) > -1) {
              ans.push(val)
            }
          })
          return ans
        }
      }, [])
      let courseLi = []
      subjectArr[0].forEach((val) => {
        if (defCList.indexOf(val.examSubjectId) > -1) {
          courseLi.push({
            examSubjectId: val.examSubjectId,
            examSubject: val.examSubject
          })
        }
      })
      courseLi.unshift({
        examSubjectId: 0,
        examSubject: '所有'
      })
      this.subjectDatas = courseLi
    },
    // 提交查询条件并将数据传递至表格
    findScoreRank () {
      // this.clickNum++
      // if (this.clickNum % 2 === 0) {
      //  this.tableTwo = true

      // }
      if (this.examName === '') {
        this.$message('考试名不能为空')
        return false
      }
      if (this.compareExamData === '') {
        this.$message('对比考试名不能为空')
        return false
      }
      if (this.gradeData === '') {
        this.$message('学段不能为空')
        return false
      }
      if (this.thetimeData === '') {
        this.$message('届次不能为空')
        return false
      }
      if (this.classTypeData === '') {
        this.$message('班级类型不能为空')
        return false
      }
      if (this.classData === '') {
        this.$message('班级不能为空')
        return false
      }
      if (this.subjectData === '') {
        this.$message('课程不能为空')
        return false
      }

      let updata = {
        parameters: {
          newExamParentId: this.examName,
          oldExamParentId: this.compareExamData,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          classTypeId: this.classTypeData,
          subjectId: this.subjectData,
          classId: this.classData,
          msg: this.studentNum
        },
        'currentPage': this.achFindcurrentPage,
        'pageSize': this.achFindPageSize

      }
      this.$http.post(
        '/api/v1/findScore/findExamTranscripts',
        updata
      ).then(res => {
        if (res.status === 200) {
          if (res.data.code === 1) {
            let arrlist = []
            let muiList = []
            for (let i = 0; i < res.data.data.content.length; i++) {
              if (res.data.data.content[i]) {
                arrlist.push(res.data.data.content[i])
                res.data.data.content[i].examClassSubjectScores.forEach((val) => {
                  muiList.push(val.subjectName)
                })
              }
            }
            this.tableData = arrlist
            console.log('成绩查询', this.tableData)
            this.tablemuiData = Array.from(new Set(muiList))
            this.achFindTotal = res.data.data.recordTotal
          } else {
            console.log(res.data.msg)
            this.$message({
              //  饿了么的消息弹窗组件,类似toast
              showClose: true,
              message: res.data.msg,
              type: 'error'
            })
          }
        }
      })
        .catch(err => {
          console.log(err)
        })
    },
    achFindHandleSizeChange (val) {
      this.achFindcurrentPage = 1
      this.achFindPageSize = val
      this.findScoreRank()
    },
    // 更新页数并 更新表数据
    achFindHandleCurrentChange (val) {
      this.achFindcurrentPage = val
      this.findScoreRank()
    },
    remoteMethod (query) {
      var flag = 1
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },
    comRemoteMethod (query) {
      var flag = 2
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },
    perComRemoteMethod (query) {
      var flag = 3
      if (query !== '') {
        this.loading = true
        this.initExamName(query, flag)
      } else {
        this.initExamName(query, flag)
      }
    },

    // 初始化考试名称
    initExamName (query, flag) {
      if (flag === 1) {
        this.gradeData = ''
        this.thetimeData = ''
        this.classTypeData = ''
        this.classLevelData = ''
      }
      this.$http.post('/api/v1/schoolexam/findAllSchoolExamParentByParameter', {parameter: query})
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.loading = false
              if (flag === 1) {
                this.examNames = res.data.data
              } else if (flag === 2) {
                this.comExamNames = res.data.data
              } else {
                this.perComExamNames = res.data.data
              }
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    // ---------------------------个人成绩单-------------------------------
    /**
     * 点击班级或年级查询切换弹窗
     * 学生个人成绩单初始化
     */
    viewPersonalClick (row) {
      this.clsOrGradeShow = false
      this.personalShow = true
      this.rgNumber = row.registerNumber

      this.balanceExamVal = ''
      this.perTableData = null
    },
    /**
     * 通过 对比成绩单
     */
    getCompareGradeFun () {
      let updata = {
        parameters: {
          oldExamParentId: this.examName,
          newExamParentId: this.balanceExamVal,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          classTypeId: this.classTypeData,
          msg: this.rgNumber
        },
        'currentPage': 1,
        'pageSize': 10
      }
      this.$http
        .post(
          '/api/v1/findScore/findExamTranscripts',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              var data = res.data.data.content
              var sbjctArr = []
              if (data.length > 0) {
                this.perTableData = data
                data.forEach((val) => {
                  val.examClassSubjectScores.forEach((ele) => {
                    sbjctArr.push(ele.subjectName)
                  })
                })
              }
              this.perTablemuiData = Array.from(new Set(sbjctArr))
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    /**
     * 优势与劣势数据查询
     */
    getAdvanceAndweakness () {
      let updata = {
        params: {
          examParentId: this.examName,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          registerNumber: this.rgNumber
        }
      }
      this.$http
        .get(
          '/api/v1/findScore/findStudentSuperioritySubject',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              this.advanceSbjct = res.data.data.preponderant
              this.weaknessSbjct = res.data.data.Inferiority
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    // 关注学生统计弹窗
    concernStdClick () {
      this.clsOrGradeShow = false
      this.concernStdShow = true
    },
    /**
     * 关注学生统计关闭
     */
    concernStdCloseClick () {
      this.clsOrGradeShow = true
      this.concernStdShow = false
    },
    advDepartmentClick () {
      this.clsOrGradeShow = false
      this.advDepartmentShow = true
    },
    advDepartmentCloseClick () {
      this.clsOrGradeShow = true
      this.advDepartmentShow = false
    },
    personalBackClick () {
      this.clsOrGradeShow = true
      this.personalShow = false
    },
    /**
     * 历次考试年级排名
     */
    inAllPastGradeRankClick () {
      this.inAllPastGradeRankShow = true
      // this.personalShow = false
      // console.log('个人成绩单')
    },
    /**
     * 关闭弹窗事件
     */
    PopCloseClick () {
      this.inAllPastGradeRankShow = false
      this.personalShow = true
    },
    /**
     * 历次考试总分
     */
    inAllPastAllTotalClick () {
      this.inAllPastTotalRankShow = true
      // this.personalShow = false
    },
    /**
     * 年级和班级导出
     */
    clsCourseClick () {
      let updata = {
        parameters: {
          newExamParentId: this.examName,
          oldExamParentId: this.compareExamData,
          periodId: this.gradeData,
          thetime: this.thetimeData,
          classTypeId: this.classTypeData,
          classId: this.classData
        }
      }
      if (this.examName === this.compareExamData) {
        this.$message({
          showClose: true,
          message: '请选择两次不同的考试导出',
          type: 'error'
        })
        return false
      }
      this.$http({
        method: 'post',
        url: '/api/v1/findScore/exportExamReport',
