    findPerAvgSlctExamListFun () {
      if (this.personalAvgVal.length < 1) {
        this.$message({
          showClose: true,
          message: '请选择一次考试',
          type: 'error'
        })
        return false
      }

      let updata = {
        params: {
          json: {
            examParentIds: this.personalAvgVal,
            registerNumber: this.rgNumber
          }
        }
      }
      this.$http
        .get(
          '/api/v1/findScore/findAllExamStudentSubjectScoreChart',
          updata
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              // todo
              let sbjctArr = []
              let examListTitle = []
              let studentSubjectScore = res.data.data.studentSubjectScore
              let classAvgSubjectScore = res.data.data.classAvgSubjectScore
              classAvgSubjectScore.forEach((val, index) => {
                examListTitle.push(val.examName)
                val.examAvgTotalScoreList.forEach((item, count) => {
                  sbjctArr.push({
                    subjectName: item.subjectName,
                    subjectId: item.subjectId
                  })
                })
              })
              let newsbjctArr = sbjctArr.reduce((acc, cur) => {
                if (acc.length === 0) {
                  acc.push(cur)
                } else {
                  let isKey = false
                  acc.forEach((val) => {
                    if (val.subjectId === cur.subjectId) {
                      isKey = true
                      return false
                    }
                  })
                  if (!isKey) {
                    acc.push(cur)
                  } else {
                    isKey = false
                  }
                }
                return acc
              }, [])
              console.log('成绩查询', newsbjctArr, sbjctArr)

              let sbjctData = []
              let clsAvgData = []
              let isKey = false
              let tempObj = {}
              for (let i = 0; i < newsbjctArr.length; i++) { // 课程
                clsAvgData = []
                for (let j = 0; j < classAvgSubjectScore.length; j++) { // 考试
                  isKey = false
                  for (let k = 0; k < classAvgSubjectScore[j].examAvgTotalScoreList.length; k++) { // 考试的课程list
                    if (newsbjctArr[i].subjectId === classAvgSubjectScore[j].examAvgTotalScoreList[k].subjectId) {
                      clsAvgData.push(classAvgSubjectScore[j].examAvgTotalScoreList[k].avgTotalScore)
                      isKey = true
                      break
                    }
                  }
                  if (!isKey) {
                    clsAvgData.push(0)
                  }
                }
                tempObj = {}
                tempObj = {
                  subjectName: newsbjctArr[i].subjectName,
                  subjectId: newsbjctArr[i].subjectId,
                  examListTitleData: examListTitle,
                  examscoreData: clsAvgData
                }
                sbjctData.push(tempObj)
              }
              console.log('成绩查询', sbjctData)
              // 学生个人成绩
              let perSbjctData = []
              let perAvgData = []
              tempObj = {}
              for (let i = 0; i < newsbjctArr.length; i++) { // 课程
                perAvgData = []
                for (let j = 0; j < studentSubjectScore.length; j++) { // 考试
                  isKey = false
                  for (let k = 0; k < studentSubjectScore[j].examAvgTotalScoreList.length; k++) { // 考试的课程list
                    if (newsbjctArr[i].subjectId === studentSubjectScore[j].examAvgTotalScoreList[k].subjectId) {
                      perAvgData.push(studentSubjectScore[j].examAvgTotalScoreList[k].score)
                      isKey = true
                      break
                    }
                  }
                  if (!isKey) {
                    perAvgData.push(0)
                  }
                }
                tempObj = {}
                tempObj = {
                  subjectName: newsbjctArr[i].subjectName,
                  subjectId: newsbjctArr[i].subjectId,
                  examListTitleData: examListTitle,
                  examscoreData: perAvgData
                }
                perSbjctData.push(tempObj)
              }
              console.log('成绩查询单人分数', perSbjctData)
              this.sbjctData = sbjctData
              this.perSbjctData = perSbjctData

              // 更新图
              this.clsAndPerdrawLine()
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
