    // -----------------------------------成绩导入弹窗-----------------------------------
    submitUpload () {
      this.$refs.upload.submit()
    },
    beforeUpload: function (file) {
      const extension = file.name.split('.')[1] === 'xls'
      const extension2 = file.name.split('.')[1] === 'xlsx'
      const isLt2M = file.size / 1024 / 1024 < 10
      if (!extension && !extension2) {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '上传模板只能是 xls、xlsx格式!',
          type: 'error'
        })
        return false
      }
      if (!isLt2M) {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '上传模板大小不能超过 10MB!',
          type: 'error'
        })
        return false
      }
      if ((extension || extension2) && isLt2M) {
        console.log(file)
        // 这里是重点，将文件转化为formdata数据上传
        let fd = new FormData()
        fd.append('file', file)
        fd.append('schoolStageId', 1)
        fd.append('periodId', this.achGradeData)
        fd.append('thetime', this.achThetimeData)
        fd.append('examParentId', this.achExamName)
        fd.append('modelNumber', this.modulesVal)
        this.$http.post('/api/v1/schoolscore/bathImportInfo', fd).then((res) => {
          if (res.status === 200) {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: 'error'
            })
          }
        }, (err) => {
          console.log(err)
        })
      }
      return false
    },
