  computed: {
    compareName: function () {
      return this.upscoredata + this.upline + this.score
    },
    getExamName: function () {
      var ans = ''
      if (this.examNames.length > 0) {
        this.examNames.forEach((val) => {
          if (val.examParentId === this.examName) {
            ans = val.examName
            return false
          }
        })
      }
      return ans
    },
    getExamClassName: function () {
      var ans = []

      if (this.gradeDatas.length > 0) {
        this.gradeDatas.forEach((val) => {
          if (this.gradeData === val.classPeriodId) {
            ans.push(val.classPeriod)
            return false
          }
        })
      }
      ans.push(this.thetimeData)
      ans.push('届')
      if (this.classDatas.length > 0) {
        this.classDatas.forEach((val) => {
          if (val.classId === this.classData) {
            ans.push(val.className)
            return false
          }
        })
      }

      return ans.join('')
    },
    achMdDownLoad () {
      let ans = '/api/v1/schoolscore/downLoadExcel' + '?template=' + this.mdVal
      return this.handleHref(ans)
    },
    userInfo () {
      return JSON.parse(window.localStorage.getItem('userInfo'))
    }
