    // this.getAdvanceAndweakness()
    this.findPersonalExamList()
    console.log('平均分', this.$refs)
    // // 学生个人
    // if (this.userInfo.roleId === '6') {

    // }
    // 班主任和科目老师
    if (this.userInfo.roleId === '4' && this.userInfo.roleId === '5') {
      this.advSbjctShow = true
      this.concernStdBtnShow = true
      this.achImportBtnShow = false
    }
    // 年级主任
    if (this.userInfo.roleId === '21') {
      this.advSbjctShow = false
      this.concernStdBtnShow = false
      this.achImportBtnShow = true
    }
    if (this.userInfo.roleId === '1' || this.userInfo.roleId === '2' || this.userInfo.roleId === '3') {
      this.advSbjctShow = true
      this.concernStdBtnShow = true
      this.achImportBtnShow = true
    }
  }
}
