<template>
  <div class="deploy">
      <div class="deployBox">
      </div>

      <div class="gddeploy">
        <div>
          <img src="@/assets/img/classtyle-arrow-right_03.png" alt="">
            <span>年级调配</span>
          <img src="@/assets/img/classtyle-arrow-left_05.png" alt="">
        </div>
        <div class="gddeploymain">
          <div>
            年级主任:
            <el-input v-model="gdData.fullName"
            :disabled="true"
            >
            </el-input>
          </div>
          <div>
            <span style="margin-left: 32px">学段:</span>
             <el-select v-model="gdData.periodId" placeholder="请选择" @change="findSsByPer">
              <el-option
                v-for="item in PerItems"
                :key="item.periodId"
                :label="item.periodName"
                :value="item.periodId">
              </el-option>
            </el-select>
          </div>
          <div>
           <span style="margin-left: 32px">届次:</span>
            <el-select v-model="gdData.thetime" placeholder="请选择">
              <el-option
                v-for="item in sessionItems"
                :key="item.thetime"
                :label="item.thetime"
                :value="item.thetime">
              </el-option>
            </el-select>
             <!-- <el-input v-model="CurSession" placeholder="格式为2017,2018,XXXX"></el-input> -->
          </div>

        </div>
        <div class="gddeployfooter">
          <div slot="footer" class="dialog-footer">
              <button class="save" @click="gdUdClick">修 改</button>
              <button class="exit" @click="hideDialog">返 回</button>
          </div>
        </div>
      </div>

  </div>
  </template>
<script>
import { formatDate } from '@/api/date.js'
export default {
  name: 'deploypopover',
  props: ['gddeployData'],
  data () {
    return {
      pendingGdData: null,
      gdData: {
        'periodId': 9,
        'directorId': 64,
        'fullName': '有年级主任权限',
        'periodName': '高中',
        'sdtId': 5,
        'thetime': '2008-06-01'
      },
      PerItems: null,
      CurSession: '',
      sessionItems: null,
      isUpdate: false

    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
      if (this.isUpdate) {
        this.$emit('upTable')
        this.isUpdate = false
      }
    },
    /**
     *  初始化学段下拉菜单
     */
    initFindPeriod () {
      this.$http
        .get('/api/v1/schoolperiod/findInfo')
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.PerItems = res.data.data
            } else {
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    findSsByPer () {
      this.$http
        .get(
          '/api/v1/schoolclass/findTheTime',
          {
            params: {
              periodId: this.gdData.periodId
            }
          }
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.sessionItems = res.data.data
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    checkDate () {
      let ans = false
      if (/^\s*(?!0000)[0-9]{4}\s*$/.test(this.CurSession)) {
        this.CurSession.replace(/^\s*|\s*$/g, '')
        // this.CurSession = this.CurSession + '-06-01'
        ans = true
      } else {
        this.$message({
          //  饿了么的消息弹窗组件,类似toast
          showClose: true,
          message: '请验证届次格式为0001-9999',
          type: 'error'
        })
      }
      return ans
    },
    gdUdClick () {
      this.$confirm('确定更改该年级主任的年级调配?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.updateGd()
          // this.$message({ type: 'info', message: '已生成' })
        })
        .catch(() => {
          this.$message({ type: 'info', message: '已取消删除' })
        })
    },
    updateGd () {
      let updata = {
        directorId: this.gdData.directorId,
        periodId: this.gdData.periodId,
        theTime: this.gdData.thetime
      }
      console.log('this.gdData', this.gdData, updata)
      this.$http
        .put(
          '/api/v1/schoolclass/updateDirector',
          this.$qs.stringify(updata)
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$message({
              //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'success'
              })
              this.isUpdate = true
              console.log('res.data', res.data.data)
              let resData = res.data.data
              this.gdData.periodId = resData.periodId
              this.gdData.directorId = resData.directorId
              this.gdData.periodName = resData.periodName
              this.gdData.sdtId = resData.sdtId
              this.gdData.thetime = formatDate(new Date(resData.thetime), 'yyyy-MM-dd')
            } else {
              console.log(res.data.msg)
              this.$message({
              //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }, /** method end */

  watch: {
    'gddeployData' (cur, old) {
      this.gdData = cur
    }
  },
  mounted () {
    this.initFindPeriod()
  },
  computed: {
    getModifyList () {
      return this.gdData
    }
  }
}
</script>
<style lang="scss">
.deploy {
  .deployBox {
    position: fixed;
    background: black;
    top: 0;
    opacity: 0.7;
    filter: alpha(opacity=70); /* 针对 IE8 以及更早的版本 */
    width: 100%;
    height: 100%;
    //  height: 2000px;
    min-width: 960px;
    z-index: 100;
  }
  .gddeploy {
    background: white;
    position: absolute;
    width: 420px;
    height: 435px;
    border-radius: 4px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 200;
  }
  .gddeploy > div:nth-of-type(1) {
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    line-height: 40px;
    background: #6bc08b;
  }
  .gddeploy > div:nth-of-type(1) > img {
    vertical-align: middle;
  }
  .gddeploymain > div {
    margin-top: 28px;
    margin-left: 40px;
    text-align: left;

    color: #999;
    font-size: 16px;
  }
  .gddeploymain > div:nth-of-type(1) > span {
    margin-left: 20px;
  }
  .gddeploymain {
    color: #c3c3c3;
    .el-input {
      width:220px;
    }
  }

  #project {
    border-radius: 5px;
    background: #f2f2f2;
    width: 90px;
    height: 35px;
    appearance: none;
    margin-left: 25px;
    margin-top: 10px;
    color: #c3c3c3;
  }
  .popoverinput {
    border-radius: 5px;
    background: #f2f2f2;
    width: 90px;
    height: 35px;
    margin-left: 18px;
    color: #c3c3c3;
  }
  .gddeployfooter {
    width: 420px;
    position: absolute;
    bottom: 20px;
  }
  .dialog-footer > button {
    border: 1px solid #6bc08b;
    font-size: 14px;
    border-radius: 20px;
    height: 32px;
    line-height: 32px;
    padding: 0px;
    width: 112px;
    color: #6bc08b;
    background: white;
  }
  .dialog-footer > button:hover {
    background: #6bc08b;
    color: white;
  }

}
</style>



