            if (res.data.code === 1) {
              this.$notify({
                title: '提示信息',
                message: '操作成功',
                type: 'success'
              })
              this.isUpdate = true
              this.$emit('updateTable')
              // 初始化对象
              // this.stdform = this.stdformCopy
              // this.placeOfJgArr = []
              // this.nativeHeathArr = []
              // this.placeOfOriginArr = []
              // this.registeredResidenceArr = []
              // this.Disability=[]
              // 关闭popover
              this.hideDialog()
            } else {
              this.$notify({
                title: '提示信息',
                message: res.data.code,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /** 生成學籍號 */
    regitsterBtnClick () {
      this.$http
        .get(
          '/api/v1/studentManagement/getStudentNumber'
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.stdform.registerNumber = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  饿了么的消息弹窗组件,类似toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }

  }
}
</script>
